function deepClone(obj) {
  let _obj = JSON.stringify(obj),
    objClone = JSON.parse(_obj);
  return objClone
}

var KeyplayerView = {
  svg: '',
  extractKeyplayers: function(nodes, threshold) {
    let keyplayers = nodes.filter(d => {
      // console.log(d['all_reposts_count'])
      // return +d['direct_reposts_count'] > 10
      // 直接转发量少，而整体转发量的需要单独拿开
      if (+d['all_reposts_count'] > threshold) {
        d['keyplayer'] = true
        Global.mid2keyplayer[d.mid] = true
        return true
      } else {
        d['keyplayer'] = false
        return false
      }
    })
    return keyplayers

  },

  buildKeyplayersGraph(keyplayers) {
    let links = [],
      nodes = []
    keyplayers.forEach(d => {
      nodes.push({ 'mid': d.mid, 'pid': d.pid, 'depth': d.depth })
    })
    for (let i = 0; i < nodes.length; i++) {
      for (let j = 0; j < nodes.length; j++) {
        if (nodes[i].mid == nodes[j].pid) {
          links.push({ 'source': nodes[i].mid, 'target': nodes[j].mid })
        }
      }
    }
    return { 'nodes': nodes, 'links': links }
  },

  buildKeyplayersTree(keyplayers) {
    let nodes = []
    keyplayers.forEach(d => {
      nodes.push({
        'mid': d.mid,
        'pid': d.pid,
        't': d.t,
        'depth': d.depth,
        'all_reposts_count': d.all_reposts_count,
        'direct_reposts_count': d.direct_reposts_count,
        'text': d.text,
        'name': d.name,
        'oppose_parent': d.oppose_parent,
        'following_parent': d.following_parent
      })
    })

    nodes.sort((a, b) => { return a.t - b.t })

    nodes.sort(function(a, b) {
      return a.t - b.t
    })
    let rut = nodes[0]
    rut['children'] = []

    for (let i = 1; i < nodes.length; i++) {
      addNode(rut, nodes[i])
    }

    function addNode(rut, node) {
      // console.log(node['pid'] == rut['mid'])
      if (node['pid'] == rut['mid']) {
        if (rut.children == undefined) rut.children = []
        rut.children.push(node)
      } else {
        for (let i in rut['children']) {
          addNode(rut['children'][i], node)
        }
      }
    }
    return rut
  },

  highlightKeyplayers(svg, keyplayers, callback) {
    this.enlargeKeyplayers(keyplayers)
    let fontSizeMapping = d3.scaleLinear().domain(d3.extent(keyplayers, d => {
        return d.direct_reposts_count
      }))
      .range([15 * Global.textUnitSize, 30 * Global.textUnitSize])
    this.capitalMid = keyplayers[0].mid
    this.captialPath = svg.append("g").attr('id', 'capital_g')
      .append('path')
      .attr("d", function() {
        return "M" + keyplayers[0].polygon.enlarged_boundary2.join("L") + "Z"
      })
      .style('fill-opacity', 0)
      .style('stroke', 'black')
      .style('stroke-width', 1)

    let g = svg.append('g').attr('id', 'keyplayer_svg')
    this.svg = g
    g.selectAll('path').data(keyplayers)
      .enter()
      .append('path').attr("d", d => {
        return "M" + d.polygon.enlarged_boundary.join("L") + "Z"
      })
      .attr('id', d=>{
        return 'path' + d.mid
      })
      .style('fill', d => {
        return MapColor(d)
      })
      .style('stroke', 'black')
      .style('stroke-width', 1)
      .on("click", d => {
        PathView.clickNode(d.mid)
        callback(d.mid, d.polygon.site[0], d.polygon.site[1])
        d3.event.stopPropagation()
      })
      .append('title')
      .text(d => {
        return d.text
      })

    let fontColor = 'rgb(240, 240, 240)'
    g.selectAll('text').data(keyplayers)
      .enter()
      .append('text')
      .text(d => {
        if (Global.language == 'English' && Global.translated) {
          return Global.chinese2english[d.name]
        }
        if(Global.language == 'Chinese'){
          return d.name.slice(0, Global.nameLength)
        }
        else {
          return d.name
        }
        // return d.name
      })
      .attr('filter', "url(#whiteOutlineEffect)")
      .attr('x', d => { return d.polygon.site[0] })
      .attr('y', d => { return d.polygon.site[1] })
      // .attr('text-anchor', 'middle')
      .attr('alignment-baseline', 'middle')
      .attr('dx', d => {
        if (d.name == '南小七7')
          return '-0.3em'
        if (d.name == '姚晨')
          return '-0.3em'
        return '0.2em'
      })
      .attr('dy', d => {
        if (d.name == '南小七7') {
          return '-1em'
        }
        if (d.name == '姚晨') {
          return '-1.0em'
        }
        return 0
      })
      .style('font-size', d => {
        return fontSizeMapping(d.direct_reposts_count)
      })
      .style('stroke', '#888')
      .style('fill', '#888')
      .append('title')
      .text(d=>{
        return d.name
      })


    g.selectAll('text')
      .call(d3.drag()
        .on("start", dragstarted)
        .on("drag", dragged)
        .on("end", dragended))

    function dragstarted(d) {
      // d3.select(this).raise().classed("active", true);
    }

    function dragged(d) {
      let x = d3.event.x,
        y = d3.event.y
      // console.log(x, y)
      // d3.select(this).attr("transform", "translate(" + x + ',' + y + ')')
      d.x = x,
        d.y = y

      d3.select(this).attr("x", x).attr('y', y)
    }

    function dragended(d) {
      // d3.select(this).classed("active", false);/
    }

  },
  showKeyplayers(mids) {
    if(mids.indexOf(this.capitalMid) < 0) {
      this.captialPath.style('stroke-opacity', 0.2)
    }
    else {
      this.captialPath.style('stroke-opacity', 1)
    }
    this.svg.selectAll('path').filter(d => {
        // console.log(mids.indexOf(d.mid))
        return mids.indexOf(d.mid) >= 0
      })
      .style('opacity', 1)
      .style('stroke-opacity', 1)

    this.svg.selectAll('path').filter(d => {
        return mids.indexOf(d.mid) < 0
      })
      .style('opacity', 0.2)
      .style('stroke-opacity', 0.2)

    this.svg.selectAll('text').filter(d => {
        return mids.indexOf(d.mid) >= 0
      })
      .style('display', 'block')

    this.svg.selectAll('text').filter(d => {
        return mids.indexOf(d.mid) < 0
      })
      .style('display', 'none')
  },

  enlargeKeyplayers(keyplayers) {
    let scale = d3.scaleLinear().domain(d3.extent(keyplayers, d => {
        return d.direct_reposts_count
      }))
      .range([1, 1.8])

    let kpolygons = keyplayers.map(d => {
      return d.polygon
    })
    for (let i in kpolygons) {
      let polygon = kpolygons[i]
      let site = polygon.site
      let p = scale(keyplayers[i].direct_reposts_count)
      let boundary = deepClone(polygon.boundary)
      // polygon.original_boundary = deepClone(boundary)
      for (let j in boundary) {
        boundary[j] = [(boundary[j][0] - site[0]) * p + site[0], (boundary[j][1] - site[1]) * p + site[1]]
      }
      polygon.enlarged_boundary = boundary
    }
    let polygon = kpolygons[0]
    let site = polygon.site
    let boundary = deepClone(polygon.enlarged_boundary)
    for (let j in boundary) {
      boundary[j] = [(boundary[j][0] - site[0]) * 2 + site[0], (boundary[j][1] - site[1]) * 2 + site[1]]
    }
    polygon.enlarged_boundary2 = boundary

  },

  updateKeyplayersStatus(tag) {
    let self = this
    let svg = self.svg
    if (tag) {
      svg.selectAll('path').style('opacity', 1)
        .style('stroke-opacity', 1)

       svg.selectAll('text').style('display', 'block')

    } else {
      svg.selectAll('path').style('opacity', 0.2)
        .style('stroke-opacity', 0.2)

      svg.selectAll('text').style('display', 'none')
    }
  }


}
export default KeyplayerView



