// 楼上的大屏幕是2k 屏幕
// 

var BoundaryView = {
  createBoundary: function(keyplayers, svg) {
    let g = svg.select('#boundary_svg')
    this.regionStroke = Global.regionStroke
    this.countryStroke = Global.countryStroke
    console.log(this.regionStroke)
    if ($("#tableView").height() > 2000) {
      Global.countryWidth = Global.countryWidth * 1.5
      Global.regionWidth = Global.regionWidth * 1.5
    }
    this.countrySvg = g
    this.countryBoundary = []
    let countryBoundary = {}
    let boundaryVisit = {}
    for (let i = 0; i < keyplayers.length; i++) {
      let boundaryPolygons = keyplayers[i].boundaryPolygons
      for (let j in boundaryPolygons) {
        let polygon = VoronoiMap.polygons[boundaryPolygons[j]]
        for (let k in polygon.neighbors) {
          let kindex = VoronoiMap.polygons[polygon.neighbors[k]].keyplayerIndex
          if (kindex && kindex != keyplayers[i].keyplayerIndex) {
            let edgeIndex = polygon.neighborEdges[k]
            let g1 = VoronoiMap.edges[edgeIndex][0],
              g2 = VoronoiMap.edges[edgeIndex][1]
            let k1 = kindex,
              k2 = keyplayers[i].keyplayerIndex

            if (k1 > k2) {
              let tmp = k1
              k1 = k2
              k2 = tmp
            }
            if (boundaryVisit[g1 + ',' + g2]) continue

            if (countryBoundary[k1 + ',' + k2] == undefined) {
              countryBoundary[k1 + ',' + k2] = []
            }
            countryBoundary[k1 + ',' + k2].push([g1, g2])
            boundaryVisit[g1 + ',' + g2] = true
            boundaryVisit[g2 + ',' + g1] = true

            if (Math.distance(VoronoiMap.grids[g1], VoronoiMap.grids[g2]) < 1) {
              console.log(g1, g2)
            }
            g1 = VoronoiMap.grids[VoronoiMap.edges[edgeIndex][0]]
            g2 = VoronoiMap.grids[VoronoiMap.edges[edgeIndex][1]]
            this.countryBoundary.push([g1, g2])
            // g.append('line')
            //   .attr('x1', VoronoiMap.grids[g1][0])
            //   .attr('y1', VoronoiMap.grids[g1][1])
            //   .attr('x2', VoronoiMap.grids[g2][0])
            //   .attr('y2', VoronoiMap.grids[g2][1])
            //   .attr('class', 'countryBoundary')
            //   .style('stroke', this.countryStroke)
            //   .style('stroke-width', Global.countryWidth)
            //   .style('fill', 'none')
          }
        }
      }
    }

    // return
    let grids = VoronoiMap.grids
    let gridPathArr = this.extractPath(countryBoundary)
    // 首尾不用调整
    for (let i = 0; i < gridPathArr.length; i++) {
      let gridPath = gridPathArr[i]
      if (gridPath.length > 2) {
        for (let j = 1; j < gridPath.length - 1; j += 1) {
          let s = gridPath[j - 1],
            e = gridPath[j + 1],
            m = gridPath[j]

          grids[m][0] = (grids[s][0] + grids[e][0]) / 2
          grids[m][1] = (grids[s][1] + grids[e][1]) / 2
        }
      }

      let path = gridPath.map(d => {
        return grids[d]
      })

      g.append('path')
        .attr('d', d => {
          return 'M' + path.join('L')
        })
        .attr('class', 'countryBoundary')
        .style('stroke', this.countryStroke)
        .style('stroke-width', Global.countryWidth)
        .style('fill', 'none')
    }
  },

  extractPath: function(pathDict) {
    let pathArr = []
    console.log(pathDict)
    for (let key in pathDict) {
      let path = pathDict[key],
        gridDegree = {},
        gridPath = {}
      for (let i in path) {
        let p1 = path[i][0],
          p2 = path[i][1]
        if (gridDegree[p1] == undefined) {
          gridDegree[p1] = 0
        }
        if (gridDegree[p2] == undefined) {
          gridDegree[p2] = 0
        }
        gridDegree[p1]++
        gridDegree[p2]++

        if (gridPath[p1] == undefined) {
          gridPath[p1] = []
        }
        if (gridPath[p2] == undefined) {
          gridPath[p2] = []
        }
        gridPath[p1].push(i)
        gridPath[p2].push(i)
      }
      console.log(gridDegree, gridPath)
      let start = -1,
        end = -1
      for (let i in gridDegree) {
        if (gridDegree[i] == 1) {
          if (start == -1) {
            start = i
          } else {
            if (end == -1) {
              end = i
            } else {
              console.log('wrong, two many end points')
            }
          }
        }
      }
      start = +start
      end = +end
      console.log(start, end)

      let gridArr = []
      let visit = {}
      gridArr.push(start)
      let iter = 0
      while (1) {
        if (iter++ > 100000) {
          console.log('wrong')
          break
        }
        if (start == end) break
        let indexPaths = gridPath[start]
        // console.log(start, end)
        for (let i in indexPaths) {
          if (visit[indexPaths[i]] == undefined) {
            let p1 = +path[indexPaths[i]][0],
              p2 = +path[indexPaths[i]][1]
            if (+p1 == +start) {
              gridArr.push(p2)
              start = p2
            } else if (+p2 == +start) {
              gridArr.push(p1)
              start = p1
            }
            // console.log(start, p1, p2, gridPath[p1], gridPath[p2])
            visit[indexPaths[i]] = true
          }
        }
      }
      pathArr.push(gridArr)
    }
    return pathArr
  },
  extractRegionPath: function(pathDict) {
    // 地区可能会散开
    let pathArr = []
    for (let i in pathDict) {
      let pathSegments = pathDict[i]
      let adjGraph = {}
      let degree = {}
      for (let j in pathSegments) {
        let p1 = pathSegments[j][0],
          p2 = pathSegments[j][1]
        // if(p1 == p2) console.log('???????????????')
        if (adjGraph[p1] == undefined) {
          adjGraph[p1] = []
        }
        adjGraph[p1].push(p2)
        if (adjGraph[p2] == undefined) {
          adjGraph[p2] = []
        }
        adjGraph[p2].push(p1)
        if (degree[p1] == undefined) {
          degree[p1] = 0
        }
        degree[p1]++
        if (degree[p2] == undefined) {
          degree[p2] = 0
        }
        degree[p2]++
      }
      // console.log(adjGraph)
      let iter = 0
      // console.log('iiiiii', i)
      while (1) {
        if (iter++ > 100000) {
          console.log('loop wrong')
          console.log(degree)
          break
        }
        let left = false
        let start = -1
        for (let p in degree) {
          if (degree[p] == 1) {
            start = p
            break
          }
          if (degree[p] > 1) {
            left = true
          }
        }
        if (start == -1 && left) {
          for (let p in degree) {
            if (degree[p] > 0) {
              start = p
              break
            }
          }
        }
        if (start == -1 && left == false) {
          break
        }
        // console.log(start, left)
        degree[start]--
        let arr = []
        let iter2 = 0
        arr.push(start)
        while (1) {
          if (iter++ > 1000000) {
            // 阈值要大一些
            console.log('loop wrong')
            // console.log(JSON.parse(JSON.stringify(degree)))
            break
          }
          let found = false
          for (let j in adjGraph[start]) {
            let tmp = adjGraph[start][j]
            // console.log(start, tmp, degree[tmp])
            if (degree[tmp] > 0) {
              if (degree[tmp] == 1) {
                arr.push(tmp)
                degree[tmp] = degree[tmp] - 1
                found = true
                break
              } else {
                arr.push(tmp)
                degree[tmp] = degree[tmp] - 2
                start = tmp
                break
              }
            }
          }
          if (found) break
        }
        pathArr.push(arr)
      }
    }

    // console.log(pathArr)
    return pathArr
  },
  createInsideBoundary: function(keyplayers, svg) {
    let g = svg.select('#inside_boundary_svg')
    this.regionSvg = g
    // this.regionBoundary = []
    let regionBoundary = []
    let boundaryVisit = {}
    for (let i = 0; i < keyplayers.length; i++) {
      let polygons = keyplayers[i].allocatedPolygons
      let keyplayerIndex = keyplayers[i].keyplayerIndex
      if (polygons.length < 20) continue
      for (let j = 0; j < polygons.length; j++) {
        let polygonA = VoronoiMap.polygons[polygons[j]]
        if (polygonA.tree == undefined) console.log(polygonA)
        if (polygonA.tree.mid == keyplayers[i].mid) {
          continue
        }

        let neighborEdges = polygonA.neighborEdges,
          neighbors = polygonA.neighbors
        for (let k = 0; k < neighbors.length; k++) {
          let polygonB = VoronoiMap.polygons[neighbors[k]]
          if (polygonB.keyplayerIndex != keyplayerIndex) continue

          if (polygonA.tree.clusterIndex != polygonB.tree.clusterIndex) {
            let edge = VoronoiMap.edges[neighborEdges[k]]
            let k1 = polygonA.tree.clusterIndex,
              k2 = polygonB.tree.clusterIndex,

              g1 = edge[0],
              g2 = edge[1]

            // g1 = VoronoiMap.grids[edge[0]],
            // g2 = VoronoiMap.grids[edge[1]]

            if (k1 == '-1' || k1 == -1) k1 = 1000
            if (k2 == '-1' || k2 == -1) k2 = 1000
            k1 = +k1
            k2 = +k2
            if (k1 > k2) {
              let tmp = k1
              k1 = k2
              k2 = tmp
            }
            if (boundaryVisit[g1 + ',' + g2]) continue

            if (regionBoundary[k1 + ',' + k2] == undefined) {
              regionBoundary[k1 + ',' + k2] = []
            }
            regionBoundary[k1 + ',' + k2].push([g1, g2])
            boundaryVisit[g1 + ',' + g2] = true
            boundaryVisit[g2 + ',' + g1] = true



            // g1 = VoronoiMap.grids[edge[0]]
            // g2 = VoronoiMap.grids[edge[1]]
            // g.append('line')
            //   .attr('x1', g1[0])
            //   .attr('y1', g1[1])
            //   .attr('x2', g2[0])
            //   .attr('y2', g2[1])
            //   .attr('class', 'regionBoundary')
            //   .style('stroke', this.regionStroke)
            //   .style('stroke-width', Global.regionWidth)
            //   .style('fill', 'none')

          }
        }
      }
    }

    // return

    // console.log(regionBoundary)
    let grids = VoronoiMap.grids
    let gridPathArr = this.extractRegionPath(regionBoundary)
    // 首尾不用调整
    for (let i = 0; i < gridPathArr.length; i++) {
      let gridPath = gridPathArr[i]
      if (gridPath.length > 10) { // 有的分类太短
        for (let j = 1; j < gridPath.length - 1; j += 1) {
          let s = gridPath[j - 1],
            e = gridPath[j + 1],
            m = gridPath[j]

          grids[m][0] = (grids[s][0] + grids[e][0]) / 2
          grids[m][1] = (grids[s][1] + grids[e][1]) / 2
        }
      }

      let path = gridPath.map(d => {
        return grids[d]
      })

      g.append('path')
        .attr('d', d => {
          return 'M' + path.join('L')
        })
        .attr('class', 'regionBoundary')
        .style('stroke', this.regionStroke)
        .style('stroke-width', Global.regionWidth)
        .style('fill', 'none')

    }


  },

  regionShowUpdateForRegionSelection(mids) {

    let keyplayers = Global.keyplayers

  },
  regionShowUpdate(tag) {
    if (this.regionSvg != undefined) {
      if (!tag) {
        this.regionSvg.selectAll('.regionBoundary').style('stroke', 'none')
      } else {
        this.regionSvg.selectAll('.regionBoundary').style('stroke', this.regionStroke)
      }
    }
  },
  countryShowUpdate(tag) {
    if (!tag) {
      this.countrySvg.selectAll('.countryBoundary').style('stroke', 'none')
    } else {
      this.countrySvg.selectAll('.countryBoundary').style('stroke', this.countryStroke)
    }
  }

}
window.BoundaryView = BoundaryView
export default BoundaryView;



