var SpectralColor = d3.schemeSpectral[10]
window.SpectralColor = SpectralColor

var InterpolateGreen = d3.scaleSequential(d3.interpolateGreens)
var MapColor = function(tree) {
  let depth = tree.depth,
    mid = tree.mid
  if (depth == 1 || Global.mid2keyplayer[mid]) return Global.riverColor
  return InterpolateGreen(Math.log(depth) / Math.log(Global.maxDepth))
}
window.MapColor = MapColor

var positiveColor = '#508fff'

var neutralColor = '#fcc335'

var negativeColor = '#d35757'

Global.positiveColor = positiveColor

Global.neutralColor = neutralColor

Global.negativeColor = negativeColor


var sColor = d3.scaleSequential(d3.interpolateYlOrRd)
function SentimentColor(d) {
  let sentiment = +d.positive_prob
  // console.log(sentiment)
  // return sColor(1 - sentiment)
  if (sentiment > 0.7) return positiveColor
  if (sentiment < 0.4) return negativeColor
  return neutralColor
}
window.SentimentColor = SentimentColor




let m = 0.5
let mt = 5
var MountainColor = function(depth) {
  let maxDepth = Global.maxDepth
  return InterpolateColor(Math.log(depth) / Math.log(maxDepth))
  // return InterpolateColor((mt + depth) / (maxDepth + mt) * 0.3 + 0.7)
  if (depth == 1) return InterpolateColor(m - 0.1)
  if (depth == 2) return InterpolateColor(m)
  // return InterpolateColor(m + (1 - m) * Math.pow(Math.E, (depth - maxDepth - 2) / maxDepth))
  return InterpolateColor(m + (1 - m) * Math.pow(Math.E, (depth - maxDepth - 2) / maxDepth))
}

window.MountainColor = MountainColor

window.PriorityQueue = PriorityQueue

{
  window.CommunicateWithServer = function(type, paramsObj, url, callback) {
    if (buildCodes) {
      paramsObj = JSON.stringify(paramsObj)
      $.ajax({
        type: type,
        url: url,
        data: { 'params': paramsObj },
        dataType: "json",
        success: function(data) {
          callback(data);
        },
        error: function(err) {
          callback(err)
        }
      })
    } else {
      let formData = new URLSearchParams();
      formData.append("params", JSON.stringify(paramsObj));
      if (type == 'get') {
        api.get(url, formData, data => {
          callback(data)
        }, error => {
          callback(error)
        })
      } else if (type == 'post') {
        api.post(url, formData, data => {
          callback(data)
        }, error => {
          callback(error)
        })
      }
    }
  }
}

