    keywordsProcess: function() {
      let nodes = Global.nodes,
        wordDict = {},
        wordList = []

      let threshold = Global.tfidfWordFilter // 出现次数太少的词，没有必要用
      for (let i in nodes) {
        for (let j in nodes[i].words) {
          if (wordList.indexOf(nodes[i].words[j]) < 0) {
            wordList.push(nodes[i].words[j])
            wordDict[nodes[i].words[j]] = 0
          }
          wordDict[nodes[i].words[j]]++
        }
      }
      let tmp = []
      for (let i in wordList) {
        if (wordDict[wordList[i]] > threshold) {
          tmp.push(wordList[i])
        }
      }
      wordList = tmp
      wordList.sort(function(a, b) {
        return wordDict[b] - wordDict[a]
      })

      let keyplayers = Global.keyplayers
      let iskeyplayer = {}
      for (let i in keyplayers) {
        iskeyplayer[keyplayers[i].mid] = true
      }

      function calcFre(wordCnt, tree, tag) {
        let words = tree.words
        if (tag) {
          if (iskeyplayer[tree.mid] == true) return
        }
        for (let i in words) {
          wordCnt[wordList.indexOf(words[i])]++
        }
        for (let i in tree.children) {
          calcFre(wordCnt, tree.children[i], true)
        }
      }

      // console.log(wordList, wordDict)
      for (let i in keyplayers) {
        let wordCnt = d3.range(wordList.length).map(d => { return 0 })
        calcFre(wordCnt, keyplayers[i], false)
        let wordFre = []
        // 计算每个keyplayer的关键词
        for (let j in wordCnt) {
          // console.log(wordCnt[j], wordDict[wordList[j]])
          if (wordCnt[j] > threshold) {
            wordFre.push(wordCnt[j] / wordDict[wordList[j]])
          }
        }
        let tmpWordList = wordList.map(d => {
          return d
        })

        let order = d3.range(wordList.length)

        order.sort((a, b) => {
          return wordFre[b] - wordFre[a]
        })

        let wordFre2 = [],
          wordCnt2 = [],
          wordList2 = []

        for (let j in order) {
          wordFre2.push(wordFre[order[j]])
          wordCnt2.push(wordCnt[order[j]])
          wordList2.push(wordList[order[j]])
        }
        let keywords = []
        for (let j in wordCnt2) {
          if (wordCnt2[j] > threshold) {
            if (wordList2[j] == "＂") continue
            keywords.push({ 'word': wordList2[j], 'cnt': wordCnt2[j], 'fre': wordFre2[j] })
          }
        }
        keyplayers[i].keywords = keywords
      }

      let global_wordCnt = {}
      for (let i in keyplayers) {
        let keywords = keyplayers[i].keywords
        for (let j in keywords) {
          let word = keywords[j]['word'],
            cnt = keywords[j]['cnt']
          if (global_wordCnt[word] == undefined) {
            global_wordCnt[word] = 0
          }
          global_wordCnt[word] += cnt
        }
      }
      this.drawWordleView(d3.keys(global_wordCnt), global_wordCnt)

      this.$store.state.keyplayerKeywords = Math.random()
    },
    drawWordleView: function(wordList, wordCnt) {
      let width = this.width,
        height = this.height,
        svg = this.svg_wordle
      this.wordList = wordList
      this.wordCnt = wordCnt

      var items = Object.keys(wordCnt).map(function(key) {
        return [key, wordCnt[key]];
      });

      // Sort the array based on the second element
      items.sort(function(first, second) {
        return second[1] - first[1];
      });

      // Create a new array with only the first 5 items
      // let newList = items
      let newList = items.slice(0, 150)

      Global.keywords = newList.map(d => {
        return d[0]
      })

      let sizeExtent = d3.extent(newList, d => { return d[1] })
      let scale = d3.scaleLinear().domain(sizeExtent).range([12 * Global.textUnitSize, 40 * Global.textUnitSize])
      Global.keywordScale = scale
      let frequency_list = []
      for (let i in newList) {
        let tmp = {}
        tmp['size'] = scale(newList[i][1])
        tmp['text'] = newList[i][0]
        tmp['chinese'] = newList[i][0]
        tmp['id'] = tmp['text']
        tmp['sentiment'] = Global.wordSentiment[tmp['text']] / Global.keywordsCnt[tmp['text']]
        frequency_list.push(tmp)
      }
      // console.log(frequency_list)
      cloud().size([width - 10, height - 10])
        .words(frequency_list)
        // .padding(Global.textUnitSize * 2)
        // .padding(20)
        .padding(2)
        .rotate(0)
        //.rotate(function() { return -45 + (Math.floor(Math.random() * 3)) * 45 })
        .text(function(d) {
          if (Global.language == 'English' && Global.translated) {
            return Global.chinese2english[d.text]
          }
          return d.text
        })
        //.font('Impact')
        .fontSize(function(d) { return d.size; })
        .on("end", this.drawWordle)
        .start();

    },
    updateWordleAnimation: function(nodes) {
      let wordCnt = {}
      for (let i in nodes) {
        for (let j in nodes[i]['words']) {
          let word = nodes[i]['words'][j]
          if (word == "") continue
          if (!wordCnt[word]) {
            wordCnt[word] = 0
          }
          wordCnt[word]++
        }
      }
      let words = d3.keys(wordCnt)
      // console.log('update animation', words)
      this.svg_wordle.selectAll('text')
        .attr('visibility', d => {
          // console.log(d.id)
          if (words.indexOf(d.id) >= 0) {
            return 'visible'
          } else {
            return 'hidden'
          }
        })
        .style('fill-opacity', 1)
        .style('font-size', d => {
          return Global.keywordScale(wordCnt[d.id])
        })
    },
    drawWordle: function(words) {
      console.log("word sentiment", Global.wordSentiment)
      let self = this
      let width = this.width,
        height = this.height,
        svg = this.svg_wordle
      console.log(words)
      svg.selectAll('*').remove()
      svg.append("g")
