            }
          }
          self.svg_wordle.selectAll('text').style('fill-opacity', 0.3)
          self.updateProjection(mids)
          d3.select(this).style('fill-opacity', 1)
          Global.highlightedPolygons = mids
          Global.tableViewUpdate = true
          console.log(mids)
          Global.behaviorType = 'TopicView'
          self.$store.state.highlightPolygons = 'TopicView ' + Math.random()
          d3.event.stopPropagation()
          // console.log("click", this.selectedWord, this.selectedNodes)
        })

      // this.svg_wordle.selectAll('text')
