        d.x = x,
          d.y = y

        d3.select(this).attr("x", x).attr('y', y)
      }

      function dragended(d) {
        // d3.select(this).classed("active", false);/
      }
    },
    updateWordle: function(nodes) {
      let words = []
      let wordCnt = {}
      for (let i in nodes) {
        for (let j in nodes[i]['words']) {
          // if (nodes[i]['words'][j] != "" && tmp.indexOf(nodes[i]['words'][j]) < 0) {
          //   tmp.push(nodes[i]['words'][j])
          //   if (!wordcnt.hasOwnProperty(nodes[i]['words'][j])) {
          //     words.push(nodes[i]['words'][j])
          //     wordcnt[nodes[i]['words'][j]] = 0
          //   }
          //   wordcnt[nodes[i]['words'][j]]++
          // }
          let word = nodes[i]['words'][j]
          if (word == "") continue
          if (!wordCnt[word]) {
            wordCnt[word] = 0
            words.push(word)
          }
          wordCnt[word]++
        }
      }
      this.drawWordleView(words, wordCnt)
    },
    updateProjection: function(mids) {
      let svg = d3.select('#projection').selectAll('.projectionCircle')
      svg.style('fill-opacity', d => {
