function getFullWords(node, keymids)
{
	let words = []
	for(let i in node["words"])
		if(node["words"][i] != "" && words.indexOf(node["words"][i]) < 0)
			words.push(node["words"][i])

	for(let i in node["children"])
	{
		if(keymids.indexOf(node["children"][i].mid) >= 0)
			continue
		let tmp = getFullWords(node["children"][i], keymids)
		for(let j in tmp)
			words.push(tmp[j])
	}
	return words
}

function calLength(vector)
{
	let t = 0
	for(let i = 0; i < vector.length; i++)
		t += vector[i]*vector[i]
	return Math.sqrt(t)
}

function dist(v1, v2)
{
	let a = 0
	for(let i = 0; i < v1.length; i++)
		a += v1[i]*v2[i]
	let b = calLength(v1) * calLength(v2)
	return a * 1.0 / b
}

function getVector(player, keymids)
{
	let words = getFullWords(player, keymids)
	let wordcnt = {}
	for(let i in words)
	{
		let word = words[i]
		if(!wordcnt.hasOwnProperty(word))
			wordcnt[word] = 0
		wordcnt[word]++
	}
	//console.log(wordcnt)
	let vector = []
	let keywords = Global.keywordsCnt
	for(let i in keywords)
	{
		if(keywords[i] < 8)
			continue
		if(!wordcnt.hasOwnProperty(i))
			vector.push(0)
		else
			vector.push(wordcnt[i] * 1.0 / keywords[i])
	}
	return vector
}

function Cos(player1, player2)
{
	let keyplayers = Global.keyplayersCnt
	let keymids = []
	for(let i in keyplayers)
		keymids.push(keyplayers[i].mid)
	let v1 = getVector(player1, keymids)
	let v2 = getVector(player2, keymids)
	let result = dist(v1, v2)
	return result
}

export default Cos


