import TopicView from '../../topic/TopicView.vue'
var VoronoiView = {
  'startIndex': 0,
  'opacity': 0.2,

  drawBaseMap: function(svg) {
    let g = svg.append('g').attr('id', 'base_svg')
    this.baseSvg = g
    g.selectAll('.cellg').data(VoronoiMap.polygons.filter(d => { return d.tree })).enter().append('g').attr('class', 'cell')
      .append('path').attr("d", d => {
        return "M" + d.boundary.join("L") + "Z"
      })
      .attr("fill", d => {
        return '#fff'
      })
      .style("stroke", '#fff')
      .style("stroke-width", 0.4)
  },
  baseMapUpdate: function(tag) {
    if (tag) {
      this.baseSvg.selectAll('.cellg').style('opacity', 1)
    } else {
      this.baseSvg.selectAll('.cellg').style('opacity', 0)
    }
  },
  drawLayout: function(svg, callback) {
    console.log('draw layout')
    // let g = svg.append('g').attr('id', 'city_svg')
    let g = svg.select('#city_svg')
    this.citySvg = g
    let polygons = VoronoiMap.polygons.filter(d => { return d.tree })
    polygons.sort(function(a, b) {
      return a.tree.t - b.tree.t
    })
    let no_comment = ["", "转发微博", "转发", "轉發微博", "repost", "Repost"]
    this.polygons = polygons
    let cellg = g.selectAll('.cityg')
      .data(polygons).enter().append('g').attr('class', 'cityg')

    cellg.append('path').attr("d", d => {

        return "M" + d.boundary.join("L") + "Z"
      })
      .attr('id', d => {
        return 'city' + d.tree.mid
      })
      .attr('class', 'city')
      .on("click", d => {

        // if (Global.interactionMethod == 'single') {
        //   PathView.clickNode(d.tree.mid)
        // }

        let x = d.site[0],
          y = d.site[1]

        // console.log(d, d.keyplayerIndex, d.tree.clusterIndex)

        callback(d, x, y)
        d3.event.stopPropagation()
        // if (Global.interactionMethod == 'multiple') {
        //   callback(d)
        // }
        // PopView.createTag(d3.event.x, d3.event.y, d.tree)

      })
      .style("fill", d => {

        if (Global.switchColorFlag == 'level') {
          return MapColor(d.tree)
        } else if (Global.switchColorFlag == 'sentiment') {
          return SentimentColor(d.tree)
        }
      })
      .style("stroke", d => {
        if (Global.switchColorFlag == 'level') {
          return MapColor(d.tree)
        } else if (Global.switchColorFlag == 'sentiment') {
          return SentimentColor(d.tree)
        }
      })
      .style("stroke-width", this.opacity)
      .append('title')
      .text(d => {
        return d.tree.name + ': ' + d.tree.text
      })

    // cellg.append('text')
    //   .text(d => {
    //     return d.tree.name
    //   })
    //   .attr('x', d => { return d.site[0] })
    //   .attr('y', d => { return d.site[1] })
    //   .attr('alignment-baseline', 'middle')
    //   .style('font-size', d => {
    //     return Global.textUnitSize * 8
    //   })
    //   .style('stroke', '#ccc')


    // create keyplayer polygon in region 
    // return
    let keyPolygons = polygons.filter(d => {
      return d.tree.all_reposts_count > Global.insideKeyplayerThreshold
    })

    Global.insideKeyplayers = keyPolygons.map(d => {
      return d.tree.mid
    })

    let keySvg = svg.append('g').attr('id', 'key_svg')
    // let keySvg = svg.select('#key_svg')
    this.keySvg = keySvg
    if (Global.filename == 'jiyinbianji') {
      return
    }
    keySvg.selectAll('.keyg')
      .data(keyPolygons).enter().append('g').attr('class', 'keyg')
      .append('path').attr("d", d => {
        return "M" + d.boundary.join("L") + "Z"
      })
      .attr('id', d => {
        return 'key' + d.tree.mid
      })
      .attr('class', 'key')
      .style("fill", d => {
        return 'none'
      })
      .style("stroke", d => {
        // return '#999999'
        return 'black'
      })
      .style("stroke-width", '0.1em')
  },
  updateBoundaryLayout: function() {
    this.citySvg.selectAll('.cityg')
      .select('path').attr("d", d => {
        let boundary = []
        for (let i in d.boundaryGrids) {
          boundary.push(VoronoiMap.grids[d.boundaryGrids[i]])
        }
        return "M" + boundary.join("L") + "Z"
      })
  },

  updateLayout: function(layoutMethod) {
    console.log(layoutMethod)
    let g = d3.select('#city_svg')

    g.selectAll('.cityg').select('path')
      .style("fill", d => {
        if (Global.switchColorFlag == 'level') {
          return MapColor(d.tree)
        } else if (Global.switchColorFlag == 'sentiment') {
          return SentimentColor(d.tree)
        }
      })
      .style("stroke", d => {
        if (Global.cityShown) {
          return '#888888'
        }
        if (Global.switchColorFlag == 'level') {
          return MapColor(d.tree)
        } else if (Global.switchColorFlag == 'sentiment') {
          return SentimentColor(d.tree)
        }
      })

    let keyPolygons = VoronoiMap.polygons.filter(d => {
      return d.tree && d.tree.all_reposts_count > 10
    })

    this.keySvg.selectAll('.keyg').remove()

    this.keySvg.selectAll('.keyg')
      .data(keyPolygons).enter().append('g').attr('class', 'keyg')
      .append('path').attr("d", d => {
        return "M" + d.boundary.join("L") + "Z"
      })
      .attr('id', d => {
        return 'key' + d.tree.mid
      })
      .attr('class', 'key')
      .style("fill", d => {
        return 'none'
      })
      .style("stroke", d => {
        return 'black'
      })
      .style("stroke-width", '0.1em')


  },
  cityShowUpdate: function(tag) {
    if (tag) {
      this.citySvg.selectAll('.city')
        .style("stroke", '#888888')
    } else {
      this.citySvg.selectAll('.city')
        .style('stroke', d => {
          return MapColor(d.tree)
        })
    }
  },

  hideElements: function() {
    this.citySvg.selectAll('.city').style('opacity', 0.2)
      .style('stroke-opacity', 0.2)
    this.keySvg.selectAll('.key').style('stroke-opacity', 0.2)
  },

  animationUpdate: function(mids) {
    this.citySvg.selectAll('.city').filter(d => {
        return mids.indexOf(d.tree.mid) >= 0
      })
      .style('opacity', 1)
      .style('stroke-opacity', 1)
    this.citySvg.selectAll('.city').filter(d => {
        return mids.indexOf(d.tree.mid) < 0
      })
      .style('opacity', this.opacity)
      .style('stroke-opacity', this.opacity)

    this.keySvg.selectAll('.key').filter(d => {
        return mids.indexOf(d.tree.mid) >= 0
      })
      .style('stroke-opacity', 1)


    this.keySvg.selectAll('.key').filter(d => {
        return mids.indexOf(d.tree.mid) < 0
      })
      .style('stroke-opacity', this.opacity)



  },
  startAnimation: function(callBack) {
    let self = this
    let svg = self.citySvg
    let polygons = self.polygons
    let startPolygons = polygons.slice(0, self.startIndex).map(d => { return d.tree.mid })
    svg.selectAll('.city').style('opacity', this.opacity).style('stroke-opacity', this.opacity)
    svg.selectAll('.city').filter(d => {
        return startPolygons.indexOf(d.tree.mid) >= 0
      })
      .style('opacity', 1)
      .style('stroke-opacity', 1)

    // svg.selectAll('.city').filter(d => {
    //     return startPolygons.indexOf(d.tree.mid) >= 0
    //   })
    //   .style('opacity', 1)

    self.stop = false
    redraw(self.startIndex)

    function redraw(size) {
      if (size >= polygons.length) {
        callBack()
        self.startIndex = 0
        return
      }
      svg.select('#city' + polygons[size].tree.mid).style('opacity', 1)
        .transition()
        .duration(0.0001)
        .on('end', function() {
          if (self.stop) return
          self.startIndex = size + 1
          redraw(size + 1)
        })
      // redraw(size + 1)
    }
  },
  stopAnimation: function() {
    this.stop = true
  },
  refresh: function() {
    let self = this
    let svg = self.citySvg
    svg.selectAll('.city')
      .style('opacity', 1)
      .style('stroke-opacity', 1)

    this.keySvg.selectAll('.key')
      .style('stroke-opacity', 1)

    self.startIndex = 0
  },
  changeNodesShow: function(nodeNum) {
    let polygonsShownMid = this.polygons.slice(0, nodeNum).map(d => { return d.tree.mid })

    this.citySvg
      .selectAll('.city')
      .filter(d => {
        return polygonsShownMid.indexOf(d.tree.mid) < 0
      })
      .style('opacity', this.opacity)
      .style('stroke-opacity', this.opacity)

    this.citySvg
      .selectAll('.city')
      .filter(d => {
        return polygonsShownMid.indexOf(d.tree.mid) >= 0
      })
      .style('opacity', 1)
      .style('stroke-opacity', 1)
  }
}

window.VoronoiView = VoronoiView
export default VoronoiView
;



