  drawWordleView: function(layoutMethod) {
    console.log('calc wordle layout')
    let self = this
    let keyplayer = self.keyplayer,
      keywords = self.kw,
      xRange = self.xRange,
      yRange = self.yRange
    // 这里的 scale和topicvue里面的scale不一样, 突出tfidf
    // 
    // let scale = d3.scaleLinear().domain([0, 1]).range([8, 30])
    // let scale = d3.scaleLinear().domain(d3.extent(keywords, d => {
    //   if (Global.wordSortMethod == 'tfidf') {
    //     return d.fre 
    //   }
    //   if (Global.wordSortMethod == 'cnt') {
    //     return d.cnt
    //   }
    // })).range([8, 25])
    let frequency_list = []
    for (let i in keywords) {
      let tmp = {}
      if (Global.stopWords.indexOf(keywords[i].word) >= 0) continue
      // console.log(keywords[i].word, 
      // if (Global.wordSortMethod == 'tfidf') {
      //   tmp['size'] = keywords[i].fre
      // }
      if (Global.wordSortMethod == 'cnt') {
        tmp['size'] = keywords[i].cnt
      }
      tmp['text'] = keywords[i].word
      tmp['id'] = keywords[i].word
      tmp['sentiment'] = keywords[i].sentiment
      tmp['center'] = [(xRange[1] + xRange[0]) / 2, (yRange[0] + yRange[1]) / 2]
      tmp['chinese'] = keywords[i].word
      tmp['keyplayerMid'] = keyplayer.mid
      frequency_list.push(tmp)
    }

    let width = xRange[1] - xRange[0],
      height = yRange[1] - yRange[0]
    // console.log(frequency_list, width * 1.2, height * 1.2)
    if (frequency_list.length == 0) return
    let a = cloud()
    let padding = 3 * Global.textUnitSize
    let factor = 0.9
    // if (frequency_list.length < 10) {
    //   factor = 2
    // }

    if (keyplayer.name == '财经网' || keyplayer.name == '姚晨' || keyplayer.name == '日本国驻华大使馆' ||
      keyplayer.name == '秦芦花' || keyplayer.name == '雷军') {
      factor = 0.8
    }

    if (keyplayer.name == '南小七7') {
      factor = 1
    }


    a.size([width * factor, height * factor])
      .words(frequency_list)
      .padding(padding)
      .rotate(0)
      //.rotate(function() { return -45 + (Math.floor(Math.random() * 3)) * 45 })
      .text(function(d) {
        if (Global.language == 'English' && Global.translated) {
          return Global.chinese2english[d.text]
        }
        return d.text
      })
      //.font('Impact')
      .fontSize(function(d) { return self.scale(Math.sqrt(d.size)); })
      .on("end", drawWordle)
      .start()

    function drawWordle(words) {
      if (words.length == 0) return
      let center = words[0].center
      let kpmid = words[0].keyplayerMid
      // console.log(words)
      // this 上下文不一样
      d3.select('g#topic_svg_all').append("g")
        .attr('id', 'kp' + kpmid)
        // .attr("transform", "translate(" + center[0] + "," + center[1] + ")")
        .selectAll("text")
        .data(words)
        .enter().append("text")
        .style("font-size", function(d) { return d.size + "px"; })
        .style("fill", d => {
          if (d.text == 'surprised' || d.text == '吃惊') return Global.negativeColor
          if (+d.sentiment > 0.6) return Global.positiveColor
          if (+d.sentiment < 0.4) return Global.negativeColor
          return Global.neutralColor
        })
        .style('fill-opacity', '0.9')
        // .style('stroke', 'gray')
        // .style('stroke-width', 2)
        .style('font-weight', '600')
        .attr('filter', "url(#whiteOutlineEffect)")
        .attr("text-anchor", "middle")
        .attr("transform", function(d) {
          return "translate(" + [d.x + center[0], d.y + center[1]] + ")rotate(" + d.rotate + ")";
        })
        .text(function(d) {
          return d.text
        })
        .attr('id', d => {
          return 'kp' + d.keyplayerMid + d.chinese.split('.').join('')
        })
        .attr('visibility', d => {
          if (layoutMethod == 'time') {
            return 'visible'
          }
          if (layoutMethod == 'semantic') {
            return 'hidden'
          }
        })
        .on("click", function(d) {
          let text = d.chinese
          console.log(text)
          let mids = []
          for (let i in Global.nodes) {
            if (Global.nodes[i]['words'].indexOf(text) >= 0) {
              mids.push(Global.nodes[i].mid)
            }
          }

          // d3.select('g#topic_svg').selectAll('text').style('fill-opacity', 0.1)
          // d3.select(this).style('fill-opacity', 0.9)
          Global.highlightedPolygons = mids
          Global.topicViewUpdate = true
          Global.behaviorType = 'Voronoi Topic'
          Store.state.highlightPolygons = 'VoronoiTopicView ' + Math.random()
          d3.event.stopPropagation()
          // console.log("click", this.selectedWord, this.selectedNodes)
        })
        .style('cursor', 'pointer')


      d3.select('g#topic_svg_all').selectAll('text')
        .call(d3.drag()
          .on("start", dragstarted)
          .on("drag", dragged)
          .on("end", dragended))

      function dragstarted(d) {
        // d3.select(this).raise().classed("active", true);
      }

      function dragged(d) {
        let x = d3.event.x,
          y = d3.event.y
        // console.log(x, y)
        // d3.select(this).attr("transform", "translate(" + x + ',' + y + ')')
        d.x = x,
          d.y = y

        d3.select(this).attr("x", x).attr('y', y)
      }

      function dragended(d) {
        // d3.select(this).classed("active", false);/
      }

    }

  },

