<style scoped>
.regionView
{
  width: 100px;
  height: 100px;
  border-radius: 6px;
  box-shadow: 0 1px 3px 0.5px rgba(126, 126, 126, 0.5);
  border: solid 1px rgba(126, 126, 126);
  background-color: #ffffff;
}
</style>

<template>
    <div class="regionView" :id="'regionView' + keyplayer + '_' + index" @dblclick="removeView">
    </div>
</template>
<script>
// followers 关注我的粉丝
// followee 我关注的人
import { mapGetters } from 'vuex';
let cloud = require("../js/cloud")
export default {
  name: 'PopUpMultipleView',
  props: ['keyplayer', 'index', 'x', 'y'],
  data() {
    return {
      'width': 150,
      'height': 200,
      'svg_wordle': 0
    }
  },
  mounted: function() {
    let self = this

    let clickX = self.x
    let clickY = self.y
    let mids = []
    $('#regionView' + self.keyplayer + '_' + self.index).css({ left: clickX + 20, top: clickY + 20, position: 'absolute' })    
    for(let i in Global.clusters[self.keyplayer][self.index])
        mids.push(Global.clusters[self.keyplayer][self.index][i][0])
    let words = []
    let wordCnt = {}
    for (let i in mids) {
        let node = Global.mid2node[mids[i]]
        for (let j in node['words']) {
          // if (nodes[i]['words'][j] != "" && tmp.indexOf(nodes[i]['words'][j]) < 0) {
          //   tmp.push(nodes[i]['words'][j])
          //   if (!wordcnt.hasOwnProperty(nodes[i]['words'][j])) {
          //     words.push(nodes[i]['words'][j])
          //     wordcnt[nodes[i]['words'][j]] = 0
          //   }
          //   wordcnt[nodes[i]['words'][j]]++
          // }
            let word = node['words'][j]
            if (word == "") continue
            if (!wordCnt[word]) {
                wordCnt[word] = 0
                words.push(word)
            }
            wordCnt[word]++
        }
    }
    this.width = $('#regionView' + self.keyplayer + '_' + self.index).width()
    this.height = $('#regionView' + self.keyplayer + '_' + self.index).height()
    this.svg_wordle = d3.select('#regionView' + self.keyplayer + '_' + self.index)
    .append("svg")
    .attr("height", self.height)
    .attr("width", self.width)
    $('#regionView' + self.keyplayer + '_' + self.index).draggable()
    this.drawWordleView(words, wordCnt)
  },
  computed: {
    ...mapGetters(['keyplayersExtracted']),
  },
  watch: {
    keyplayersExtracted: function() {}
  },
  methods: {
    removeView: function() {
      let self = this
      $('#regionView' + self.keyplayer + '_' + self.index).remove()
    },
    drawWordleView: function(wordList, wordCnt) {
      let width = this.width,
        height = this.height,
        svg = this.svg_wordle

      var items = Object.keys(wordCnt).map(function(key) {
        return [key, wordCnt[key]];
      });

      // Sort the array based on the second element
      items.sort(function(first, second) {
        return second[1] - first[1];
      });

      // Create a new array with only the first 5 items
      let newList = items
      //let newList = items.slice(0, 150)

      let sizeExtent = d3.extent(newList, d => { return d[1] })
      // let scale = d3.scaleLinear().domain(sizeExtent).range([8 * Global.textUnitSize, 20 * Global.textUnitSize])
      let scale = Global.topicSizeScale
      let frequency_list = []
      for (let i in newList) {
        let tmp = {}
        tmp['size'] = scale(newList[i][1])
        tmp['text'] = newList[i][0]
        tmp['chinese'] = newList[i][0]
        tmp['id'] = tmp['text']
        tmp['sentiment'] = Global.wordSentiment[tmp['text']] / Global.keywordsCnt[tmp['text']]
        frequency_list.push(tmp)
      }
      // console.log(frequency_list)
      cloud().size([width - 10, height - 10])
        .words(frequency_list)
        // .padding(Global.textUnitSize * 2)
        .padding(5)
        .rotate(0)
        //.rotate(function() { return -45 + (Math.floor(Math.random() * 3)) * 45 })
        .text(function(d) {
          if (Global.language == 'English' && Global.translated) {
            return Global.chinese2english[d.text]
          }
          return d.text
        })
        //.font('Impact')
        .fontSize(function(d) { return d.size; })
        .on("end", this.drawWordle)
        .start();

    },
    drawWordle: function(words) {
      console.log("word sentiment", Global.wordSentiment)
      let self = this
      let width = this.width,
        height = this.height,
        svg = this.svg_wordle
      console.log(words)
      svg.selectAll('*').remove()
      svg.append("g")
        .attr("transform", "translate(" + width / 2 + "," + height / 2 + ")")
        .selectAll("text")
        .data(words)
        .enter().append("text")
        .style("font-size", function(d) { return d.size + "px"; })
        .style("font-weight", "400")
        .style("fill", function(d, i) {
          let tmp = d.sentiment
          if (tmp < 0.4)
            return Global.negativeColor
          if (tmp > 0.6)
            return Global.positiveColor
          return Global.neutralColor
        })
        .attr("text-anchor", "middle")
        .attr("transform", function(d) {
          return "translate(" + [d.x, d.y] + ")rotate(" + d.rotate + ")";
        })
        .text(function(d) {
          return d.text
        })
    }
  }
}

</script> <!-- Add "scoped" attribute to limit CSS to this component only -->



