<template>
  <div id="tableView">
    <table id="weibotable" class="display" width="100%"></table>
  </div>
</template>
<script>
require('../../../static/css/datatable.css')
import 'datatables.net'
import { mapGetters } from 'vuex';
export default {
  name: 'TableView',
  data() {
    return {
      requiredFileds: ['username', 'text', 't', 'mid', 'direct_reposts_count'],
      requiredIndex: [],
      dataList: [],
      cellCnt: 0,
      mappingMid: {},
      mid2Data: [],
      firstPlay: false,
      iDisplayLength: 10
    }
  },
  mounted: function() {
    // $('#weibotable').DataTable({
    //   data: [],
    //   columns: [
    //     { title: "Name" },
    //     { title: "Position" },
    //     { title: "Office" },
    //     { title: "Extn." }
    //   ]
    // });
    if($('#voronoiView').width() > 2000) {
      this.iDisplayLength = 40
    }
  },
  computed: {
    ...mapGetters(['dataLoaded', 'keyplayersExtracted',
      'clickNodeEvent', 'refreshStatus', 'highlightPolygons'
    ]),
  },
  watch: {
    keyplayersExtracted: function() {
      // return
      let self = this
      let filteredData = [],
        fields = Global.data['fields'],
        tree = Global.data['tree']
      self.requiredFileds.forEach(d => {
        self.requiredIndex.push(fields.indexOf(d))
      })
      self.dataList = []
      self.getDataList()
      // console.log(self.mappingMid)
      // console.log(self.dataList, self.cellCnt)
      self.updateTable(self.dataList, true)

    },
    refreshStatus: function() {
      Global.clickedNodes = []
      this.firstPlay = false
      this.updateTable(this.dataList, true)
    },
    highlightPolygons: function(signal) {
      if (Global.tableViewUpdate == false || Global.behaviorType == 'HighlightPath') return
      let self = this
      let data = []
      let highlightedPolygons = Global.highlightedPolygons
      let source = signal.split(" ")[0]
      if (this.firstPlay == false) {
        this.firstPlay = true
        $('#weibotable').DataTable().clear().draw()
        $('#weibotable').DataTable().order([2, "desc"])
      }
      console.log(Global.timeLineStatus, source)
      if (source == 'TimelineView' && (Global.timeLineStatus == 'Play' ||
          Global.timeLineStatus == 'Pause')) {
        let freshPolygons = Global.freshPolygons
        freshPolygons.forEach(mid => {
          data.push(self.mid2Data[mid])
        })
        this.addData(data)
      } else {
        highlightedPolygons.forEach(mid => {
          data.push(self.mid2Data[mid])
        })
        this.firstPlay = false
        data = data.reverse()
        this.updateTable(data, true)
      }
    },
    clickNodeEvent: function(val) {
      let self = this
      return
      let data = []
      val = val + ''
      if (val.split(' ')[0] == 'Table') return
      let clickedNodes = Global.nodesClickedOnMap
      // clickedNodes.forEach(mid => {
      //   data.push(self.mid2Data[mid])
      // })
      let table = $('#weibotable').DataTable()
      table.rows()
        .nodes()
        .to$()
        .removeClass('selected')

      for (let i in clickedNodes) {
        $('#' + clickedNodes[i]).addClass('selected')
      }
      // var indexes = table.rows().eq(0).filter(function(rowIdx) {
      //   let name = table.row(rowIdx).data()['Name'],
      //     weibo = table.row(rowIdx).data()['Weibo'],
      //     time = table.row(rowIdx).data()['Time']

      //   let mid = self.mappingMid[name + weibo + time]
      //   if (clickedNodes.indexOf(mid) >= 0) {
      //     return true
      //   } else {
      //     return false
      //   }
      // });

      // table.rows(indexes)
      //   .nodes()
      //   .to$()
      //   .addClass('selected')
      // data = data.reverse()
      // if (data.length == 0) {
      //   data = this.dataList
      //   this.updateTable(data, true)
      // } else {
      //   this.updateTable(data, false)
      // }
    }
  },

  methods: {
    readData: function(filename) {
      console.log('filename', filename)
    },
    getDataList: function() {
      let self = this
      let ndata = {},
        a = self.requiredIndex

      let nodes = Global.nodes
      for (let i in nodes) {
        let ndata = {}
        ndata['Name'] = nodes[i]['data'][a[0]]
        ndata['Weibo'] = nodes[i]['data'][a[1]].trim()
        if (ndata['Weibo'] == '') {
          ndata['Weibo'] = '转发微博'
        }
        ndata['Time'] = format(nodes[i]['data'][a[2]] * 1000)
        ndata['mid'] = nodes[i]['data'][a[3]]
        ndata['Num'] = nodes[i]['data'][a[4]]
        ndata['Depth'] = nodes[i].depth

        this.dataList.push(ndata)
        this.mappingMid[ndata['Name'] + ndata['Weibo'] + ndata['Time']] = ndata['mid']
        this.mid2Data[ndata['mid']] = ndata
      }
      // for (let i in tree['children']) {
      //   this.getDataList(tree['children'][i])
      // }

      function add0(m) { return m < 10 ? '0' + m : m }

      function format(shijianchuo) {
        var time = new Date(shijianchuo);
        var y = time.getFullYear();
        var m = time.getMonth() + 1;
        var d = time.getDate();
        var h = time.getHours();
        var mm = time.getMinutes();
        var s = time.getSeconds();
        return y + '-' + add0(m) + '-' + add0(d) + ' ' + add0(h) + ':' + add0(mm) + ':' + add0(s);
      }
    },
    updateTable: function(dataList, init) {
      let self = this
      let iDisplayLength = this.iDisplayLength
     
      $('#weibotable').empty()
      let schema = {
        destroy: true,
        data: dataList,
        "columnDefs": [{
          "orderable": false,
          "targets": [1]
        }],
        "iDisplayLength": iDisplayLength,
        rowId: 'mid',
        "lengthMenu": [iDisplayLength, 24, 26, 28, 30, 35, 45, 100],
        columns: [
          { data: "Name", title: "Name" },
          { data: "Weibo", title: "Weibo" },
          { data: "Time", title: "Time" },
          { data: "Num", title: "N" },
          { data: "Depth", title: "D" }
        ]
      }
      if (init) {
        schema["order"] = [
          // [2, "dsc"] // asc
          [2, "asc"]
        ]
      } else {
        schema['bSort'] = false
      }
      var table = $('#weibotable').DataTable(schema)
      if (init == false) {
        table.$('tr').addClass('selected')
      }
      $('#weibotable tbody').on('click', 'tr', function() {
        let name = table.row(this).data()['Name'],
          weibo = table.row(this).data()['Weibo'],
          time = table.row(this).data()['Time']
        // console.log(name, weibo, time)
        let mid = self.mappingMid[name + weibo + time]
        self.$store.state.clickNodeEvent = 'Table ' + Math.random()
        Global.clickedNodeMid = mid
        if ($(this).hasClass('selected')) {
          $(this).removeClass('selected')
          // PathView.removePath(mid)
        } else {
          // table.$('tr.selected').removeClass('selected');
          $(this).addClass('selected')
          // PathView.clickNode(mid, 'table')
        }
      });
    },
    addData: function(data) {
      // console.log(data)
      var table = $('#weibotable').DataTable()
      table.rows.add(data).draw()
      for(let i in data) {
        if(Global.insideKeyplayers.indexOf(data[i]['mid'])>=0) {
          $('#' + data[i]['mid']).addClass('selected')
        }
      }
      // for(let i in data) {

      // }
      // table.rows.InsertAt(data, 0)
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#tableView {
  /*background-color: #cccccc;*/
  padding-top: 0.2em;

}

#weibotable_length {
  /*display: none*/
  /*padding-top: 0.15em;*/
}

#weibotable_info {
  padding-top: 0.55em;
  font-size: 0.8em;
}

#weibotable_paginate {
  font-size: 0.8em
}


.dataTables_wrapper .dataTables_paginate .paginate_button {
  padding: 0.2em 0.4em
}

select {
  width: 60px;
}

table.dataTable thead th {
  padding: 2px;
}

input {
  width: 60px;
}

table.dataTable td {
  font-size: 0.8em;
  max-width: 0;
  overflow: hidden;
  /*text-overflow: ellipsis;*/
  white-space: nowrap;
}

table.dataTable tr:hover td {
  overflow: auto;
  text-overflow: clip;
  white-space: normal;
  cursor: pointer;
}

table.dataTable tr:active td {
  overflow: auto;
  text-overflow: clip;
  white-space: normal;
}

table.dataTable tr.selected {
  background: #FFCF8B;
}

table.dataTable tr.selected td {
  color: #555;
}

</style>



