      </button>
      <button class="mybutton" name='multiple' id='multiple' @click="changeInteraction">
        Multiples
      </button>
    </div>
    <div style=" position: relative; float: left; top: 0px; left: 14rem" id='buttonGroup'>
      <button class="mybutton" name='time' id='time' @click="changeLayout">
        Time
      </button>
      <button class="mybutton" name='semantic' id='semantic' @click="changeLayout">
        Semantics
      </button>
    </div>
    <div style="position: relative; float: left; top: 0px; left: 16rem" id='buttonGroup'>
      <button class="mybutton" name='english' id='english' @click="languageChange">
        {{ language }}
      </button>
      <button class="mybutton" name='legend' id='legend' @click="showLegend">
        Legend
      </button>
    </div>
    <div id="voronoi">
      <svg>
        <defs>
          <filter id="whiteOutlineEffect">
            <feMorphology in="SourceAlpha" result="MORPH" operator="dilate" radius= "1" />
            <feColorMatrix in="MORPH" result="WHITENED" type="matrix" values="-1 0 0 1 0, 0 -1 0 1 0, 0 0 -1 1 0, 0 0 0 1 0" />
            <feMerge>
              <feMergeNode in="WHITENED" />
              <feMergeNode in="SourceGraphic" />
            </feMerge>
          </filter>
        </defs>
      </svg>
      <PopUpView v-for="node in clickedNodeArr" :key="node.mid" :mid="node.mid" :x="node.x" :y="node.y" v-on:removePopUp="getRemovedMid">
      </PopUpView>
      <PopUpMultipleView v-for="node in clickedRegionArr" :key="node.keyplayer + '_' + node.index" :keyplayer="node.keyplayer" :index="node.index" :x="node.x" :y="node.y">
      </PopUpMultipleView>
      <!--     <div style="position:absolute; top: 0px; right: 30px" id='buttonGroup'>
        <button class="mybutton" name='country' id='country' @click="buttonClick">
          Country
        </button>
        <button class="mybutton" name='region' id='region' @click="buttonClick">
          Region
        </button>
        <button class="mybutton" name='city' id='city' @click="buttonClick">
          City
        </button>
        <button class="mybutton" name='river' id='river' @click="buttonClick">
          River
        </button>
        <button class="mybutton" name='bridge' id='bridge' @click="buttonClick">
          River
        </button>
        <button class="mybutton" name='route' id='route' @click="buttonClick">
          River
        </button>
      </div> -->
    </div>
    <LegendView></LegendView>
    <!-- <RepostView></RepostView> -->
    <!--     <div>
      <canvas id='canvas'></canvas>
    </div> -->
  </div>
</template>
<script>
require('../../../static/css/bootstrap.css')
import PathView from './js/path'
import TopicView from './js/topic'
import KeyplayerView from './js/keyplayer'
import DataPreprocess from './js/dataprocess'
import TreeProcess from './js/treeprocess'
import RiverView from './js/river'
import BoundaryView from './js/boundary'
import packCircles from './js/pack/index'
import enclose from "./js/pack/enclose";
