import LegendView from '@/components/map/legend/LegendView'

import RepostView from '@/components/repost/RepostView'

import { mapGetters } from 'vuex';


export default {
  name: 'VoronoiVue',
  components: { LegendView, PopUpView, RepostView, PopUpMultipleView },
  data() {
    return {
      divName: 'voronoi',
      nodeNum: 0,
      viewWidth: 0,
      viewHeight: 0,
      repostingCnt: 0,
      padding: { 'left': 10, 'right': 10, 'top': 30, 'bottom': 10 },
      buttonClicked: { 'country': true, 'region': true, 'city': true, 'river': true, 'route': true, 'bridge': true },
      nodesToShow: 0,
      min: 0,
      max: 0,
      layoutMethod: 'time',
      interactionMethod: 'single',
      keywordShown: true,
      nameShown: true,
      backgroundShown: true,
      similarityThreshold: 0.55,
      clickedNodeArr: [],
      clickedRegionArr: [],
      legendShow: true,
      language: 'En'
    }
  },
  mounted: function() {
    this.layoutMethod = Global.layoutMethod
    //this.interactionMethod = Global.interactionMethod
    for (let i in this.buttonClicked) {
      if (this.buttonClicked[i]) {
        d3.select('#' + i).classed("buttonClicked", true)
      } else {
        d3.select('#' + i).classed("buttonClicked", false)
      }
    }
    if($('#voronoiView').width() > 2000) {
      $('feMorphology').attr('radius', '2')
    }

    Global.keywordShown = this.keywordShown

    d3.select('#' + this.layoutMethod).classed("buttonClicked", true)

    d3.select('#' + this.interactionMethod).classed('buttonClicked', true)
    d3.select('#name').classed('buttonClicked', this.nameShown)
    d3.select('#keyword').classed('buttonClicked', this.keywordShown)
    d3.select('#background').classed('buttonClicked', this.backgroundShown)
    if (Global.language == 'English') {
      // d3.select('#english').classed('buttonClicked', this.keywordShown)
      this.language = '中'
    } else {
      this.language = 'En'
    }
    d3.select('#legend').classed('buttonClicked', this.legendShow)

    let self = this
    let filename = 'nanxiaoqi'
    self.init()

    let translated = false


    // Global.translated = translated
    // Global.filename = filename
    // if (translated) {
    //   d3.json('static/data/' + filename + '.json').then(function(data) {
    //     d3.json('static/translate/' + filename + '.json').then(function(dict) {
    //       Global.englishDictionary = {}
    //       Global.chineseDictionary = {}
    //       for (let i in dict['dict']) {
    //         let pair = dict['dict'][i]
    //         Global.englishDictionary[pair[0]] = pair[1]
    //         Global.chineseDictionary[pair[1]] = pair[0]
    //       }
    //       Global.data = data
    //       self.loadData()
    //     })
    //   })
    // } else {
    //   d3.json('static/data/' + filename + '.json').then(function(data) {
    //     Global.data = data
    //     self.loadData()
    //   })
    // }
    // })
    // self.svg.append('marker').attr('id', 'arrow')
    //      .attr('markerHeight', 5)
    //      .attr('markerWidth', 5)
    //      .attr('markerUnits', 'strokeWidth')
    //      .attr('orient', 'auto')
    //      .attr('refX', 5)
    //      .attr('refY', 5)
    //      .attr('viewBox', '0 0 10 10')
    //      .append('svg:path')
    //        .attr('d', "M 0 0 L 10 5 L 0 10 z")
    // self.svg.append('g').append('line')
    //   .attr("marker-end", "url(#arrow)")
    //   .attr('x1', 0).attr('y1', 2).attr('x2', 100).attr('y2', 100)
    //   .style("stroke", "black")
    //   .style("stroke-opacity", 0.8)
    //   .style("fill", "none")
    //   .style("pointer-events", "none")
    //   .style("stroke-width", 0.8)
    // return
    // this.loadData()
  },
  computed: {
    ...mapGetters(['keyplayerThresholdChange', 'dataLoaded',
      'highlightPolygons', 'refreshStatus',
      'firstAnimation', 'keyplayerKeywords', 'clickNodeEvent'
    ]),
  },
  watch: {
    keyplayerThresholdChange: function(val) {
      // KeyplayerView.updateKeyplayerThreshold(val
      this.loadData()
    },
    dataLoaded: function() {
      console.log('data load')
      // return
      this.loadData()
    },
    clickNodeEvent: function(val) {
      let self = this
      val = val + ''
      if (val.split(' ')[0] == 'Table') {
        let mid = Global.clickedNodeMid
        if (Global.nodesClickedOnMap.indexOf(mid) < 0) {
          let site = Global.mid2node[mid].polygon.site
          PathView.clickNode(mid)
          self.clickedNodeArr.push({ 'mid': mid, 'x': site[0], 'y': site[1] })
          Global.nodesClickedOnMap.push(mid)
        } else {
          self.getRemovedMid(mid)
        }
      }
    },
    firstAnimation: function() {
      this.animationClick()
      KeyplayerView.showKeyplayers([])
    },
    keyplayerKeywords: function() {
      console.log('keyplayerKeywords')
      if (this.layoutMethod == 'time') {
        d3.select('#topic_svg').selectAll('text').attr('visibility', 'hidden')
        d3.select('#topic_svg_all').selectAll('text').attr('visibility', 'visible')
        d3.select('#region_topic_svg').selectAll('text').attr('visibility', 'hidden')
      }
      if (this.layoutMethod == 'semantic') {
        d3.select('#topic_svg').selectAll('text').attr('visibility', 'hidden')
        d3.select('#topic_svg_all').selectAll('text').attr('visibility', 'hidden')
        d3.select('#region_topic_svg').selectAll('text').attr('visibility', 'visible')
      }
      TopicView.drawTopics(this.layoutMethod)
    },

    highlightPolygons: function(val, old) {
      // console.log(val, old)
      // this.animationClick(val)
      let self = this
      d3.select('#topic_svg').selectAll('text').attr('visibility', 'hidden')
      d3.select('#topic_svg_all').selectAll('text').attr('visibility', 'hidden')
      d3.select('#region_topic_svg').selectAll('text').attr('visibility', 'hidden')

      if (Global.behaviorType != 'HighlightPath') {
        Global.nodesClickedOnMap = []
        $('.snapshotView').remove()
        $('.popline').remove()
        d3.selectAll('.pathg').remove()
        self.clickedNodeArr = []
      }

      if(Global.highlightedPolygons.length  == 0) {
        $('#refreshButton').click()
        return
      }

      VoronoiView.animationUpdate(Global.highlightedPolygons)
      KeyplayerView.showKeyplayers(Global.highlightedPolygons)
      // RiverView.updateLayoutStatus(false)
      RiverView.pathUpdateAccordingEndPoints(Global.highlightedPolygons)
      BoundaryView.countryShowUpdate(false)
      BoundaryView.regionShowUpdate(false)
      if (val.split(',') == 'VoronoiView') {
        BoundaryView.regionShowUpdateForRegionSelection(Global.highlightedPolygons)
      }
      if (Global.timeLineStatus == 'Pause') {
        this.keywordShown = true
        Global.keywordShown = true
        d3.select('#keyword').classed('buttonClicked', this.keywordShown)
      }

      if (Global.timeLineStatus == 'Play' && $('#voronoiView').width() > 2000) {
        // 大屏幕上不实时计算
        Global.keywordShown = false
      }
      this.oceanShowUpdate(false)
      // d3.select('#topic_svg').selectAll('text').attr('visibility', 'hidden')
      // d3.select('#topic_svg_all').selectAll('text').attr('visibility', 'hidden')
      // d3.select('#region_topic_svg').selectAll('text').attr('visibility', 'hidden')
      TopicView.highlightTopics(val)
    },
    refreshStatus: function() {
      this.refreshClick()
    }
  },
  methods: {
    init: function() {
      let self = this
      let svgWidth = self.svgWidth = $('#' + self.divName).width()
      let svgHeight = self.svgHeight = $('#' + self.divName).height()
      self.viewWidth = svgWidth - self.padding.left - self.padding.right
      self.viewHeight = svgHeight - self.padding.top - self.padding.bottom
      // self.viewWidth = self.viewHeight = Math.min(self.viewWidth, self.viewHeight)
      self.svg = d3.select('#' + self.divName).select('svg')
        .attr('width', svgWidth)
        .attr('height', svgHeight)
        // .style('background-color', Global.seaColor)
        // .call(d3.zoom().scaleExtent([1, 12]).on("zoom", zoomed))
        .append('g')
        .attr('transform', 'translate(' + self.padding.left + ',' + self.padding.top + ')')
        .attr('id', 'map_svg')

      Global.textUnitSize = svgWidth / 1000

      function zoomed() {
        self.svg.attr("transform", d3.event.transform);
      }
    },
    showLegend: function() {
      this.legendShow = !this.legendShow
      d3.select('#legend').classed('buttonClicked', this.legendShow)

      if (this.legendShow) {
        d3.select('#legendView').style('display', 'block')
      } else {
        d3.select('#legendView').style('display', 'none')
      }

    },
    showKeyWord: function() {
      this.keywordShown = !this.keywordShown
      Global.keywordShown = this.keywordShown
      console.log(this.keywordShown)
      d3.select('#keyword').classed('buttonClicked', this.keywordShown)
      // this.svg.select('#topic_svg').selectAll('text').classed('unshown', !this.keywordShown)
      // this.svg.select('#region_topic_svg').selectAll('text').classed('unshown', !this.keywordShown)
      if (this.keywordShown) {
        if (Global.timeLineStatus == 'Play') {
          if (Global.layoutMethod == 'semantic') {
            this.svg.select('#topic_svg_all').selectAll('text').attr('visibility', 'hidden')
            this.svg.select('#topic_svg').selectAll('text').attr('visibility', 'hidden')
            this.svg.select('#region_topic_svg').selectAll('text').attr('visibility', 'visible')
          }
          if (Global.layoutMethod == 'time') {
            this.svg.select('#topic_svg_all').selectAll('text').attr('visibility', 'hidden')
            this.svg.select('#topic_svg').selectAll('text').attr('visibility', 'visible')
            this.svg.select('#region_topic_svg').selectAll('text').attr('visibility', 'hidden')
          }

        } else if (Global.timeLineStatus == 'Reset') {
          if (Global.layoutMethod == 'semantic') {
            this.svg.select('#topic_svg_all').selectAll('text').attr('visibility', 'hidden')
            this.svg.select('#topic_svg').selectAll('text').attr('visibility', 'hidden')
            this.svg.select('#region_topic_svg').selectAll('text').attr('visibility', 'visible')
          }
          if (Global.layoutMethod == 'time') {
            this.svg.select('#topic_svg_all').selectAll('text').attr('visibility', 'visible')
            this.svg.select('#topic_svg').selectAll('text').attr('visibility', 'hidden')
            this.svg.select('#region_topic_svg').selectAll('text').attr('visibility', 'hidden')
          }
        } else if (Global.timeLineStatus == 'Pause') {
          this.svg.select('#topic_svg_all').selectAll('text').attr('visibility', 'hidden')
          this.svg.select('#topic_svg').selectAll('text').attr('visibility', 'visible')
          this.svg.select('#region_topic_svg').selectAll('text').attr('visibility', 'hidden')

        }
      } else {
        this.svg.select('#topic_svg_all').selectAll('text').attr('visibility', 'hidden')
        this.svg.select('#topic_svg').selectAll('text').attr('visibility', 'hidden')
        this.svg.select('#region_topic_svg').selectAll('text').attr('visibility', 'hidden')
      }

    },
    showName: function() {
      this.nameShown = !this.nameShown
      d3.select('#name').classed('buttonClicked', this.nameShown)
      this.svg.select('#keyplayer_svg').selectAll('text').classed('unshown', !this.nameShown)
    },

    showBackground: function() {
      this.backgroundShown = !this.backgroundShown
      d3.select('#background').classed('buttonClicked', this.backgroundShown)
      if (this.backgroundShown) {
        d3.select('#voronoiView').style('background-color', Global.seaColor)
      } else {
        d3.select('#voronoiView').style('background-color', '#ffffff')
      }
    },

    languageChange: function() {
      console.log(Global.language)
      if (Global.language == 'Chinese') {
        // d3.select('#english').classed('buttonClicked', true)
        this.language = '中'
        Global.language = 'English'
      } else {
        // d3.select('#english').classed('buttonClicked', false)
        this.language = 'En'
        Global.language = 'Chinese'
      }
      this.$store.state.languageChange = Math.random()

      TopicView.drawTopics(Global.layoutMethod)

      this.svg.select('#keyplayer_svg').selectAll('text')
        .text(d => {
          // console.log(d.name, Global.chineseDictionary[d.name])
          if (Global.language == 'English') return Global.chinese2english[d.name]
          else return d.name.slice(0, Global.nameLength)
        })
    },
    getRemovedMid: function(mid) {
      let self = this

      function getRepostPath(mid) {
        let path = []
        while (1) {
          path.push(mid)
          mid = Global.mid2node[mid].pid
          if (mid == '') {
            break
          }
        }

        path = path.reverse()
        return path
      }

      let removedIds = getRepostPath(mid)
      let highlightedPolygons = Global.highlightedPolygons
      console.log('???,', highlightedPolygons)
      for (let i = 0; i < removedIds.length; i++) {
        let mid = removedIds[i]
        let index = highlightedPolygons.indexOf(mid)
        if (index >= 0) {
          highlightedPolygons.splice(index, 1)
        }
        console.log(index)
      }
      self.$store.state.highlightPolygons = 'VoronoiView ' + Math.random()

      let index = Global.nodesClickedOnMap.indexOf(mid)
      $('#snapshotView' + mid).remove()
      $('#popline' + mid).remove()
      self.clickedNodeArr.splice(index, 1)
      PathView.removePath(mid)
      Global.nodesClickedOnMap.splice(index, 1)
      self.$store.state.clickNodeEvent = Math.random()
    },
    animationClick: function() {
      console.log('hide elements')
      let self = this
      // this.playStatus = 'pause'
      self.buttonClicked.river = false
      // RiverView.riverShowUpdate(false)
      self.buttonClicked.region = false
      self.buttonClicked.country = false
      BoundaryView.regionShowUpdate(false)
      BoundaryView.countryShowUpdate(false)
      KeyplayerView.updateKeyplayersStatus(false)
      RiverView.updateLayoutStatus(false)
      VoronoiView.hideElements()
      self.oceanShowUpdate(false)

      function callBack() {
        self.buttonClicked.river = true
        // RiverView.riverShowUpdate(true)
        self.buttonClicked.region = true
        BoundaryView.regionShowUpdate(true)
        self.buttonClicked.country = true
        BoundaryView.countryShowUpdate(true)
        KeyplayerView.updateKeyplayersStatus(true)
        RiverView.updateLayoutStatus(true)
        self.oceanShowUpdate(true)
      }
    },
    refreshClick: function() {
      let self = this
      // self.playStatus = 'play'
      VoronoiView.refresh()
      self.buttonClicked.river = true
      RiverView.riverShowUpdate(true)

      if (self.layoutMethod == 'semantic') {
        self.buttonClicked.region = true
        BoundaryView.regionShowUpdate(true)
      }
      d3.select('g#topic_svg').selectAll('text').remove()
      self.buttonClicked.country = true
      BoundaryView.countryShowUpdate(true)
      // VoronoiView.baseMapUpdate(false)
      KeyplayerView.updateKeyplayersStatus(true)
      RiverView.updateLayoutStatus(true)
      PathView.removeAllPath()
      TopicView.showAll()
      self.oceanShowUpdate(true)
      Global.highlightedPolygons = []
      Global.freshPolygons = []
      self.clickedNodeArr = []
      Global.nodesClickedOnMap = []
      PathView.removeAllPath()
      d3.select('#pop_svg').selectAll('line').remove()
      d3.selectAll('.snapshotView').remove()

      $('#repostView').hide()
    },
    oceanShowUpdate: function(tag) {
      if (tag) {
        this.svg.select('#gaussion_svg').selectAll('path').style('fill-opacity', 1)
        this.svg.select('#gaussion_svg').selectAll('path').style('stroke-opacity', 1)
      } else {
        this.svg.select('#gaussion_svg').selectAll('path').style('fill-opacity', 0.1)
        this.svg.select('#gaussion_svg').selectAll('path').style('stroke-opacity', 0.1)
      }
    },
    nodesNumChange: function() {
      console.log(this.nodesToShow)
      this.svg.select('#gaussion_svg').selectAll('path').style('fill-opacity', 0.1)
      if (this.nodesToShow == this.max) {
        this.refreshClick()
      } else {
        VoronoiView.changeNodesShow(this.nodesToShow)
      }
    },
    buttonClick: function(e) {
      let btname = e.target.name
      this.buttonClicked[btname] = !this.buttonClicked[btname]
      if (this.buttonClicked[btname]) {
        d3.select('#' + btname).classed("buttonClicked", true)
      } else {
        d3.select('#' + btname).classed("buttonClicked", false)
      }
      if (btname == 'river') {
        RiverView.riverShowUpdate(this.buttonClicked[btname])
      }
      if (btname == 'city') {
        VoronoiView.cityShowUpdate(this.buttonClicked[btname])
      }
      if (btname == 'country') {
        BoundaryView.countryShowUpdate(this.buttonClicked[btname])
      }
      if (btname == 'region') {
        BoundaryView.regionShowUpdate(this.buttonClicked[btname])
      }
    },
    changeLayout: function(e) {
      let btname = e.target.name
      console.log(btname, this.layoutMethod)
      if (btname != this.layoutMethod) {
        $('#refreshButton').click()
        d3.select('#' + btname).classed('buttonClicked', true)
        d3.select('#' + this.layoutMethod).classed('buttonClicked', false)
        this.layoutMethod = btname
        Global.layoutMethod = btname
        // console.log(btname)
        // if (Global.layoutMethod[btname] == false) {
        //   Global.layoutMethod[btname] = true
        //   VoronoiMap.calTreeLayout(Global.keyplayers, this.svg, btname)
        // }
        if (this.layoutMethod == 'semantic') {
          VoronoiMap.polygons.forEach(d => {
            d.tree = d.semantic_tree
          })
          Global.nodes.forEach(d => {
            d.polygon = d.semantic_polygon
          })
        }
        if (this.layoutMethod == 'time') {
          VoronoiMap.polygons.forEach(d => {
            d.tree = d.time_tree
          })
          Global.nodes.forEach(d => {
            d.polygon = d.time_polygon
          })
        }
        VoronoiView.updateLayout(this.layoutMethod)
        // $('#refreshButton').click()
        if (this.layoutMethod == 'semantic') {
          this.buttonClicked['region'] = true
          d3.select('#region').classed("buttonClicked", true)
          if (Global.createdRegionBoundary == false) {
            BoundaryView.createInsideBoundary(Global.keyplayers, this.svg)
            Global.createdRegionBoundary = true
          }
          BoundaryView.regionShowUpdate(true)
          d3.select('g#topic_svg').selectAll('text').attr('visibility', 'hidden')
          d3.select('g#topic_svg_all').selectAll('text').attr('visibility', 'hidden')
          d3.select('g#region_topic_svg').selectAll('text').attr('visibility', 'visible')
        }
        if (this.layoutMethod == 'time') {
          this.buttonClicked['region'] = false
          d3.select('#region').classed("buttonClicked", false)
          BoundaryView.regionShowUpdate(false)
          // d3.select('#topic_svg').attr('visibility', 'hidden')
          // d3.select('#topic_svg_all').attr('visibility', 'hidden')
          d3.select('g#region_topic_svg').selectAll('text').attr('visibility', 'hidden')
        }
      }
    },
    changeInteraction: function(e) {
      let btname = e.target.name
      if (this.interactionMethod != btname) {
        d3.select('#' + this.interactionMethod).classed('buttonClicked', false)
        d3.select('#' + btname).classed('buttonClicked', true)
        this.interactionMethod = btname
        Global.interactionMethod = btname
      }

    },
    loadData: function() {
      let self = this
      let start = new Date()
      console.log(Global.data)
      DataPreprocess(Global.data)

      // FRLayout(self.svg, self.viewWidth, self.viewHeight, Global.nodes, Global.mid2node)
      // return
      console.log('data process time', new Date() - start)
      self.svg.selectAll('*').remove()
      $('.snapshotView').remove()
      // self.svg.append('rect')
