      function clickCallback(polygon, x, y) {
        // console.log(polygon)
        // 
        if (Global.interactionMethod == 'multiple') {
          let highlightedPolygons = []
          let clusterIndex = polygon.tree.clusterIndex
          let polygons = Global.keyplayers[polygon.keyplayerIndex].allocatedPolygons

          polygons.forEach(d => {
            let p = VoronoiMap.polygons[d]
            if (p.tree.clusterIndex == clusterIndex) {
              highlightedPolygons.push(p.tree.mid)
            }
          })

          self.clickedRegionArr.push({ 'keyplayer': polygon.keyplayerIndex, 'index': clusterIndex, 'x': x, 'y': y })
          //Global.highlightedPolygons = highlightedPolygons
          //self.$store.state.highlightPolygons = 'VoronoiView ' + Math.random()
        } else {
          let mid = polygon.tree.mid
          if (Global.nodesClickedOnMap.indexOf(mid) < 0) {
            PathView.clickNode(polygon.tree.mid)
            self.clickedNodeArr.push({ 'mid': polygon.tree.mid, 'x': x, 'y': y })
            Global.nodesClickedOnMap.push(mid)
            if (Global.behaviorType == 'HighlightPath') {
              Global.highlightedPolygons = Global.highlightedPolygons.concat(getRepostPath(mid))
            } else {
              Global.behaviorType = 'HighlightPath'
              Global.highlightedPolygons = getRepostPath(mid)
            }
            self.$store.state.highlightPolygons = 'VoronoiView ' + Math.random()
            // self.$store.state.clickNodeEvent = Math.random()
          } else {


            self.getRemovedMid(mid)
          }
        }
      }

      function getRepostPath(mid) {
        let path = []
        while (1) {
          path.push(mid)
          mid = Global.mid2node[mid].pid
          if (mid == '') {
            break
          }
        }

        path = path.reverse()
        return path
      }


