    erosionRelations: function(keyplayers) {

      // 默认都是true
      // 
      // 
      let sdis = 0.2 // 默认只有桥的距离
      for (let i = 0; i < keyplayers.length; i++) {
        for (let j = i + 1; j < keyplayers.length; j++) {
          let mid1 = keyplayers[i].mid,
            mid2 = keyplayers[j].mid
          Global.opinionRelations[mid1 + ',' + mid2] = true
          Global.opinionRelations[mid2 + ',' + mid1] = true
        }
      }

      for (let i = 1; i < keyplayers.length; i++) {
        // console.log(keyplayers[i])
        for (let j = i - 1; j >= 0; j--) {
          if (keyplayers[i].pid == keyplayers[j].mid) {
            let mid1 = keyplayers[i].mid,
              mid2 = keyplayers[j].mid
            let cos = Cos(keyplayers[i], keyplayers[j])
            console.log(1 - cos, keyplayers[i].name, keyplayers[j].name)
            Global.semanticDistance[mid1 + ',' + mid2] = 2 * (Global.similarityThreshold - cos) * sdis + sdis
            Global.semanticDistance[mid2 + ',' + mid1] = 2 * (Global.similarityThreshold - cos) * sdis + sdis
            console.log(keyplayers[i].name, keyplayers[j].name, cos)
            if (cos < Global.similarityThreshold) {
              // console.log(keyplayers[i].name, keyplayers[j].name, cos)
              Global.opinionRelations[mid1 + ',' + mid2] = false
              Global.opinionRelations[mid2 + ',' + mid1] = false
              keyplayers[i].oppose_parent = true
              Global.mid2node[keyplayers[i].mid].oppose_parent = true
            }
          }
        }
      }

      // 对于小的中间节点 直接当成岛屿

      for (let i = 0; i < keyplayers.length; i++) {
        let island = false
        for (let j = i + 1; j < keyplayers.length; j++) {
          if (keyplayers[j].pid == keyplayers[i].mid) {
            let cnt_p = keyplayers[i].all_reposts_count,
              cnt_c = keyplayers[j].all_reposts_count
            if ((cnt_p - cnt_c) < 0.01 * cnt_c) {
              let mid1 = keyplayers[i].mid,
                mid2 = keyplayers[j].mid

              Global.opinionRelations[mid1 + ',' + mid2] = false
              Global.opinionRelations[mid2 + ',' + mid1] = false

              island = true
              Global.semanticDistance[mid1 + ',' + mid2] = sdis
              Global.semanticDistance[mid2 + ',' + mid1] = sdis
            }
          }
          break
        }
        if (island) {
          for (let j = 0; j < keyplayers.length; j++) {
            if (keyplayers[j].mid == keyplayers[i].pid) {
              let mid1 = keyplayers[i].mid,
                mid2 = keyplayers[j].mid
              Global.opinionRelations[mid1 + ',' + mid2] = false
              Global.opinionRelations[mid2 + ',' + mid1] = false

              Global.semanticDistance[mid1 + ',' + mid2] = sdis
              Global.semanticDistance[mid2 + ',' + mid1] = sdis
            }
          }
        }
      }


      for (let i = 1; i < keyplayers.length; i++) {
        for (let j = i - 1; j >= 0; j--) {
          if (keyplayers[i].pid == keyplayers[j].mid) {

            let mid1 = keyplayers[i].mid,
              mid2 = keyplayers[j].mid

            if (Global.opinionRelations[mid1 + ',' + mid2] == false) {
              Global.islands[mid1] = true
            }

            if (Global.followingRelations[mid1 + ',' + mid2] == false) {
              Global.islands[mid1] = true
            }
          }
        }
      }

      // 默认都腐蚀
      for (let i = 0; i < keyplayers.length; i++) {
        for (let j = i + 1; j < keyplayers.length; j++) {
          let mid1 = keyplayers[i].mid,
            mid2 = keyplayers[j].mid
          Global.errosion[mid1 + ',' + mid2] = true
          Global.errosion[mid2 + ',' + mid1] = true
        }
      }

      for (let i = 0; i < keyplayers.length; i++) {
        for (let j = i + 1; j < keyplayers.length; j++) {
          let mid1 = keyplayers[i].mid,
            mid2 = keyplayers[j].mid,
            pid1 = keyplayers[i].pid,
            pid2 = keyplayers[j].pid

          if (mid1 == pid2) { // 一般父子不腐
            // console.log(keyplayers[i].name, keyplayers[j].name)
            // console.log(Global.opinionRelations[mid1 + ',' + mid2], Global.followingRelations[mid1 + ',' + mid2])
            if (Global.opinionRelations[mid1 + ',' + mid2] && Global.followingRelations[mid1 + ',' + mid2]) {
              Global.errosion[mid1 + ',' + mid2] = false
              Global.errosion[mid2 + ',' + mid1] = false
            }
          }

          if (pid1 == pid2) {
            if (!Global.islands[mid1] && !Global.islands[mid2]) {
              Global.errosion[mid1 + ',' + mid2] = false
              Global.errosion[mid2 + ',' + mid1] = false
            }
          }
        }
      }

      // for (let i = 0; i < keyplayers.length; i++) {
      //   for (let j = i + 1; j < keyplayers.length; j++) {
      //     let mid1 = keyplayers[i].mid,
      //       mid2 = keyplayers[j].mid,
      //       pid1 = keyplayers[i].pid,
      //       pid2 = keyplayers[j].pid

      //     if (Global.opinionRelations[mid1 + ',' + mid2] == false) {
      //       Global.errosion[mid1 + ',' + mid2] = true
      //       Global.errosion[mid2 + ',' + mid1] = true
      //     }
      //     if (Global.followingRelations[mid1 + ',' + mid2] == false) {
      //       Global.errosion[mid1 + ',' + mid2] = true
      //       Global.errosion[mid2 + ',' + mid1] = true
      //     }

      //     if (pid1 == pid2) {
      //       if (Global.islands[mid1] || Global.islands[mid1]) {
      //         Global.errosion[mid1 + ',' + mid2] = true
      //         Global.errosion[mid2 + ',' + mid1] = true
      //       }
      //     }
      //   }
      // }
    },

    rescale: function(keyplayers, polygons, viewWidth, viewHeight) {

      let padding = 10
      let minx = d3.min(keyplayers, d => { return d.v_node.x - d.v_node.r - padding }),
        maxx = d3.max(keyplayers, d => { return d.v_node.x + d.v_node.r + padding }),
        miny = d3.min(keyplayers, d => { return d.v_node.y - d.v_node.r - padding }),
        maxy = d3.max(keyplayers, d => { return d.v_node.y + d.v_node.r + padding })

      let bw = d3.max(polygons, d => {
        return d3.max(d.boundary, d2 => { return d2[0] })
      }) - d3.min(polygons, d => {
        return d3.min(d.boundary, d2 => { return d2[0] })
      })

      let bh = d3.max(polygons, d => {
        return d3.max(d.boundary, d2 => { return d2[1] })
      }) - d3.min(polygons, d => {
        return d3.min(d.boundary, d2 => { return d2[1] })
      })
      // console.log(viewWidth, viewHeight, bw, bh)
      padding = 0
      let w = maxx - minx,
        h = maxy - miny

      let scale = Math.min(bw / w, bh / h)

      let cx = d3.sum(polygons, d => {
        return d.site[0]
      }) / polygons.length

      let cy = d3.sum(polygons, d => {
        return d.site[1]
      }) / polygons.length
      // console.log(cx, cy)
      let centerx = (maxx + minx) / 2,
        centery = (maxy + miny) / 2
      polygons.forEach(d => {
        d.site[0] = (d.site[0] - cx) / scale + centerx
        d.site[0] = +d.site[0].toFixed(2)

        d.site[1] = (d.site[1] - cy) / scale + centery
        d.site[1] = +d.site[1].toFixed(2)

        d.boundary.forEach(b => {
          b[0] = (b[0] - cx) / scale + centerx
          b[0] = +b[0].toFixed(2)

          b[1] = (b[1] - cy) / scale + centery
          b[1] = +b[1].toFixed(2)

        })

        d.neighborEdges.forEach(e => {
          e[0][0] = (e[0][0] - cx) / scale + centerx
          e[0][0] = +e[0][0].toFixed(2)

          e[0][1] = (e[0][1] - cy) / scale + centery
          e[0][1] = +e[0][1].toFixed(2)

          e[1][0] = (e[1][0] - cx) / scale + centerx
          e[1][0] = +e[1][0].toFixed(2)

          e[1][1] = (e[1][1] - cy) / scale + centery
          e[1][1] = +e[1][1].toFixed(2)
        })

      })

      // bw = d3.max(polygons, d => {
      //   return d3.max(d.boundary, d2 => { return d2[0] })
      // }) - d3.min(polygons, d => {
      //   return d3.min(d.boundary, d2 => { return d2[0] })
      // })

      // bh = d3.max(polygons, d => {
      //   return d3.max(d.boundary, d2 => { return d2[1] })
      // }) - d3.min(polygons, d => {
      //   return d3.min(d.boundary, d2 => { return d2[1] })
      // })

      // console.log('dfdff', bw, bh)

      VoronoiMap.polygonsBoundary = { 'topLeft': { 'x': minx, 'y': miny }, 'bottomRight': { 'x': maxx, 'y': maxy } }


    },



