        })
        .attr('cy', d => {
          return d.v_node.y
        })
        .attr('r', d => { return 3 })
        .attr('fill', Global.riverColor)


    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.mybutton {
  background-color: white;
  border: 0.5px solid #ccc;
  border-radius: 2px;
  color: black;
  float: left;
  box-shadow: none;

  /*padding: 15px 32px;*/
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 0.7rem !important;
  /*margin: 4px 2px;*/
  cursor: pointer;
}


@media only screen and (min-width: 500px) {
  #voronoi {
    position: absolute;
    top: 20px;
    width: 100%;
    height: calc(100% - 20px);
  }

  #legendView {
    position: absolute;
    right: 0%;
    bottom: 0%;
    /*height: 22%;*/
    width: 300px;
    /*width: 20%;*/
    background-color: rgb(227, 238, 249);
    border: 1px solid rgb(207, 225, 242);
    border-radius: 5px;
  }

  #repostView {
    position: absolute;
    left: 5px;
    top: 5%;
    /*height: 22%;*/
    width: 300px;
    /*width: 20%;*/
    background-color: rgb(227, 238, 249);
    border: 1px solid rgb(207, 225, 242);
    border-radius: 5px;
  }
}

@media only screen and (min-width: 2000px) {
  #voronoi {
    position: absolute;
    top: 30px;
    width: 100%;
    height: calc(100% - 30px);
  }

  #legendView {
    position: absolute;
    right: 0%;
    bottom: 0%;
    /*height: 22%;*/
    width: 550px;
    /*width: 20%;*/
    background-color: rgb(227, 238, 249);
    border: 1px solid rgb(207, 225, 242);
    border-radius: 5px;
  }

  #repostView {
    position: absolute;
    left: 5px;
    top: 5%;
    /*height: 22%;*/
    width: 500px;
    /*width: 20%;*/
    background-color: rgb(227, 238, 249);
    border: 1px solid rgb(207, 225, 242);
    border-radius: 5px;
  }
}


.mybutton.focus,
.mybutton:focus {
  outline: 0;
  box-shadow: none !important;
}

.el-slider__bar {
  background-color: #ccc !important;
}

.el-slider__button {
  border: 2px solid gray;
}

.buttonClicked {
  background-color: #e6e6e6 !important;
}

.unshown {
  display: none;
}

</style>



