var nodeCnt = 0
var DataPreprocess = function(data) {
  let tree = data['tree']
  let rt = +tree['data'][Global.data.fields.indexOf('t')]
  nodeCnt = 0
  Global.routeColor = d3.interpolateBlues(0.45)
  Global.nodes = []
  Global.mid2node = {}
  Global.rootUser = tree['data'][Global.data.fields.indexOf('username')]
  Global.rootText = tree['data'][Global.data.fields.indexOf('text')]
  Global.mid2node = {}
  Global.semanticDistance = {}
  Global.interactionMethod = 'single'
  Global.maxDepth = 0
  Global.mid2cluster = {}
  Global.followingRelations = {}
  Global.opinionRelations = {}
  Global.siblings = {}
  Global.parentChildren = {}
  Global.keywordsCnt = {}
  Global.keywords = []
  Global.flows = {}
  Global.errosion = {}
  Global.islands = {}
  Global.keyplayersTree = ''
  Global.keyplayers = []
  Global.wordSentiment = {}
  Global.nodesClickedOnMap = []
  Global.createdRegionBoundary = false
  Global.gaussionValueThreshold = 0.1
  goTree(tree, 1, rt, undefined)
  treeSort(tree, 't')
  tree['pid'] = ''
  console.log(nodeCnt)
}

function goTree(tree, depth, rt, parent) {

  nodeCnt++
  tree['mid'] = tree['data'][Global.data.fields.indexOf('mid')]
  tree['pid'] = tree['data'][Global.data.fields.indexOf('parent')]
  tree['t'] = tree['data'][Global.data.fields.indexOf('t')]
  tree['follower'] = tree['data'][Global.data.fields.indexOf('followers_count')]
  tree['followee'] = tree['data'][Global.data.fields.indexOf('friends_count')]
  tree['rt'] = (+tree['t']) - rt // 相对于根节点的延迟时间
  tree['direct_reposts_count'] = tree['children'].length
  tree['all_reposts_count'] = 0
  tree['text'] = tree['data'][Global.data.fields.indexOf('text')].trim()
  tree['depth'] = depth
  tree['type'] = ''
  tree['positive_prob'] = tree['data'][Global.data.fields.indexOf('positive_prob')]
  tree['sentiment'] = tree['data'][Global.data.fields.indexOf('sentiment')]

  tree['uid'] = tree['data'][Global.data.fields.indexOf('uid')]
  // if(tree.mid == '3426824078420786') {
  //   console.log(tree['data'][Global.data.fields.indexOf('following_parent')])
  //   console.log(parent['uid'], tree['uid'])
  // }
  tree['following_parent'] = tree['data'][Global.data.fields.indexOf('following_parent')]
  // dxy 1735747402
  // zilin 1251560221
  if (parent != undefined) {
    if (parent['uid'] == '1735747402' && tree['uid'] == '1251560221') {
      tree['following_parent'] = false
    }
    if (parent['uid'] == '1251560221' && tree['uid'] == '1735747402') {
      tree['following_parent'] = false
    }

    if (parent['uid'] == '1619196775' && tree['uid'] == '1471143571') {
      tree['following_parent'] = false
    }


    if (parent['uid'] == '3189089501' && tree['uid'] == '1619196775') {
      tree['following_parent'] = false
    }
    if (parent['uid'] == '2170291565' && tree['uid'] == '2662148103') {
      tree['following_parent'] = false
    }



  }

  tree['oppose_parent'] = tree['data'][Global.data.fields.indexOf('oppose_parent')]
  let words = tree['data'][Global.data.fields.indexOf('words')]
  tree['words'] = []
  for (let i = 0; i < words.length;) {
    if (words[i] == '🇨' && words[i + 1] == '🇳') {
      tree.words.push('中国')
      i += 2
      continue
    }

    if (words[i] == '🇯' && words[i + 1] == '🇵') {
      tree.words.push('日本')
      i += 2
      continue
    }

    if (words[i] == '中' && words[i + 1] == '日') {
      tree.words.push('中日')
      i += 2
      continue
    }
    if (words[i] == '👏') {
      words[i] = '鼓掌'
    }
    if (words[i] == '🎉') {
      words[i] = '祝贺'
    }
    if (words[i] == '🙏') {
      words[i] == '保佑'
    }
    if (words[i] == '骷髅') {
      words[i] = '害怕'
    }
    if (words[i] == '人類') {
      words[i] = '人类'
    }

    if (words[i] == '實驗') {
      words[i] = '实验'
    }
    if (words[i] == '编') {
      words[i] = '编辑'
    }
    if (words[i] == '夢') {
      words[i] = '梦'
    }

    if (words[i] == '南小七') {
      words[i] = '南小七7'
    }
    if (words[i] == '微博真') {
      words[i] = '微博'
    }
    if (words[i] == '太牛') {
      words[i] = 'good'
    }
    if (words[i] == '大嘴') {
      words[i] = '姚晨'
    }
    if (words[i] == '時') {
      words[i] = '时'
    }

    if (isEmojiCharacter(words[i])) {
      i = i + 1
      continue
    }


    if (Global.stopWords.indexOf(words[i]) >= 0) {
      i += 1
      continue
    }
    tree.words.push(words[i])
    i += 1
  }
  tree['positive_prob'] = tree['data'][Global.data.fields.indexOf('positive_prob')]

  if (parent != undefined) {
    if (parent['uid'] == tree['uid']) {
      tree['following_parent'] = true
    }
  }
  // if(nodeCnt > 20000) {
  //   console.log('wrong loop')
  //   return
  // }
  // console.log(depth)
  // 只考虑孩子没有关注父亲
  Global.followingRelations[tree['mid'] + ',' + tree['pid']] = tree['following_parent'] //
  Global.followingRelations[tree['pid'] + ',' + tree['mid']] = tree['following_parent'] //

  // Global.opinionRelations[tree['pid'] + ',' + tree['mid']] = !tree['oppose_parent']
  // Global.opinionRelations[tree['mid'] + ',' + tree['pid']] = !tree['oppose_parent']

  // Global.parentChildren[tree['pid'] + ',' + tree['mid']] = true
  // Global.parentChildren[tree['mid'] + ',' + tree['pid']] = true

  let tmp = []
  for (let i in tree['words'])
    if (tree['words'][i] != '' && tmp.indexOf(tree['words'][i]) < 0) {
      tmp.push(tree['words'][i])
      if (!Global.wordSentiment.hasOwnProperty(tree['words'][i]))
        Global.wordSentiment[tree['words'][i]] = 0
      Global.wordSentiment[tree['words'][i]] += tree['positive_prob']
    }

  for (let i in tmp) {
    if (!Global.keywordsCnt.hasOwnProperty(tmp[i]))
      Global.keywordsCnt[tmp[i]] = 0
    Global.keywordsCnt[tmp[i]]++
  }


  tree['name'] = tree['data'][Global.data.fields.indexOf('username')]

  Global.nodes.push(tree)
  Global.mid2node[tree['mid']] = tree
  for (let i in tree['children']) {
    goTree(tree['children'][i], depth + 1, rt, tree)
    tree['all_reposts_count'] += tree['children'][i]['all_reposts_count'] + 1
  }
  // 太耗时间
  // for (let i = 0; i < tree['children'].length; i++) {
  //   for (let j = i + 1; j < tree['children'].length; j++) {
  //     let mid1 = tree['children'][i].mid,
  //       mid2 = tree['children'][j].mid
  //     Global.siblings[mid1 + ',' + mid2] = true
  //     Global.siblings[mid2 + ',' + mid1] = true
  //   }
  // }
  tree['weight'] = Math.sqrt(tree['all_reposts_count'])
  if (depth > Global.maxDepth) {
    Global.maxDepth = depth
  }
}

function treeSort(tree, key) {
  if (tree['children'].length > 1) {
    tree['children'].sort(function(a, b) {
      return a[key] - b[key]
    })
    for (let i in tree['children']) {
      treeSort(tree['children'][i], key)
    }
  }
}

function treeSort_back(tree, key1, key2) {
  let fields = Global.data.fields
  let index1 = fields.indexOf(key1)
  let index2 = fields.indexOf(key2)
  if (tree['children'].length > 1) {
    tree['children'].sort(function(c, d) {
      let a = c['data']
      let b = d['data']
      // console.log(a[index1], b[index2])
      if (+a[index1] < +b[index1]) return -1
      if (+a[index1] > +b[index2]) return 1
      if (+a[index1] == +b[index2]) {
        return +a[index2] - (+b[index2])
      }
    })
    for (let i in tree['children']) {
      treeSort(tree['children'][i], key1, key2)
    }
  }
}


export default DataPreprocess



