<template>
  <div class="repostView" id="repostView" style="background:white; border: 1px solid gray; border-radius: 3px; font-size:0.5rem; ">
    <div id='left' style="float: left;">
      <div id='photo' style="padding: 2px">
        <img src='static/images/nanxiaoqi.jpg' id='rphoto' />
      </div>
    </div>
    <div id='right' style="float: right; padding: 2px; font-weight: bold;">
      <div id="head"> {{ name }}
        <div class="btn-group btn-group-xs" role="group" aria-label="..." style="float: right;">
          <span :id="'span' + mid" class="repostremove" @click="removeView">
            <font-awesome-icon icon="times" />
          </span>
        </div>
      </div>
      <div id="content" style="margin: 2px; font-size: 0.4rem; background-color: white; font-size: 0.4rem; text-align:left;">{{ text }}</div>
      <div id="time" style="float: left; margin: 2px; font-size: 0.4rem; background-color: white; font-size: 0.4rem; text-align:left;"> {{ time }}</div>
      <div id="rnum" style="float: left; margin: 2px; font-size: 0.4rem; background-color: white; font-size: 0.4rem; text-align:left;"> {{ rnum }}</div>
    </div>
  </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
  name: 'PopUpView',
  data() {
    return {
      'name': 'dfd',
      'text': '',
      'removedMid': '',
      'repostWidth': '13rem',
      'headSize': '0.5rem',
      'textFontSize': '0.4rem',
      'nameFontSize': '0.5rem',
      'mid': '',
      'text': 'dfdfdfdfd',
      'time': '',
      'rnum': 0
    }
  },
  mounted: function() {
    let self = this
    d3.select('#repostView' + self.mid).style('border', '1px solid gray')
      .style('width', this.repostWidth)
    let width = $('#repostView' + self.mid).width()
    let ratio = 1 / 3
    let left_width = width * ratio * 0.99,
      right_width = width * (1 - ratio) * 0.99
    d3.select('#repostView').select('#left').style('width', left_width + 'px')
    d3.select('#repostView').select('#right').style('width', right_width + 'px')

    d3.select('#repostView').select('#left').style('font-size', this.nameFontSize)
    d3.select('#repostView').select('#right').style('font-size', this.textFontSize)
    $('#rphoto').css('width', left_width)
    $('.repostView').hide()
  },
  computed: {
    ...mapGetters(['highlightPolygons']),
  },
  watch: {
    highlightPolygons: function() {
      let self = this
      if (!Global.freshPolygons || !Global.freshPolygons.length) return
      let data = Global.freshPolygons.map(d => {
        return Global.mid2node[d]
      }).filter(d => {
        return d.all_reposts_count > Global.insideKeyplayerThreshold
      })

      if (data.length == 0) return

      let item = '',
        cnt = -1;
      for (let i in data) {
        if (data[i].all_reposts_count > cnt) {
          cnt = data[i].all_reposts_count
          item = data[i]
        }
      }
      // console.log(item)
      // loadImg(0)
      loadSingleImg(item)

      function loadSingleImg(item) {

        self.text = item.text
        self.name = item.name
        self.time = 'Delay: ' + self.delayTimeFormat(item.rt) // 相对于根节点延迟时间
        self.rnum = '#Repost: ' + item.all_reposts_count
        let uid = 'common'
        if (Global.filename == 'nanxiaoqi') {
          uid = item['uid']
        }
        console.log(uid)
        $('#rphoto').attr('src', 'static/images/' + uid + '.jpg')
        let color = SentimentColor(item)
        d3.select('#repostView').style('border', '1px solid ' + color)
        d3.select('#repostView').select('#head').style('background-color', color)
          .style('font-size', self.headSize)
        let height = $('#repostView').height()
        let width = $('#photo').width()

        $('#rphoto').on('load', function() {
          $('#repostView').show()
        })
      }


      function loadImg(i) {
        if (i >= data.length) return
        // console.log(data[i])
        // $('.repostView').hide()
        console.log(self.name, i, data.length)
        self.text = data[i].text
        self.name = data[i].name
        self.time = 'Delay: ' + self.delayTimeFormat(data[i].rt) // 相对于根节点延迟时间
        self.rnum = '#Repost: ' + data[i].all_reposts_count
        let uid = data[i]['uid']
        $('#rphoto').attr('src', 'static/images/' + uid + '.jpg')
        let color = SentimentColor(data[i])
        d3.select('#repostView').style('border', '1px solid ' + color)
        d3.select('#repostView').select('#head').style('background-color', color)
          .style('font-size', self.headSize)
        let height = $('#repostView').height()
        let width = $('#photo').width()

        if (i == 0) {
          $('#repostView').show()
        }

        $('#rphoto').on('load', function() {
          // $('#repostView').show()
          loadImg(i + 1)
        })
        // $('#rphoto').css('margin-top', (height - width) / 2)
        // d3.select('#repostView').style('border-top', '3px solid' + color)
      }



    }
  },
  methods: {
    removeView: function() {
      $('#repostView').remove()
    },
    delayTimeFormat: function(d) {
      if (d == 0) return 0
      d = +d
      if (d < 60) {
        return d + 's'
      }
      if (d < 3600) {
        return (d / 60).toFixed(1) + 'm'
      }
      if (d < 3600 * 24) {
        return (d / 3600).toFixed(1) + 'h'
      }
      if (d < 3600 * 24 * 30) {
        return (d / 3600 / 24).toFixed(1) + 'd'
      }
      if (d < 3600 * 24 * 365) {
        return (d / 3600 / 24 / 30).toFixed(1) + 'M'
      }
      return (d / 3600 / 24 / 365).toFixed(1) + 'y'
    },
  }
}

</script> <!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.snapshotView {
  cursor: pointer;
}

</style>



