<template>
  <div id="main">
    <b-navbar toggleable="md" type="dark" variant="info">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-navbar-brand href="#">R-Map</b-navbar-brand>
      <b-collapse is-nav id="nav_collapse">
        <b-navbar-nav>
          <b-nav-item-dropdown left>
            <!-- Using button-content slot -->
            <template slot="button-content">
              Data
            </template>
            <b-dropdown-item v-for="file in filelist" :key="file" @click="readData(file)">{{ file }}
            </b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
        <b-navbar-nav style="margin-left: 20px">
          <b-nav-text> Root: {{ defaultFile }} </b-nav-text>
          <b-nav-text id='rootWeibo'> Weibo: {{ shortWeibo }} </b-nav-text>
          <b-nav-text> Repostings: {{ repostingNumStr }} </b-nav-text>
          <b-nav-text> Depth: {{ repostDepth }} </b-nav-text>
          <b-tooltip ref="tooltip" target="rootWeibo" placement="bottom">
            {{ rootWeibo }}
          </b-tooltip>
         <!--  <b-nav-text> Key Player </b-nav-text>
          <div style="display: inline-block; width: 10rem; margin-left: 0.5rem;">
            <el-slider v-model="keyplayerThreshold" :min="min" :max="max" @change="sliderChange"></el-slider>
          </div> -->
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <Voronoi></Voronoi>

    <TableView></TableView>
    <TimelineView></TimelineView>

    <TopicView></TopicView>
 
    <div class='loader'>
      <div class='background'> </div>
      <div class="inloader"> </div>
    </div>
  </div>
</template>
<script>
import Voronoi from '@/components/map/Voronoi'
import TableView from '@/components/table/TableView'
// import SkeletonView from '@/components/skeleton/SkeletonView'
import TimelineView from '@/components/timeline/TimelineView'
import TopicView from '@/components/topic/TopicView'
import { mapGetters } from 'vuex'
window.TimelineView = TimelineView
window.Voronoi = Voronoi
window.TopicView = TopicView
window.TableView = TableView
export default {
  name: 'Main',
  components: { Voronoi, TableView, TimelineView, TopicView },
  data() {
    return {
      keyplayerThreshold: 0,
      min: 0,
      max: 600,
      name: '',
      rootUser: '',
      repostingNum: 0,
      repostingNumStr: 0,
      repostDepth: 0,
      shortWeibo: '...',
      rootWeibo: '',
      defaultFile: '南小七',
      // defaultFile: '婴儿',
      // defaultFile: 'chunwan',
      // defaultFile: 'mumu',
      // defaultFile: 'anbeijinsan',
      // defaultFile: 'qinshi',
      // defaultFile: '婴儿',
      // defaultFile: '安倍晋三',
      // defaultFile: '春晚',
      // defaultFile: 'liulangdiqiu',
      // defaultFile: '基因编辑',
      // defaultFile: '王自如',
      // defaultFile: '奔驰',
      filelist: ['雷军', '安倍晋三', '刘欣辩论', '南小七', '春晚', '刘慈欣', '基因编辑', '婴儿', '王自如'],
      fileMapping: ['leijun',  'anbeijinsan', 'debate', 'nanxiaoqi', 'chunwan', 'liucixin', 'jiyinbianji', 'baby1', 'wangziru']
    }
  },
  mounted: function() {
    let self = this
    d3.select(this.$el).selectAll('.el-slider__runway')
    d3.select(this.$el).selectAll('.el-slider__bar').style('background-color', '#9e9e9e')
    d3.select(this.$el).selectAll('.el-slider__button')
      .style('border', '2px solid #aaa')
    let width = $('#main').width()
    console.log('screen, size', width)
    $('.inloader').css({ 'width': width / 30, 'height': width / 30 })
    document.documentElement.style.fontSize = width / 2
    let filename = this.defaultFile
    this.name = this.defaultFile

    this.readData(filename)

  },
  computed: {
    ...mapGetters(['keyplayersExtracted', 'layoutFinishied', 'highlightPolygons']),
  },
  watch: {
    keyplayersExtracted: function() {
      this.repostingNum = Object.keys(Global.mid2node).length
      this.repostingNumStr = this.repostingNum
      this.rootUser = Global.rootUser
      this.repostDepth = Global.maxDepth
      this.rootWeibo = Global.rootText
      // this.max = Global.nodes.length
      this.keyplayerThreshold = Global.keyplayerThreshold
    },
    layoutFinishied: function(val) {
      $('.loader').hide()
      if(Global.language == 'English') {
        this.defaultFile = 'Little7'
      }
    },
    highlightPolygons: function(val) {
      this.repostingNumStr = Global.highlightedPolygons.length + '/' + this.repostingNum
    }
  },
  methods: {
    readData: function(filename) {
      let self = this
      let translated = Global.translated
      console.log(filename)
      self.defaultFile = filename
      filename = this.fileMapping[this.filelist.indexOf(filename)]

      Global.filename = filename
      $('.loader').show()
      if (translated) {
        d3.json('static/data/' + filename + '.json').then(function(data) {
          d3.json('static/translate/' + filename + '.json').then(function(dict) {
            // self.loadingInstance = Loading.service({ fullscreen: true })
            Global.english2chinese = {}
            Global.chinese2english = {}
            let existSameWord = false
            for (let key in dict['dict']) {
              let value = dict['dict'][key]
              if (Global.english2chinese[value] != undefined) {
                console.log(value)
                existSameWord = true
              }
              // Global.englishDictionary[key] = value
              Global.chinese2english[key] = value
              Global.english2chinese[value] = key
            }
            Global.data = data
            self.$store.state.dataLoaded = Math.random()
          })
        })
      } else {
        d3.json('static/data/' + filename + '.json').then(function(data) {
          // self.loadingInstance = Loading.service({ fullscreen: true })
          Global.data = data
          if(Global.language == 'English') {
            Global.language = 'Chinese'
          }
          self.$store.state.dataLoaded = Math.random()
        })
      }
    },
    sliderChange: function() {
      // this.loadingInstance = Loading.service({ fullscreen: true })
      Global.keyplayerThreshold = this.keyplayerThreshold
      self.$store.state.keyplayerThresholdChange = Math.random()

    }

  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
