      border-radius: 5px;
    }
    #topicView {
      position: absolute;
      right: calc(75% + 2px);
      left: 2px;
      top: calc(68% + 2px);
      bottom: 5px;
      border: 1px solid #ccc;
      border-radius: 5px;
    }

    #voronoiView {
      position: absolute;
      /*width: 70%;*/
      left: 25%;
      right: calc(15% + 2px);
      top: 40px;
      height: calc(100% - 40px - 5px);
      /*bottom: 0%;*/
      border: 1px solid #ccc;
      border-radius: 5px;
        /*bottom: 5px;*/
    }

    #timelineView {
      position: absolute;
      /*width: 70%;*/
      left: 85%;
      width: 15%;
      top: 40px;
      height: calc(100% - 40px - 5px);
      /*bottom: 0%;*/
      border: 1px solid #ccc;
      border-radius: 5px;
      background-color:
    }
}

@media only screen and (min-width: 4000px){
    html {
        font-size: 150%!important; /* 16*/
    }
    #tableView {
      position: absolute;
      left: 2px;
      right: calc(75% + 2px);
      top: 60px;
      bottom: 32%;
      border: 1px solid #ccc;
      border-radius: 5px;
    }
    #topicView {
      position: absolute;
      right: calc(75% + 2px);
      left: 2px;
      top: calc(68% + 2px);
      bottom: 5px;
      border: 1px solid #ccc;
      border-radius: 5px;
    }

    #voronoiView {
      position: absolute;
      /*width: 70%;*/
      left: 25%;
      right: calc(15% + 2px);
      top: 60px;
      height: calc(100% - 60px - 5px);
      /*bottom: 0%;*/
      border: 1px solid #ccc;
      border-radius: 5px;
      /*background-color: red;*/
    }

    #timelineView {
      position: absolute;
      /*width: 70%;*/
