      h = 200 / 3
    } else {
      svg.append("circle").attr("cx", 250 / 3).attr("cy", 50 / 3).attr("r", 5).attr("fill", Global.negativeColor).on("click", d => {
        Global.highlightedPolygons = mids[0]
        Global.tableViewUpdate = true
        Global.topicViewUpdate = true
        Global.behaviorType = 'PopUpView'
        self.$store.state.highlightPolygons = 'PopUpView ' + Math.random()
      })
      svg.append("circle").attr("cx", 250 / 3).attr("cy", 50).attr("r", 5).attr("fill", Global.neutralColor).on("click", d => {
        Global.highlightedPolygons = mids[1]
        Global.tableViewUpdate = true
        Global.topicViewUpdate = true
        Global.behaviorType = 'PopUpView'
        self.$store.state.highlightPolygons = 'PopUpView ' + Math.random()
      })
      svg.append("circle").attr("cx", 250 / 3).attr("cy", 250 / 3).attr("r", 5).attr("fill", Global.positiveColor).on("click", d => {
        Global.highlightedPolygons = mids[2]
        Global.tableViewUpdate = true
        Global.topicViewUpdate = true
        Global.behaviorType = 'PopUpView'
        self.$store.state.highlightPolygons = 'PopUpView ' + Math.random()
      })
      h = 100
    }

    if (node.pid != "")
      svg.append('text').text(parent.name).attr('x', 5).attr('y', sty + 25)
    svg.attr("height", h)
  },
  computed: {
    ...mapGetters(['keyplayersExtracted']),
  },
  watch: {
    keyplayersExtracted: function() {}
  },
  methods: {
    removeView: function() {
      let self = this
      console.log('removeId', self.removedMid)
      $('#popline' + self.removedMid).remove()
      $('#snapshotView' + self.removedMid).remove()
      PathView.removePath(self.removedMid)
      self.$emit('removePopUp', self.removedMid)
    },
    numberFormat: function(d) {
      if (d > 1000 * 1000) return (d / 1000 / 1000).toFixed(1) + 'm'
      if (d > 1000 * 10) return (d / 1000).toFixed(0) + 'k'
      if (d > 1000) return (d / 1000).toFixed(1) + 'k'
      return d

    },
    delayTimeFormat: function(d) {
      if (d == 0) return 0
      d = +d
      if (d < 60) {
        return d + 's'
      }
      if (d < 3600) {
        return (d / 60).toFixed(1) + 'm'
      }
      if (d < 3600 * 24) {
        return (d / 3600).toFixed(1) + 'h'
      }
      if (d < 3600 * 24 * 30) {
        return (d / 3600 / 24).toFixed(1) + 'd'
      }
      if (d < 3600 * 24 * 365) {
        return (d / 3600 / 24 / 30).toFixed(1) + 'M'
      }
      return (d / 3600 / 24 / 365).toFixed(1) + 'y'
    }
  }
}

</script> <!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.snapshotView {
  cursor: pointer;
}

</style>



