// import packCircles from './pack/index'
import enclose from './pack/enclose'

function radiusMapping(a) {
  return Math.sqrt(a)
}

function calLayout(parent, tree) {
  tree['v_node'] = { 'x': 0, 'y': 0 }
  tree['s_node'] = { 'x': 0, 'y': 0 } // 垂直向下交点
  if (tree.children == undefined || tree.children.length == 0) {
    // console.log(tree.all_reposts_count)
    tree.r = radiusMapping(tree.all_reposts_count + 1)
    tree.x = 0
    tree.y = 0
    tree.max_r = tree.r
    tree['s_node'] = { 'x': 0, 'y': -1 * tree.r }
    tree['v_node'].r = radiusMapping(tree.all_reposts_count + 1)
     // console.log(tree['v_node'].r)
    return
  }

  for (let i = 0; i < tree.children.length; i++) {
    calLayout(tree, tree.children[i])
  }

  tree.x = 0
  tree.y = 0 // 子树作为整体, 大圆的中心位置

  let left = tree.all_reposts_count + 1 // 节点本身
  for (let i = 0; i < tree.children.length; i++) {
    left = left - tree.children[i].all_reposts_count - 1
  }
  let circles = []
  let extraDisValue = Global.semanticDistanceValue 
  tree['v_node'].r = radiusMapping(left)
  tree['v_node'].x = 0
  tree['v_node'].y = 0
  tree['max_r'] = tree['v_node'].r
  // console.log(tree['v_node'].r)
  // circles.push({ 'r': ratidiusMapping(left) })

  for (let i = 0; i < tree.children.length; i++) {
    tree.max_r = Math.max(tree['children'].max_r, tree.max_r)
    circles.push({
      'r': tree.children[i]['r'],
      'mid': tree.children[i]['mid'],
      'name': tree.children[i]['name']
    })
  }
  packCircles(tree['v_node'], circles, tree, extraDisValue)
  // console.log(circles)
  // tree.v_node.x = circles[0].x
  // tree.v_node.y = circles[0].y
  let close_node = enclose(circles.concat(tree['v_node']))
  tree.x = close_node.x
  tree.y = close_node.y
  tree.r = close_node.r
  // 大圆与y轴的交点
  {
    // (x - tree.x)^2 + (y - tree.y)^2 = tree.r^2
    // tree.s_node.x = 0
    // tree.s_node.y = -1 * Math.sqrt(tree.r * tree.r - tree.x * tree.x) + tree.y

    // if(tree.)
  }

  // 应该是计算大圆与中心圆的相切点，这样父子节点始终相切在一起
  {

    let dis = Math.distance(tree['v_node'], tree)
    let unitVec = {}
    unitVec['x'] = (tree['v_node'].x - tree.x) / dis
    unitVec['y'] = (tree['v_node'].y - tree.y) / dis
    tree['s_node'].x = tree['v_node'].x + unitVec.x * tree['v_node'].r
    tree['s_node'].y = tree['v_node'].y + unitVec.y * tree['v_node'].r
    // 必须保证tree的圆与tree 和孩子的构成的整体大圆相切，才能确保最后的距离满足满足语义差异的距离
    // 
    // 
    tree.r = Math.distance(tree['s_node'], tree) // 改变整体大圆的大小，硬性满足上述条件,
    // 大圆的半径减小，中心不变，保证相切处
  }


  // 计算相对 大圆 x ,y 位置，转换坐标系
  // 
  tree['v_node'].x = tree['v_node'].x - tree.x
  tree['v_node'].y = tree['v_node'].y - tree.y

  tree.s_node.x = tree.s_node.x - tree.x
  tree.s_node.y = tree.s_node.y - tree.y
  for (let i = 0; i < circles.length; i++) {
    circles[i].x = circles[i].x - tree.x
    circles[i].y = circles[i].y - tree.y
  }
  tree.x = 0
  tree.y = 0
  for (let i = 0; i < tree.children.length; i++) {
    tree.children[i].x = circles[i].x
    tree.children[i].y = circles[i].y
  }

}

function transform(angle, node) {
  let x = node.x,
    y = node.y
  node.x = x * Math.cos(angle) - y * Math.sin(angle)
  node.y = y * Math.cos(angle) + x * Math.sin(angle)
}

function cal3Angle(a, c, b) {
  let ac = Math.distance(a, c)
  let ab = Math.distance(a, b)
  let bc = Math.distance(b, c)
  let angle = Math.acos((ac * ac + bc * bc - ab * ab) / (2 * ac * bc))
  if (isNaN(angle)) return 0
  if (angle < 0) {
    angle = angle + Math.PI
  }
  if (sideLine(a, c, b) > 0) angle = angle * -1
  return angle
}

function sideLine(p1, p2, p3) {
  // (x1-x3)*(y2-y3)-(y1-y3)*(x2-x3)
  // -1 逆时针
  // 
  return (p1[0] - p3[0]) * (p2[1] - p3[1]) - (p1[1] - p3[1]) * (p2[0] - p3[0]) >= 0 ? -1 : 1
}

function rotate(tree) {
  let r = tree.r
  let v_node = tree['v_node']
  // 四边形的一个角度
  for (let i in tree.children) {
    let angle = cal3Angle([v_node.x, v_node.y], [tree.children[i].x, tree.children[i].y],
      [tree.children[i].s_node.x + tree.children[i].x, tree.children[i].s_node.y + tree.children[i].y])
    // console.log(angle)
    transform(angle, tree.children[i].s_node)
    transform(angle, tree.children[i].v_node)
    for (let j in tree.children[i].children) {
      transform(angle, tree.children[i].children[j])
    }
    rotate(tree.children[i])
  }
}

function updateLayout(tree, depth) {

  tree['v_node'].x = tree['v_node'].x + tree.x
  tree['v_node'].y = tree['v_node'].y + tree.y
  tree['s_node'].x = tree['s_node'].x + tree.x
  tree['s_node'].y = tree['s_node'].y + tree.y

  for (let i in tree.children) {
    tree.children[i].x = tree.x + tree.children[i].x
    tree.children[i].y = tree.y + tree.children[i].y
    updateLayout(tree.children[i])
  }
}
  


function packLayout(keyplayers, tree, viewWidth, viewHeight, padding) {
  calLayout(undefined, tree)
  rotate(tree)
  updateLayout(tree, 0)
  let _keyplayers = []

  function goTree(tree) {
    _keyplayers.push(tree)
    for (let i in tree.children) {
      goTree(tree.children[i])
    }
  }
  goTree(tree)
  rescale(_keyplayers, viewWidth, viewHeight, padding)
  for (let i = 0; i < _keyplayers.length; i++) {
    keyplayers[i]['v_node'] = _keyplayers[i]['v_node']
    keyplayers[i]['s_node'] = _keyplayers[i]['s_node']
  }

}

function rescale(nodes, vw, vh, padding) {
  let max_x = d3.max(nodes, d => { return d.v_node.x + d.v_node.r }),
    min_x = d3.min(nodes, d => { return d.v_node.x - d.v_node.r }),
    max_y = d3.max(nodes, d => { return d.v_node.y + d.v_node.r }),
    min_y = d3.min(nodes, d => { return d.v_node.y - d.v_node.r })
  // let max_x = d3.max(nodes, d => { return d.v_node.x }),
  //   min_x = d3.min(nodes, d => { return d.v_node.x }),
  //   max_y = d3.max(nodes, d => { return d.v_node.y }),
  //   min_y = d3.min(nodes, d => { return d.v_node.y })

  let mw = max_x - min_x,
    mh = max_y - min_y

  let cx = (max_x + min_x) / 2,
    cy = (max_y + min_y) / 2

  let scale = Math.max(mw / (vw - padding), mh / (vh - padding))

  // scale = 1
  window.LayoutScale = scale
  nodes.forEach(d => {
    d.v_node.x = (d.v_node.x - cx) / scale + vw / 2
    d.v_node.y = (d.v_node.y - cy) / scale + vh / 2
    d.v_node.r = d.v_node.r / scale
    d.s_node.x = (d.s_node.x - cx) / scale + vw / 2
    d.s_node.y = (d.s_node.y - cy) / scale + vh / 2
    d.x = (d.x - cx) / scale + vw / 2
    d.y = (d.y - cy) / scale + vh / 2
    d.r = d.r / scale
  })
}



function calAngle(r1, r2) {
  return Math.asin(r2 / (r1 + r2))
}

function packCircles(node, circles, tree, extraDisValue) {

  let leftAngle = Math.PI / 6,
    pi = Math.PI,
    depth = tree.depth


  let sumAngle = d3.sum(circles, d => {
    return 2 * calAngle(node.r, d.r)
  })

  // y 轴向下展开 60度 为空白地方
  // 
  let ratio = 1
  let extraDis = 0
  // let extraValue = 1 // 不是关注的，意见反对的 

  if (circles.length == 1) {
    if (Global.islands[circles[0].mid])
      extraDis = extraDisValue * Global.semanticDistance[tree.mid + ',' + circles[0].mid]

    circles[0].x = 0
    console.log(tree.name, circles[0].name, extraDis)
    circles[0].y = node.r + circles[0].r + extraDis
    return
  }

  console.log('sumAngles', sumAngle)
  if (sumAngle < 2 * pi) {
    // 均匀划分
    // 不超过180度
    if (depth != 1) {
      let start = -1 * (pi / 2 - (2 * pi - sumAngle) / 2)
      for (let i = 0; i < circles.length; i++) {
        extraDis = 0
        if (Global.islands[circles[i].mid]) {
          extraDis = extraDisValue * Global.semanticDistance[tree.mid + ',' + circles[i].mid]
        }
        console.log(tree.name, circles[i].name, extraDis)

        let angle = start + calAngle(node.r, circles[i].r)
        circles[i].x = (node.r + circles[i].r * ratio + extraDis) * Math.cos(angle)
        circles[i].y = (node.r + circles[i].r * ratio + extraDis) * Math.sin(angle)
        start = start + 2 * calAngle(node.r, circles[i].r)
      }
    } else {
      // console.log('??????')
      let space = (2 * pi - sumAngle) / circles.length
      let start = -1 * (pi / 2 - space / 2)
      for (let i = 0; i < circles.length; i++) {
        extraDis = 0
        if (Global.islands[circles[i].mid]) {
          extraDis = extraDisValue * Global.semanticDistance[tree.mid + ',' + circles[i].mid]
        }
        console.log(tree.name, circles[i].name, extraDis)

        let angle = start + calAngle(node.r, circles[i].r)
        circles[i].x = (node.r + circles[i].r * ratio + extraDis) * Math.cos(angle)
        circles[i].y = (node.r + circles[i].r * ratio + extraDis) * Math.sin(angle)
        start = start + 2 * calAngle(node.r, circles[i].r) + space
      }
    }
  } else {
    // console.log(node, circles)
    // 所有圆超过2pi
    // 对于最差情况下，所有节点都是kplayer，会出
    // 
    console.log('guo 2pi')
    let start = -Math.PI / 2
    // 闭合的circle 太浪费空间，尤其是
    // 
    // let sumAngle = d3.sum(circles, d => {
    //   return 2 * calAngle(node.r, d.r)
    // })
    // 不能保证连续性，全部都是岛屿
    for (let i = 0; i < circles.length; i++) {
      extraDis = 0
      if (Global.islands[circles[i].mid]) {
        extraDis = extraDisValue * Global.semanticDistance[tree.mid + ',' + circles[i].mid]
      }
      let gotAngle = 2 * calAngle(node.r, circles[i].r * ratio) / sumAngle * 2 * pi
      let dis = circles[i].r / Math.sin(gotAngle / 2)
      let angle = start + gotAngle / 2
      circles[i].x = dis * Math.cos(angle)
      circles[i].y = dis * Math.sin(angle)
      start = start + gotAngle
    }

  }

}

function packCircles2(node, circles, depth) {

  let leftAngle = Math.PI / 6,
    pi = Math.PI


  let sumAngle = d3.sum(circles, d => {
    return 2 * calAngle(node.r, d.r)
  })
  let totalR = d3.sum(circles, d => {
    return d.r
  })

  // y 轴向下展开 60度 为空白地方
  // 
  let ratio = 1
  let extra = 1
  let extraValue = 1 // 不是关注的，意见反对的 

  if (circles.length == 1) {
    circles[0].x = 0
    circles[0].y = node.r + circles[0].r
    return
  }

  if (depth == 1) {
    // 360du 都可以排布
    let start = -pi / 2
    for (let i = 0; i < circles.length; i++) {
      extra = 1
      if (circles[i].oppose_parent || circles[i].following_parent == false) {
        extra = extraValue
      }
      let angle = start + (2 * pi * circles[i].r / totalR) / 2
      circles[i].x = (node.r + circles[i].r * ratio + 20) * Math.cos(angle)
      circles[i].y = (node.r + circles[i].r * ratio + 20) * Math.sin(angle)
      start = start + 2 * pi * circles[i].r / totalR
    }
  } else {
    // 只能排180
    let start = 0
    for (let i = 0; i < circles.length; i++) {
      extra = 1
      if (circles[i].oppose_parent || circles[i].following_parent == false) {
        extra = extraValue
      }
      let angle = start + (pi * circles[i].r / totalR) / 2
      circles[i].x = (node.r + circles[i].r * ratio * extra) * Math.cos(angle)
      circles[i].y = (node.r + circles[i].r * ratio * extra) * Math.sin(angle)
      start = start + pi * circles[i].r / totalR
    }
  }

}



export default packLayout



