// Javascript implementation of the Fruchterman Reingold Algorithm.
// By Donghao.Ren
// References:
//  Fruchterman, T. M. J., & Reingold, E. M. (1991). Graph Drawing by Force-Directed Placement. Software: Practice and Experience, 21(11).

// Nodes:
//   n      : number of nodes, identified with 0 to N - 1
//   edges  : [[v1, v2], ...]
//   params : { attraction = 0.75, repulsion = 0.75, max_iterations = 800, size = 1 }
var jsfr_initialize = function(n, edges, params) {
  // Load parameters.
  var size = 1;
  var speed = 10;
  var gravity = 0;
  if (params != undefined) {
    if (params.size != undefined) size = params.size;
    if (params.speed != undefined) speed = params.speed;
    if (params.gravity != undefined) gravity = params.gravity;
  }
  var area = size * size;
  var AREA_MULTIPLICATOR = 1.0;
  var SPEED_DIVISOR = 800;
  // Initialize.
  //var temperature = size / 10;
  var epsilon = 0.000001;
  var nodes = new Array();
  // Node: x y dx dy radius
  if (params != undefined && params.radius != undefined) {
    // Load initial position.
    for (var i = 0; i < n; i++) {
      nodes.push([Math.random() * size, Math.random() * size, 0, 0, params.radius[i], 0]);
    }
  } else {
    // Random positions.
    for (var i = 0; i < n; i++) {
      // px, py, vx, vy.
      nodes.push([Math.random() * size, Math.random() * size, 0, 0, 1, 0]);
    }
  }
  var max_displace = Math.sqrt(AREA_MULTIPLICATOR * area) / 10.0;
  var k = Math.sqrt(AREA_MULTIPLICATOR * area) / (1 + n);
  if (params.k) k = params.k;
  var auto_k = true;

  var context = {
    k: k,
    size: size,
    speed: speed,
    gravity: gravity,
    area: area,
    auto_k: auto_k,
    max_width: params.max_width,
    max_height: params.max_height,
    AREA_MULTIPLICATOR: AREA_MULTIPLICATOR,
    SPEED_DIVISOR: SPEED_DIVISOR,
    epsilon: epsilon,
    nodes: nodes,
    edges: edges,
    iterate_number: 0,
    // append one user, suppose edges is already changed.
    append: function(x, y) {
      if (x == undefined) x = Math.random() * this.size;
      if (y == undefined) y = Math.random() * this.size;
      this.nodes.push([x + Math.random() - 0.5, y + Math.random() - 0.5, 0, 0, 0, 0]);
    },
    iterate: function(d_divisor) {
      var size = this.size;
      var speed = this.speed;
      var gravity = this.gravity;
      var area = this.area;
      var SPEED_DIVISOR = this.SPEED_DIVISOR;
      var epsilon = this.epsilon;
      var nodes = this.nodes;
      var edges = this.edges;
      var k = this.k;
      var auto_k = this.auto_k;
      var n = nodes.length;
      // Cleanup forces.
      for (var i = 0; i < n; i++) {
        nodes[i][2] = 0;
        nodes[i][3] = 0;
      }
      // Calculate repulsive forces.
      var this_radius = 1e100;
      for (var i = 0; i < n; i++) {
        for (var j = 0; j < n; j++) {
          if (i == j) continue;
          if (auto_k) k = nodes[i][4] + nodes[j][4];
          var dx = nodes[i][0] - nodes[j][0];
          var dy = nodes[i][1] - nodes[j][1];
          var d = Math.sqrt(dx * dx + dy * dy);
          this_radius = Math.min(this_radius, d);

          if (d < nodes[i][4] + nodes[j][4]) {
            d *= Math.exp((d - (nodes[i][4] + nodes[j][4])) / 30);
          }
          if (d < 300) {
            //d -= (nodes[i][4] + nodes[j][4]);
            if (d < epsilon) d = epsilon;
            var f = k * k / d;
            nodes[i][2] += dx / d * f;
            nodes[i][3] += dy / d * f;
          }
        }
      }
      // Calculate attractive forces.
      for (var e = 0; e < edges.length; e++) {
        var i = edges[e][0];
        var j = edges[e][1];
        if (auto_k) k = nodes[i][4] + nodes[j][4];
        var dx = nodes[i][0] - nodes[j][0];
        var dy = nodes[i][1] - nodes[j][1];
        var d = Math.sqrt(dx * dx + dy * dy);
        if (d < epsilon) d = epsilon;
        var f = (d * d) / k;
        dx = (dx / d) * f;
        dy = (dy / d) * f;
        nodes[i][2] -= dx;
        nodes[i][3] -= dy;
        nodes[j][2] += dx;
        nodes[j][3] += dy;
      }
      k = this.k;
      // Edge angle force.
      /*
      for(var e1 = 0; e1 < edges.length; e1++) {
          for(var e2 = e1 + 1; e2 < edges.length; e2++) {
              var xe1 = edges[e1];
              var xe2 = edges[e2];
              var c, a, b;
              if(xe1[0] == xe2[0]) { c = xe1[0]; a = xe1[1]; b = xe2[1]; }
              if(xe1[0] == xe2[1]) { c = xe1[0]; a = xe1[1]; b = xe2[0]; }
              if(xe1[1] == xe2[0]) { c = xe1[1]; a = xe1[0]; b = xe2[1]; }
              if(xe1[1] == xe2[1]) { c = xe1[1]; a = xe1[0]; b = xe2[0]; }
              if(c) {
                  var v1x = nodes[a][0] - nodes[c][0];
                  var v1y = nodes[a][1] - nodes[c][1];
                  var v2x = nodes[b][0] - nodes[c][0];
                  var v2y = nodes[b][1] - nodes[c][1];
                  var l1 = Math.sqrt(v1x * v1x + v1y * v1y);
                  var l2 = Math.sqrt(v2x * v2x + v2y * v2y);
                  if(l1 < epsilon) l1 = epsilon;
                  if(l2 < epsilon) l2 = epsilon;
                  v1x /= l1; v1y /= l1;
                  v2x /= l2; v2y /= l2;
                  var dx = v1x - v2x;
                  var dy = v1y - v2y;
                  var l = Math.sqrt(dx * dx + dy * dy) * 5;
                  if(l < epsilon) l = epsilon;
                  var f = k * k / l / l * Math.max(l1, l2);
                  var fx = dx * f;
                  var fy = dy * f;
                  nodes[a][2] += fx;
                  nodes[a][3] += fy;
                  nodes[b][2] -= fx;
                  nodes[b][3] -= fy;
              }
          }
      }
      */
      // Gravity
      for (var i = 0; i < n; i++) {
        var dx = nodes[i][0];
        var dy = nodes[i][1];
        var d = Math.sqrt(dx * dx + dy * dy);
        if (d < epsilon) d = epsilon;
        var f = 0.01 * k * gravity * d;
        nodes[i][2] -= dx / d * f;
        nodes[i][3] -= dy / d * f;

        if (this.max_width) {
          var d = null;
          if (nodes[i][0] + nodes[i][4] > this.max_width)
            d = nodes[i][0] + nodes[i][4] - this.max_width;
          if (nodes[i][0] - nodes[i][4] < -this.max_width)
            d = this.max_width + nodes[i][0] - nodes[i][4];
          if (d) {
            var distance = Math.abs(d);
            var sign = d > 0 ? 1 : -1;
            nodes[i][2] -= sign * Math.exp(distance / 10);
          }
        }
        if (this.max_height) {
          var d = null;
          if (nodes[i][1] + nodes[i][4] > this.max_height)
            d = nodes[i][1] + nodes[i][4] - this.max_height;
          if (nodes[i][1] - nodes[i][4] < -this.max_height)
            d = this.max_height + nodes[i][1] - nodes[i][4];
          if (d) {
            var distance = Math.abs(d);
            var sign = d > 0 ? 1 : -1;
            nodes[i][3] -= sign * Math.exp(distance / 10);
          }
        }
      }
      for (var i = 0; i < n; i++) {
        nodes[i][2] *= speed / SPEED_DIVISOR;
        nodes[i][3] *= speed / SPEED_DIVISOR;
      }
      // Update positions.
      var max_distance = 0;
      for (var i = 0; i < n; i++) {
        if (nodes[i][5]) continue;
        var vx = nodes[i][2];
        var vy = nodes[i][3];
        var d = Math.sqrt(vx * vx + vy * vy);
        if (d < epsilon) d = epsilon;
        var dl = Math.min(max_displace * (speed / SPEED_DIVISOR), d);
        var dx = vx / d * dl;
        var dy = vy / d * dl;
        var nx = nodes[i][0] + dx;
        var ny = nodes[i][1] + dy;
        max_distance = Math.max(max_distance, Math.sqrt(dx * dx + dy * dy));
        nodes[i][0] = nx;
        nodes[i][1] = ny;
      }
      this.iterate_number++;
      // Converge?
      //if(max_distance < this_radius * 0.001) return true;
      return false;
    }
  };
  return context;
};

var jsfr_run = function(n, edges, params) {
  let ctx = jsfr_initialize(n, edges, params);
  let iter = n
  for (var i = 0; i < iter * 2; i++) {
    console.log('iterate')
    ctx.iterate();
  }
  return ctx.nodes;
}

var FRLayout = function(svg, width, height, nodes, mid2node) {
  nodes = nodes.slice(0, 200)
  let n = nodes.length
  let edges = []
  let mid2index = {}
  for (let i = 0; i < nodes.length; i++) {
    mid2index[nodes[i].mid] = i
  }
  for (let i = 0; i < n; i++) {
    let j = -1
    if (mid2node[nodes[i].pid] != undefined) {
      j = mid2index[mid2node[nodes[i].pid].mid]
    }
    if (j >= 0) {
      edges.push([i, j])
    }
  }
  let params = {
    size: 1,
    speed: 10,
    gravity: 0
  }
  console.log('start calculation')
  let size = Math.min(width, height)
  let positions = jsfr_run(n, edges, params)

  svg.append('g').selectAll('line')
     .data(edges)
     .enter()
     .append('line')
     .attr('x1', d=>{
        return positions[d[0]][0] * size
     })
     .attr('y1', d=>{
        return positions[d[0]][1] * size
     })
     .attr('x2', d=>{
        return positions[d[1]][0] * size
     })
     .attr('y2', d=>{
        return positions[d[1]][1] * size
     })
     .style('stroke', 'gray')
     .style('stroke-width', 0.5)

  svg.append('g').selectAll('circle')
     .data(positions)
     .enter()
     .append('circle')
     .attr('cx', d=>{
        return d[0] * size
     })
     .attr('cy', d=>{
        return d[1] * size
     })
     .attr('r', 2)

}



window.FRLayout = FRLayout
export default FRLayout



