import kMeans from "./kmeans.js"

function getFullWords(node, keymids)
{
	let words = []
	for(let i in node["words"])
		if(node["words"][i] != "" && words.indexOf(node["words"][i]) < 0)
			words.push(node["words"][i])

	for(let i in node["children"])
	{
		if(keymids.indexOf(node["children"][i].mid) >= 0)
			continue
		let tmp = getFullWords(node["children"][i], keymids)
		for(let j in tmp)
			words.push(tmp[j])
	}
	return words
}

let no_comment = ["", "转发微博", "转发", "轉發微博", "repost", "Repost"]

function hasComments(node, keymids)
{
	if(keymids.indexOf(node["mid"]) >= 0)
		return false

	if(no_comment.indexOf(node["text"]) < 0)
		return true

	for(let i in node["children"])
	{
		let flag = hasComments(node["children"][i], keymids)
		if(flag)
			return true
	}
	return false
}

function deepClone(obj){
    let _obj = JSON.stringify(obj),
        objClone = JSON.parse(_obj);
    return objClone
}

function calDist(v1, v2)
{
	let t = 0
	for(let i = 0; i < v1.length; i++)
		t += v1[i]*v2[i]
	return t
}

function calSSE(vectors, clusters)
{
	let result = 0
	let km = new kMeans({
    		K: clusters
	})
	km.cluster(vectors)
	while (km.step())
	{
		km.findClosestCentroids();
		km.moveCentroids();

		if(km.hasConverged())
		   	break;
	}
	for(let j = 0; j < clusters; j++)
	{
		for(let k in km.clusters[j])
		{
			let tmp = km.clusters[j][k]
			let vector = vectors[tmp]
			result += calDist(vector, km.centroids[j])
		}
	}
	return result
}

function getMinCluster(vectors, clusters)
{
	let result = 0
	let km = new kMeans({
    		K: clusters
	})
	km.cluster(vectors)
	while (km.step())
	{
		km.findClosestCentroids();
		km.moveCentroids();

		if(km.hasConverged())
		   	break;
	}
	let mn = 1e9;
	for(let j = 0; j < clusters; j++)
		mn = Math.min(mn, km.clusters[j].length)
	return mn
}

function Cluster(keyplayers, n_clusters)
{
	console.log(keyplayers)

	let keymids = []
	for(let i in keyplayers)
		keymids.push(keyplayers[i].mid)

	let outJson = []

	for(let i in keyplayers)
	{
		let node = keyplayers[i]
		let keywords = {}
		let tmp = getFullWords(node, keymids)
		for(let k in tmp)
		{
			if(!keywords.hasOwnProperty(tmp[k]))
				keywords[tmp[k]] = 0
			keywords[tmp[k]]++
		}
		// console.log(keywords)s
		let out = {}
		out["key_player_mid"] = node["mid"]
		out["key_player_text"] = node["text"]
		out["keywords"] = keywords
		out["centers"] = []
		out[-1] = []
		out[0] = []

		let vectors = []
		let texts = []
		let mids = []

		let no_comment = ["", "转发微博", "转发", "轉發微博", "repost", "Repost"]

		for(let j in node["children"])
		{
			let mid = node["children"][j]["mid"]
			let text = node["children"][j]["text"]
			if(!hasComments(node["children"][j], keymids))
			{
				out[-1].push([mid, ""])
				continue
			}
			let tmp = getFullWords(node["children"][j], keymids)
			if(tmp.length == 0)
			{
				out[0].push([mid, text])
				continue
			}
			let wordcnt = {}
			for(let k in tmp)
			{
				if(!wordcnt.hasOwnProperty(tmp[k]))
					wordcnt[tmp[k]] = 0
				wordcnt[tmp[k]]++
			}
			let vector = []
			let flag = false
			for(let k in keywords)
			{
				if(keywords[k] < 8)
					continue
				if(!wordcnt.hasOwnProperty(k))
					vector.push(0)
				else
				{
					flag = true
					vector.push(wordcnt[k] * 1.0 / keywords[k])
				}
			}
			if(flag)
			{
				vectors.push(vector)
				mids.push(mid)
				texts.push(text)
			}
			else
				out[0].push([mid, text])
		}

		let minclusters = 3
		let maxclusters = Math.min(vectors.length - 1, 8)

		if(vectors.length <= minclusters)
		{
			for(let j = 0; j < vectors.length; j++)
			{
				out[j + 1] = []
				out[j + 1].push([mids[j], texts[j]])
				out["centers"].push(vectors[j])
			}
			outJson.push(out)
			continue
		}

		let SSE = {}
		/*for(let j = minclusters - 1; j <= maxclusters + 1; j++)
			SSE[j] = calSSE(vectors, j)
		let mx = -1e9*/
		let clusters
		/*for(let j = minclusters; j <= maxclusters; j++)
			if((SSE[j - 1] - SSE[j]) - (SSE[j] - SSE[j + 1]) > mx)
			{
				mx = (SSE[j - 1] - SSE[j]) - (SSE[j] - SSE[j + 1])
				clusters = j
			}*/

		for(let j = maxclusters; j >= minclusters; j--)
		{
			clusters = j;
			if (getMinCluster(vectors, j) > 4)
				break
		}
		

		let km = new kMeans({
    		K: clusters
		})
		km.cluster(vectors)
		while (km.step())
		{
		    km.findClosestCentroids();
		    km.moveCentroids();
			// console.log("debug", vectors.length, clusters, vectors)

		    if(km.hasConverged())
		    	break;
		}
		for(let j = 0; j < clusters; j++)
		{
			out[j + 1] = []
			for(let k in km.clusters[j])
			{
				let tmp = km.clusters[j][k]
				out[j + 1].push([mids[tmp], texts[tmp]])
			}
			out["centers"].push(km.centroids[j])
		}
		outJson.push(out)
	}
	console.log("cluster", outJson)
	return outJson
}
export default Cluster



