<template>
  <div id="timelineView">
    <div style="position:absolute; top: 0px; right: 20px" id='buttonGroup'>
      <button class="mybutton" name='region' id='playButton' @click="animationClick">
        {{ playStatus }}
      </button>
      <button class="mybutton" name='region' id='refreshButton' @click="refreshClick">
        Reset
      </button>
      <!-- <button class="mybutton" name='region' id='alignButton' @click="alignClick">
        Align
      </button>
 -->
    </div>
    <div id='timeline'>
    </div>
    <div style="display: inline-block; position:absolute; bottom: 0px; left: 15px; right: 15px">
      <el-slider v-model="sliderValue" :format-tooltip="formatTooltip" @change="sliderChange"></el-slider>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
  name: 'TimelineView',
  data() {
    return {
      rootH: 10,
      padding: { 'left': 10, 'right': 70, 'top': 60, 'bottom': 50 },
      aligned: false,
      playStatus: 'Play',
      startTime: 0,
      endTime: 0,
      firstAnimation: true,
      timeRange: [0, 0],
      delayTime: [0, 0],
      splitTime: 3600 * 24,
      ratio: 0.9,
      updateTime: 200, //ms
      allSteps: 500,
      timeStep: 1, // s
      alignStep: 1,
      currentDelayTime: 0,
      firstDrag: true,
      currentStep: 0,
      sampleTime: 3600 * 24, // one day
      minSampleTime: 60,
      maxSampleTime: 3600 * 24,
      sliderValue: 0
    }
  },
  mounted: function() {
    let width = $('#timeline').width(),
      height = $('#timeline').height()
    let svg = d3.select('#timeline').append('svg').attr('width', width).attr('height', height)
    if ($('#tableView').height() > 2000) {
      this.padding.top = 170
    }
    this.svg = svg
    this.width = width
    this.height = height
  },
  computed: {
    ...mapGetters(['keyplayersExtracted', 'changeColorEncoding', 'highlightPolygons']),
  },
  watch: {
    keyplayersExtracted: function() {
      this.timeRange = [0, 0]
      this.delayTime = [0, 0]
      this.currentDelayTime = 0
      this.timeStep = 1
      this.alignStep = 1
      this.currentDelayTime = 0
      this.firstDrag = true
      this.sliderValue = 0
      this.firstAnimation = true
      d3.select('#control').attr('transform', 'translate(0, 0)')
      this.process(Global.keyplayers)
    },
    changeColorEncoding: function() {
      console.log(Global.switchColorFlag)
      let tmp = this.svg.selectAll(".rect")
      if (Global.switchColorFlag == "level")
        tmp.style('fill', d => {
          return MapColor(d)
        })
      else {
        tmp.style('fill', d => {
          return SentimentColor(d)
        })
      }
    },
    highlightPolygons: function(val, old) {
      // console.log("timeline update")
      let svg = this.svg
      let mids = Global.highlightedPolygons
      if (mids.length == 0) {
        svg.selectAll('.rect')
          .style('opacity', 1)

        svg.selectAll('.kprect')
          .style('opacity', 1)

        return
      }
      svg.selectAll('.rect').filter(d => {
          return mids.indexOf(d.mid) >= 0
        })
        .style('opacity', 1)

      svg.selectAll('.rect').filter(d => {
          return mids.indexOf(d.mid) < 0
        })
        .style('opacity', 0.1)


      svg.selectAll('.kprect').filter(d => {
          return mids.indexOf(d.root.mid) >= 0
        })
        .style('opacity', 1)

      svg.selectAll('.kprect').filter(d => {
          return mids.indexOf(d.root.mid) < 0
        })
        .style('opacity', 0.1)
    }
  },

  methods: {
    animationClick: function() {
      // console.log(this.playStatus)
      this.svg.select('#brush').call(this.brush.move, null)
      if (this.playStatus == 'Pause') {
        // this.$store.state.playStatus = 'Pause'
        this.playStatus = 'Play'
        clearInterval(this.timer)
        Global.freshPolygons = []
        Global.behaviorType = 'TimelineView'
        this.$store.state.highlightPolygons = 'TimelineView ' + Math.random()
        Global.timeLineStatus = 'Pause'

      } else {
        if (this.firstAnimation) {
          this.svg.selectAll('.rect')
            .style('opacity', 0.1)
          if (this.aligned) {
            this.currentDelayTime = 0
          } else {
            this.currentTime = this.timeRange[0]
          }
          this.firstAnimation = false
          this.$store.state.firstAnimation = Math.random()
        }
        this.firstDrag = false
        Global.timeLineStatus = 'Play'
        this.startAnimation()
        this.playStatus = 'Pause'

      }
    },

    refreshClick: function() {
      this.svg.select('#brush').call(this.brush.move, null)
      this.playStatus = 'Play'
      this.svg.selectAll('.rect')
        .style('opacity', 1)
      this.svg.selectAll('.kprect')
        .style('opacity', 1)
      this.firstAnimation = true
      d3.select('#control').attr('transform', 'translate(0, 0)')
      this.$store.state.refreshStatus = Math.random()
      this.firstDrag = true
      Global.timeLineStatus = 'Reset'

    },
    process: function(keyplayers) {
      let self = this
      let iskeyplayer = {}
      let orderArr = []
      for (let i in keyplayers) {
        iskeyplayer[keyplayers[i].mid] = true
      }
      let repostlist = []
      let mid2Index = {}
      Global.nodes.sort(function(a, b) {
        return a.t - b.t
      })

      self.splitTime = Global.nodes[Math.floor(Global.nodes.length * self.ratio)].t
      self.times = []
      for (let i in Global.nodes)
        self.times.push(Global.nodes[i].t)
      self.maxSampleTime = self.sampleTime = self.times[self.times.length - 1] - self.times[0]
      let ssssst = keyplayers[0].t
      // 前80%的节点一定占据80%空间，线性时间受到后面的转发影响太大
      for (let i in keyplayers) {
        let list = []
        console.log(keyplayers[i].name, keyplayers[i].t - ssssst)
        self.extract(list, keyplayers[i], iskeyplayer)
        keyplayers[i].delayTime = 0
        list.sort(function(a, b) {
          return a.t - b.t
        })
        mid2Index[keyplayers[i].mid] = i
        list.forEach(d => {
          d.delayTime = d.t - keyplayers[i].t // 相对keyplayer 的延迟时间,下面会排序
        })
        for (let i = 0; i < list.length; i++) {
          list[i].delayStep = i + 1
        }
        repostlist.push({ 'root': keyplayers[i], 'repostings': list })
      }

      Global.nodes.sort(function(a, b) {
        return a.delayTime - b.delayTime
      })

      self.delaySplitTime = Global.nodes[Math.floor(Global.nodes.length * self.ratio)].delayTime
      self.delayTimes = []
      for (let i in Global.nodes)
        self.delayTimes.push(Global.nodes[i].delayTime)


      self.firstOrder(Global.keyplayersTree, orderArr)

      let tmp = repostlist
      repostlist = []
      for (let i in orderArr) {
        repostlist.push(tmp[mid2Index[orderArr[i]]])
      }
      self.repostlist = repostlist
      self.draw(repostlist)
      self.drawTree(repostlist)

    },

    firstOrder: function(tree, arr) {
      arr.push(tree.mid)
      for (let i in tree['children']) {
        this.firstOrder(tree['children'][i], arr)
      }
    },

    extract(arr, tree, iskeyplayer) {
      arr.push(tree)
      for (let i in tree['children']) {
        if (iskeyplayer[tree['children'][i].mid]) continue
        this.extract(arr, tree['children'][i], iskeyplayer)
      }
    },

    draw: function(repostlist) {
      // 时间轴，可对齐，可不对齐
      let svg = this.svg
      let self = this
      let padding = this.padding
      padding.right = parseInt($('body').css('font-size').split('px')[0]) * 2
      let viewWidth = this.width - padding.left - padding.right
      let viewHeight = this.height - padding.top - padding.bottom
      this.viewHeight = viewHeight

      let timeRange = d3.extent(Global.nodes, d => {
        return +d.t
      })
      this.timeRange = timeRange

      let delayRange = [0, d3.max(repostlist, d1 => {
        return d3.max(d1.repostings, d2 => { return d2.delayTime })
      })]
      this.delayRange = delayRange

      self.timeStep = (self.splitTime - timeRange[0]) / self.allSteps
      self.timeStep2 = (timeRange[1] - self.splitTime) / (self.allSteps * (1 - self.ratio))
      let rootH = this.rootH
      let splitTime = this.splitTime
      let ratio = this.ratio
      let times = this.times
      /*let maxTime = this.maxTime
      let delayTimeCnt = 0
      let delayTimeTot = 0
      for(let i = 0; i < times.length - 1; i++)
        if(times[i + 1] <= splitTime && times[i + 1] - times[i] > maxTime)
        {
          delayTimeCnt++
          delayTimeTot += times[i + 1] - times[i]
        }

      console.log("delayTimeCnt", delayTimeCnt)

      let delayHeight = viewHeight * 0.03
      let tmpscale = d3.scaleLinear().domain([timeRange[0], splitTime - delayTimeTot])
      tmpscale.range([0, viewHeight * ratio - delayHeight * delayTimeCnt])

      let yscaleDomain = []
      let yscaleRange = []
      yscaleDomain.push(timeRange[0])
      yscaleRange.push(0)
      delayTimeTot = 0
      delayTimeCnt = 0
      for(let i = 0; i < times.length - 1; i++)
        if(times[i + 1] <= splitTime && times[i + 1] - times[i] > maxTime)
        {
          yscaleDomain.push(times[i], times[i + 1])
          yscaleRange.push(tmpscale(times[i] - delayTimeTot) + delayTimeCnt * delayHeight, tmpscale(times[i] - delayTimeTot) + delayHeight * (delayTimeCnt + 1))
          delayTimeCnt++
          delayTimeTot += times[i + 1] - times[i]
        }

      yscaleDomain.push(splitTime, timeRange[1] * 1.01)
      yscaleRange.push(viewHeight * ratio, viewHeight)
      console.log("yscaleDomain", yscaleDomain)
      console.log("yscaleRange", yscaleRange)*/

      d3.select('#dayButton').classed('buttonClicked', true)
      d3.select('#hourButton').classed('buttonClicked', false)

      this.getScales()

      let yscale = this.yscale
      let delayScale = this.delayScale

      // let alignRange = [0, d3.max(repostlist, d => {
      //   return d.repostings.length
      // }) + 2]
      // this.alignRange = alignRange
      // let alignScale = d3.scaleLinear().domain([alignRange[0], alignRange[1] * 1.01])
      //   .range([0, viewHeight])
      // this.alignScale = alignScale
      // this.alignStep = alignRange[1] / this.allSteps

      let binWidth = viewWidth / repostlist.length
      this.binWidth = binWidth
      svg.selectAll('*').remove()

      // 边框
      svg.append('rect')
        .attr('x', padding.left - binWidth * 0.2)
        .attr('y', padding.top + rootH)
        .attr('width', viewWidth + binWidth * 0.2)
        .attr('height', viewHeight + 5)
        .style('fill', 'none')
        .style('stroke', '#eee')
        .style('stroke-width', 1)

      let brush = d3.brush()
        .on("brush", brushed)
        .on("end", brushendded)
      this.brush = brush

      // brush
      let g = svg.append('g').attr('transform', 'translate(' + padding.left + ',' + (
          padding.top + rootH) + ')')
        .append('g').attr('id', 'brush').call(brush)

      function brushed() {
        if (!d3.event.selection)
          return
        let left = d3.event.selection[0][0],
          right = d3.event.selection[1][0]
        let st = d3.event.selection[0][1],
          en = d3.event.selection[1][1]
        let start, end
        let yscale = self.yscale
        let delayScale = self.delayScale
        if (!self.aligned) {
          start = yscale.invert(st)
          end = yscale.invert(en)
          self.currentTime = end
        } else {
          start = delayScale.invert(st)
          end = delayScale.invert(en)
          self.currentDelayTime = end
        }
        Global.timeLineStatus = 'Brush'
        self.viewUpdate3(left, right, start, end, false)
      }

      function brushendded() {
        if (!d3.event.selection)
          return
        let left = d3.event.selection[0][0],
          right = d3.event.selection[1][0]
        let st = d3.event.selection[0][1],
          en = d3.event.selection[1][1]
        let start, end
        let yscale = self.yscale
        let delayScale = self.delayScale
        if (!self.aligned) {
          start = yscale.invert(st)
          end = yscale.invert(en)
          self.currentTime = end
        } else {
          start = delayScale.invert(st)
          end = delayScale.invert(en)
          self.currentDelayTime = end
        }
        Global.timeLineStatus = 'Brush'
        self.viewUpdate3(left, right, start, end, true)
      }


      // svg.append('line').attr('x1', viewWidth + padding.left)
      //   .attr('y1', padding.top)
      //   .attr('x2', viewWidth + padding.left)
      //   .attr('y2', padding.top + viewHeight)
      //   .style('stroke', '#eee')
      //   .style('stroke-width', 1)

      // svg.append('line').attr('x1', padding.left - binWidth * 0.2)
      //   .attr('y1', padding.top)
      //   .attr('x2', padding.left - binWidth * 0.2)
      //   .attr('y2', padding.top + viewHeight)
      //   .style('stroke', '#eee')
      //   .style('stroke-width', 1)

      { // 画每个cell
        let g0 = svg.append('g').attr('transform', 'translate(' + padding.left + ',' + (
          padding.top + rootH) + ')')

        let tg = g0.selectAll('g')
          .data(repostlist)
          .enter()
          .append('g')
          .attr('transform', (d, i) => {
            return 'translate(' + (i * binWidth) + ',0)'
          })
          .attr("class", "rectGroup")


        tg.append('rect')
          .attr('x', 0)
          .attr('y', d => {
            return yscale(d.root.t) - rootH
          })
          .attr('width', binWidth * 0.8)
          .attr('height', d => {
            return rootH
          })
          .style('fill', d => {
            return MapColor(d.root)
          })
          .attr('class', 'kprect')
          .attr('id', d => {
            return 'rect' + d.root.mid
          })

        let rect = tg.append('g').selectAll('rect')
          .data(d => {
            return d.repostings
          })
          .enter()
          .append('rect')
          .attr('x', 0)
          .attr('y', d => {
            return yscale(d.t)
          })
          .attr('width', binWidth * 0.8)
          .attr('height', d => {
            let h = 1
            return h
          })
          .style('fill', d => {
            if (Global.switchColorFlag == 'level') {
              return MapColor(d)
            } else if (Global.switchColorFlag == 'sentiment') {
              return SentimentColor(d)
            }

          })
          .attr('id', d => {
            return 'rect' + d.mid
          })
          .attr('class', 'rect')
          .on('click', d => {
            console.log(d)
            console.log(timeFormat(d.t * 1000))
          })

        rect.append('title')
          .text(d => {
            return d.name
          })
      }

      /*let timeTicks = [
      0,
      yscale.invert(viewHeight * 0.1) - timeRange[0],
      yscale.invert(viewHeight * 0.2) - timeRange[0],
      yscale.invert(viewHeight * 0.3) - timeRange[0],
      yscale.invert(viewHeight * 0.4) - timeRange[0],
      yscale.invert(viewHeight * 0.5) - timeRange[0],
      yscale.invert(viewHeight * 0.6) - timeRange[0],
      yscale.invert(viewHeight * 0.7) - timeRange[0],
      yscale.invert(viewHeight * 0.8) - timeRange[0],
      yscale.invert(viewHeight * 0.9) - timeRange[0],
      timeRange[1] - timeRange[0]
      ]*/
      let timeTicks = [0]
      for (let i = 1; i <= 19; i++) {
        let tmp = yscale.invert(viewHeight * 0.05 * i) - timeRange[0]
        timeTicks.push(tmp)
      }
      timeTicks.push(timeRange[1] - timeRange[0])
      //console.log("timeTicks", timeTicks)
      this.timeTicks = timeTicks
      /*let delayTimeTicks = [
      0,
      delayScale.invert(viewHeight * 0.1),
      delayScale.invert(viewHeight * 0.2),
      delayScale.invert(viewHeight * 0.3),
      delayScale.invert(viewHeight * 0.4),
      delayScale.invert(viewHeight * 0.5),
      delayScale.invert(viewHeight * 0.6),
      delayScale.invert(viewHeight * 0.7),
      delayScale.invert(viewHeight * 0.8),
      delayScale.invert(viewHeight * 0.9),
      delayRange[1]
      ]*/
      let delayTimeTicks = [0]
      for (let i = 1; i <= 19; i++) {
        let tmp = delayScale.invert(viewHeight * 0.05 * i)
        delayTimeTicks.push(tmp)
      }
      delayTimeTicks.push(delayRange[1])
      this.delayTimeTicks = delayTimeTicks
      console.log("test", timeTicks.length, delayTimeTicks.length)


      let g1 = svg.append('g').attr('transform', 'translate(' + (padding.left + viewWidth) + ',' + (padding.top + rootH) + ')')

      let timeg = g1.selectAll('g').data(timeTicks).enter()
        .append('g').attr('transform', d => {
          return 'translate(0' + ',' + yscale(timeRange[0] + d) + ')'
        })

      timeg.append('line')
        .attr('x1', 0)
        .attr('y1', 0)
        .attr('x2', '0.3em')
        .attr('y2', 0)
        .style('stroke', '#eee')
        .style('stroke-width', 1)

      timeg.append('text')
        .text(d => { return self.delayTimeFormat(d) })
        .attr('x', '0.4em')
        .attr('y', 0)
        .attr('dy', '0.25em')
        .attr('text-anchor', 'start')
        .attr('font-size', '0.6em')
        .attr('opacity', (d, i) => {
          // return 1
          if (i == 0 || self.delayTimeFormat(d) != self.delayTimeFormat(timeTicks[i - 1]))
            return 1
          return 0
        })



      { // 播放器

        let controlColor = '#ccc'
        let g = svg.append('g').attr('transform', 'translate(0,' + (padding.top + rootH) + ')')
          .append('g').attr('id', 'control').call(d3.drag()
            .on("start", dragstarted)
            .on("drag", dragged)
            .on("end", dragended))


        g.append('line')
          .attr('x1', padding.left - binWidth * 0.2)
          .attr('x2', padding.left + viewWidth)
          .attr('id', 'controlLine')
          .style('stroke', controlColor)
          .style('stroke-width', 1)

        let h = binWidth * 0.2
        let p1 = [padding.left + viewWidth, 0],
          p2 = [padding.left + viewWidth + h, -h],
          p3 = [padding.left + viewWidth + h, h]

        let tri = [p1, p2, p3]

        g.append('path')
          .attr('d', 'M' + tri.join('L') + 'Z')
          .attr('id', 'controlTri')
          .style('fill', controlColor)
          .style('stroke', controlColor)
          .style('stroke-width', 1)


        function dragstarted(d) {
          self.svg.select('#brush').call(brush.move, null);
          self.firstAnimation = false
          if (self.firstDrag) {
            self.$store.state.firstAnimation = Math.random()
          }
          self.firstDrag = false
          self.svg.selectAll('.rect')
            .style('opacity', 0.1)

          Global.timeLineStatus = 'Drag'
        }

        function dragged(d) {
          let y = d3.event.y
          // console.log(y)
          d3.select('#control').attr('transform', 'translate(0,' + y + ')')
          Global.timeLineStatus = 'Drag'
          self.viewUpdate2(y, false) // 
        }

        function dragended(d, i) {
          let y = d3.event.y
          d3.select('#control').attr('transform', 'translate(0,' + y + ')')
          Global.timeLineStatus = 'Drag'
          self.viewUpdate2(y, true) // 
        }
      }
      this.sliderChange(0)
    },

    delayTimeFormat: function(d) {
      if (d == 0) return 0
      d = +d
      if (d < 60) {
        return d + 's'
      }
      // if (d < 3600) {
      //   return Math.floor(d / 60) + 'm'
      // }
      // if (d < 3600 * 24) {
      //   return Math.floor(d / 3600) + 'h'
      // }

      // if (d < 3600 * 24 * 30) {
      //   return Math.floor(d / 3600 / 24) + 'd'
      // }

      // if (d < 3600 * 24 * 365) {
      //   return Math.floor(d / 3600 / 24 / 30) + 'M'
      // }
      // return Math.floor(d / 3600 / 24 / 365) + 'y'
      if (d < 3600) {
        return (d / 60).toFixed(1) + 'm'
      }
      if (d < 3600 * 24) {
        return (d / 3600).toFixed(1) + 'h'
      }

      if (d < 3600 * 24 * 30) {
        return (d / 3600 / 24).toFixed(1) + 'd'
      }

      if (d < 3600 * 24 * 365) {
        return (d / 3600 / 24 / 30).toFixed(1) + 'M'
      }
      return (d / 3600 / 24 / 365).toFixed(1) + 'y'
    },

    drawTree: function(repostlist) {
      let rootH = this.rootH
      let svg = this.svg

      let padding = this.padding,
        viewWidth = this.width - padding.left - padding.right,
        viewHeight = this.height - padding.top - padding.bottom
      let binWidth = viewWidth / repostlist.length
      let maxDepth = d3.max(repostlist, d => { return d.root.depth })
      let h = padding.top / (maxDepth + 1)
      let g = svg.append('g').attr('transform', 'translate(' + padding.left + ',' + 0 + ')')

      let yscale = this.yscale

      let linkg = g.append('g')

      let nodeg = g.append('g')

      nodeg = nodeg.selectAll('g')
        .data(repostlist)
        .enter()
        .append('g')
        .attr('transform', (d, i) => {
          return 'translate(' + (i * binWidth) + ',0)'
        })

      nodeg.append('line').attr('class', 'vertical')
        .attr('x1', binWidth * 0.4)
        .attr('y1', d => {
          return d.root.depth * h
        })
        .attr('x2', binWidth * 0.4)
        .attr('y2', d => {
          return padding.top + yscale(d.root.t)
        })
        .style('stroke-width', 0.5)
        .style('stroke', '#ccc')
        .style('fill', 'none')
        .style("stroke-dasharray", ("4, 2"))


      for (let i = 1; i < repostlist.length; i++) {
        for (let j = i - 1; j >= 0; j--) {
          let r1 = repostlist[i].root,
            r2 = repostlist[j].root
          if (r1.pid == r2.mid) {
            let x1 = i * binWidth + 0.4 * binWidth,
              // y1 = yscale(r1.t) - rootH * 2,
              y1 = r1.depth * h,
              x2 = j * binWidth + 0.4 * binWidth,
              y2 = r2.depth * h
            // y2 = yscale(r2.t) - rootH * 2

            let midx = x1,
              midy = y2

            let points = [
              [x1, y1],
              [midx, midy],
              [x2, y2]
            ]

            let mid1 = r1.mid,
              mid2 = r2.mid

            linkg.append('path')
              .attr('d', 'M' + points.join('L'))
              .style('stroke-width', 1.5)
              .style('stroke', d => {
                if (Global.opinionRelations[mid1 + ',' + mid2] == false) {
                  return Global.routeColor
                }
                if (Global.followingRelations[mid1 + ',' + mid2] == false) {
                  return Global.bridgeColor
                }
                return Global.riverColor
              })
              .style('fill', 'none')
              .style("stroke-dasharray", d => {
                if (Global.opinionRelations[mid1 + ',' + mid2] == false) {
                  return ("2, 2")
                }
              })
          }
        }
      }
      if (Global.filename == 'nanxiaoqi') {
        let g = nodeg.append('g').attr('transform', d => {
          return 'translate(' + binWidth * 0.4 + ',' + d.root.depth * h + ')'
        })
        let r = binWidth * 0.25
        g.append('defs')
          .append('clipPath')
          .attr('id', d => { return 'user' + d.root.name })
          .append('circle')
          .attr('r', r)
        // .style('fill', Global.riverColor)
        g.append("image")
          .attr("xlink:href", d => {
            let url = ''
            if (d.root.name == '南小七7') {
              // url = 'assets/logo.png'
              url = 'static/images/nanxiaoqi.jpg'
            }
            if (d.root.name == '姚晨') {
              url = 'static/images/yaochen.jpg'
            }
            if (d.root.name == '十年砍柴') {
              url = 'static/images/kanchai.jpg'
            }
            if (d.root.name == '新浪娱乐') {
              url = 'static/images/sina.jpg'
            }
            if (d.root.name == '王志安') {
              url = 'static/images/wangzhian.jpg'
            }
            return url
          })
          .attr('clip-path', d => {
            return 'url(#user' + d.root.name + ')'
          })
          .attr("x", -r * 1.1)
          .attr("y", -r * 1.1)
          .attr("width", r * 2.2)
          .attr("height", r * 2.2)
          .attr('cursor', 'pointer')
          .on('click', d => {
            let mids = [d.root.mid]
            for (let i in d.repostings)
              mids.push(d.repostings[i].mid)
            svg.selectAll('.rect').filter(d => {
                return mids.indexOf(d.mid) >= 0
              })
              .style('opacity', 1)

            svg.selectAll('.rect').filter(d => {
                return mids.indexOf(d.mid) < 0
              })
              .style('opacity', 0.1)


            svg.selectAll('.kprect').filter(d => {
                return mids.indexOf(d.root.mid) >= 0
              })
              .style('opacity', 1)

            svg.selectAll('.kprect').filter(d => {
                return mids.indexOf(d.root.mid) < 0
              })
              .style('opacity', 0.1)

            Global.highlightedPolygons = mids
            Global.tableViewUpdate = true
            Global.topicViewUpdate = true
            Global.behaviorType = 'TimelineView'
            this.$store.state.highlightPolygons = 'TimelineView ' + Math.random()
          })

      } else {
        nodeg.append('circle')
          .attr('cx', binWidth * 0.4)
          .attr('cy', d => {
            return d.root.depth * h
          })
          .attr('r', binWidth * 0.15)
          .style('fill', Global.riverColor)
          .style('cursor', 'pointer')
          .on('click', d => {
            let mids = [d.root.mid]
            for (let i in d.repostings)
              mids.push(d.repostings[i].mid)
            svg.selectAll('.rect').filter(d => {
                return mids.indexOf(d.mid) >= 0
              })
              .style('opacity', 1)

            svg.selectAll('.rect').filter(d => {
                return mids.indexOf(d.mid) < 0
              })
              .style('opacity', 0.1)


            svg.selectAll('.kprect').filter(d => {
                return mids.indexOf(d.root.mid) >= 0
              })
              .style('opacity', 1)

            svg.selectAll('.kprect').filter(d => {
                return mids.indexOf(d.root.mid) < 0
              })
              .style('opacity', 0.1)

            Global.highlightedPolygons = mids
            Global.tableViewUpdate = true
            Global.topicViewUpdate = true
            Global.behaviorType = 'TimelineView'
            this.$store.state.highlightPolygons = 'TimelineView ' + Math.random()
          })
          .append('title')
          .text(d => { return d.root.name })
      }
      console.log("test", repostlist)
    },
    alignClick: function() {
      let self = this
      this.refreshClick()
      this.aligned = !this.aligned
      let padding = this.padding,
        viewWidth = this.width - padding.left - padding.right,
        viewHeight = this.height - padding.top - padding.bottom,
        repostlist = this.repostlist
      let binWidth = viewWidth / repostlist.length
      let maxDepth = d3.max(repostlist, d => { return d.root.depth })
      let svg = this.svg
      let h = padding.top / (maxDepth + 1)
      let yscale = this.yscale
      let alignScale = this.alignScale
      let rootH = this.rootH
      let delayScale = this.delayScale

      if (this.aligned) {
        svg.selectAll('.vertical')
          .attr('y2', d => {
            return padding.top
          })

        svg.selectAll('.kprect')
          .attr('y', d => {
            return 0 - rootH
          })
        svg.selectAll('.rect')
          .attr('y', (d) => {
            // return alignScale(d.delayStep)
            return delayScale(d.delayTime)
          })
        $('#alignButton').addClass('buttonClicked')

        svg.selectAll('text')
          .text(function(d, i) {
            return self.delayTimeFormat(self.delayTimeTicks[i])
          })
          .attr('opacity', (d, i) => {
            if (i == 0 || self.delayTimeFormat(self.delayTimeTicks[i]) != self.delayTimeFormat(self.delayTimeTicks[i - 1]))
              return 1
            return 0
          })

      } else {
        svg.selectAll('.vertical')
          .attr('y2', d => {
            return padding.top + yscale(d.root.t)
          })

        svg.selectAll('.kprect')
          .attr('y', d => {
            return yscale(d.root.t) - rootH
          })

        svg.selectAll('.rect')
          .attr('y', d => {
            return yscale(d.t)
          })

        $('#alignButton').removeClass('buttonClicked')
        svg.selectAll('text')
          .text(function(d, i) {
            return self.delayTimeFormat(self.timeTicks[i])
          })
          .attr('opacity', (d, i) => {
            if (i == 0 || self.delayTimeFormat(self.timeTicks[i]) != self.delayTimeFormat(self.timeTicks[i - 1]))
              return 1
            return 0
          })
      }
    },

    startAnimation: function() {
      let self = this
      let timer
      let padding = this.padding,
        viewWidth = this.width - padding.left - padding.right,
        viewHeight = this.height - padding.top - padding.bottom
      let ticks = Global.timeLineSteps
      let yscale = this.yscale
      let delayScale = this.delayScale

      if (!self.aligned) {
        let startTime = this.timeRange[0],
          endTime = this.timeRange[1]

        let currentTime = self.currentTime,
          timeStep = self.timeStep

        let splitTime = this.splitTime + this.timeRange[0]
        let times = self.times

        timer = setInterval(function() {
          // console.log(currentTime, endTime)
          if (self.currentTime >= endTime) {
            clearInterval(timer)
            self.playStatus = 'Play'
          } else {
            self.previousTime = self.currentTime
            let currentY = yscale(self.currentTime) + viewHeight / ticks
            Global.timeLineWhere = currentY / viewHeight
            self.currentTime = yscale.invert(currentY)
            self.viewUpdate(startTime, self.currentTime, self.previousTime, true)
          }
        }, self.updateTime)
        this.timer = timer
      } else {
        let startDelayTime = this.delayRange[0],
          endDelayTime = this.delayRange[1]

        let currentDelayTime = self.currentDelayTime,
          delayStep = self.delayStep

        let delaySplitTime = this.delaySplitTime
        let delayTimes = self.delayTimes

        timer = setInterval(function() {
          if (self.currentDelayTime >= endDelayTime) {
            clearInterval(timer)
            self.playStatus = 'Play'
          } else {
            self.previousTime = self.currentDelayTime
            let currentY = delayScale(self.currentDelayTime) + viewHeight / ticks
            Global.timeLineWhere = currentY / viewHeight
            self.currentDelayTime = delayScale.invert(currentY)
            self.viewUpdate(startDelayTime, self.currentDelayTime, self.previousTime, true)
          }
        }, self.updateTime)
        this.timer = timer
        // let startStep = this.alignRange[0],
        //   endStep = this.alignRange[1]

        // let currentStep = self.currentStep,
        //   alignStep = self.alignStep

        // let splitTime = this.splitTime + this.timeRange[0]

        // timer = setInterval(function() {
        //   if (currentStep >= endStep) {
        //     clearInterval(timer)
        //     self.playStatus = 'Play'
        //   } else {
        //     self.currentStep = self.currentStep + alignStep
        //     self.viewUpdate(startStep, self.currentStep, true)
        //   }
        // }, self.updateTime)
        // this.timer = timer
      }

    },
    viewUpdate2: function(y, notify) { // 时间轴拖动
      let start = 0,
        end = 0
      if (!this.aligned) {
        start = this.timeRange[0]
        end = this.yscale.invert(y)
        this.previousTime = this.currentTime
        this.currentTime = end
      } else {
        // start = this.alignRange[0]
        // end = this.alignScale.invert(y)
        // this.currentStep = end
        start = this.delayRange[0]
        end = this.delayScale.invert(y)
        this.previousTime = this.currentDelayTime
        this.currentDelayTime = end
      }

      Global.timeLineWhere = y / this.viewHeight
      this.viewUpdate(start, end, this.previousTime, notify)
    },
    viewUpdate: function(start, end, previous, notify) {
      // console.log(notify, start, end)
      let y = 0
      if (!this.aligned) {
        y = this.yscale(end)
      } else {
        y = this.delayScale(end)
      }
      // console.log(y)
      d3.select('#control')
        .transition()
        .attr('transform', 'translate(0,' + y + ')')
        .duration(0)

      let mids = [],
        fresh_mids = [] // 在新跳的时间步添加的微博

      if (!this.aligned) {
        mids = Global.nodes.filter(d => {
          return +d.t >= start && +d.t <= end
        }).map(d => {
          return d.mid
        })

        fresh_mids = Global.nodes.filter(d => {
          return +d.t >= previous && +d.t < end
        }).map(d => {
          return d.mid
        })

      } else {
        mids = Global.nodes.filter(d => {
          return +d.delayTime >= start && +d.delayTime <= end
        }).map(d => {
          return d.mid
        })

        fresh_mids = Global.nodes.filter(d => {
          return +d.t >= previous && +d.t < end
        }).map(d => {
          return d.mid
        })

      }

      let svg = this.svg

      svg.selectAll('.rect').filter(d => {
          return mids.indexOf(d.mid) >= 0
        })
        .style('opacity', 1)

      svg.selectAll('.rect').filter(d => {
          return mids.indexOf(d.mid) < 0
        })
        .style('opacity', 0.1)


      svg.selectAll('.kprect').filter(d => {
          return mids.indexOf(d.root.mid) >= 0
        })
        .style('opacity', 1)

      svg.selectAll('.kprect').filter(d => {
          return mids.indexOf(d.root.mid) < 0
        })
        .style('opacity', 0.1)

      // console.log(fresh_mids)
      if (notify) {
        // console.log(mids.length, notify)
        Global.highlightedPolygons = mids
        Global.freshPolygons = fresh_mids
        Global.tableViewUpdate = true
        Global.topicViewUpdate = true
        Global.behaviorType = 'TimelineView'
        this.$store.state.highlightPolygons = 'TimelineView ' + Math.random()
      }

    },
    viewUpdate3: function(left, right, start, end, notify) {
      let y = 0
      if (!this.aligned) {
        y = this.yscale(end)
      } else {
        y = this.delayScale(end)
      }
      // console.log(y)
      d3.select('#control')
        .transition()
        .attr('transform', 'translate(0,' + y + ')')
        .duration(0)

      let mids = []

      let svg = this.svg
      let binWidth = this.binWidth
      let gs = svg.selectAll('.rectGroup').filter((d, i) => {
        return i * binWidth >= left && i * binWidth + binWidth * 0.8 <= right
      })

      if (!this.aligned) {
        gs.selectAll('.rect').filter(d => {
          if (+d.t >= start && +d.t <= end) {
            mids.push(d.mid)
            return true
          }
          return false
        })
        gs.selectAll('.kprect').filter(d => {
          if (+d.root.t >= start && +d.root.t <= end) {
            mids.push(d.root.mid)
            return true
          }
          return false
        })
      } else {
        gs.selectAll('.rect').filter(d => {
          if (+d.delayTime >= start && +d.delayTime <= end) {
            mids.push(d.mid)
            return true
          }
          return false
        })
        gs.selectAll('.kprect').filter(d => {
          if (+d.root.delayTime >= start && +d.root.delayTime <= end) {
            mids.push(d.root.mid)
            return true
          }
          return false
        })
      }

      svg.selectAll('.rect').filter(d => {
          return mids.indexOf(d.mid) >= 0
        })
        .style('opacity', 1)

      svg.selectAll('.rect').filter(d => {
          return mids.indexOf(d.mid) < 0
        })
        .style('opacity', 0.1)


      svg.selectAll('.kprect').filter(d => {
          return mids.indexOf(d.root.mid) >= 0
        })
        .style('opacity', 1)

      svg.selectAll('.kprect').filter(d => {
          return mids.indexOf(d.root.mid) < 0
        })
        .style('opacity', 0.1)


      if (notify) {
        // console.log(mids.length, notify)
        Global.highlightedPolygons = mids
        Global.tableViewUpdate = true
        Global.topicViewUpdate = true
        Global.behaviorType = 'TimelineView'
        this.$store.state.highlightPolygons = 'TimelineView ' + Math.random()
      }

    },
    getScales: function() {
      let svg = this.svg
      let self = this
      let padding = this.padding,
        viewWidth = this.width - padding.left - padding.right,
        viewHeight = this.height - padding.top - padding.bottom

      let tmp = []
      let N = 0
      let sampleTime = this.sampleTime
      let timeRange = this.timeRange
      let delayRange = this.delayRange
      let times = this.times
      // sampling
      for (let i = 0; i <= Math.floor((timeRange[1] - timeRange[0]) / sampleTime); i++)
        tmp.push([])
      for (let i = 0; i < times.length; i++)
      //if(times[i] <= splitTime)
      {
        let p = Math.floor((times[i] - timeRange[0]) / sampleTime)
        tmp[p].push(times[i])
        N++
      }
      let yscaleDomain = []
      let yscaleRange = []
      let now = 0
      for (let i = 0; i <= Math.floor((timeRange[1] - timeRange[0]) / sampleTime); i++) {
        if (tmp[i].length == 0)
          continue
        let st = now * 1.0 / N * viewHeight
        now += tmp[i].length
        yscaleDomain.push(tmp[i][0])
        yscaleRange.push(st)
      }

      yscaleDomain.push(timeRange[1] * 1.01)
      yscaleRange.push(viewHeight)
      //console.log("yscaleDomain", yscaleDomain)
      //console.log("yscaleRange", yscaleRange)

      let yscale = d3.scaleLinear().domain(yscaleDomain)
      yscale.range(yscaleRange)
      this.yscale = yscale

      let delayTimes = this.delayTimes
      let delayScaleDomain = []
      let delayScaleRange = []
      tmp = []
      N = 0
      for (let i = 0; i <= Math.floor((delayRange[1] - delayRange[0]) / sampleTime); i++)
        tmp.push([])
      for (let i in delayTimes) {
        let p = Math.floor((delayTimes[i] - delayRange[0]) / sampleTime)
        tmp[p].push(delayTimes[i])
        N++
      }
      now = 0
      for (let i = 0; i <= Math.floor((delayRange[1] - delayRange[0]) / sampleTime); i++) {
        if (tmp[i].length == 0)
          continue
        let st = now * 1.0 / N * viewHeight
        now += tmp[i].length
        delayScaleDomain.push(tmp[i][0])
        delayScaleRange.push(st)
      }

      delayScaleDomain.push(delayRange[1] * 1.01)
      delayScaleRange.push(viewHeight)
      let delayScale = d3.scaleLinear().domain(delayScaleDomain)
      delayScale.range(delayScaleRange)
      this.delayScale = delayScale

      let timeTicks = [0]
      for (let i = 1; i <= 19; i++) {
        let tmp = yscale.invert(viewHeight * 0.05 * i) - timeRange[0]
        timeTicks.push(tmp)
      }
      timeTicks.push(timeRange[1] - timeRange[0])
      //console.log("timeTicks", timeTicks)
      this.timeTicks = timeTicks

      let delayTimeTicks = [0]
      for (let i = 1; i <= 19; i++) {
        let tmp = delayScale.invert(viewHeight * 0.05 * i)
        delayTimeTicks.push(tmp)
      }
      delayTimeTicks.push(delayRange[1])
      this.delayTimeTicks = delayTimeTicks
    },

    changeSampleTime: function() {
      let svg = this.svg
      let self = this
      let padding = this.padding,
        viewWidth = this.width - padding.left - padding.right,
        viewHeight = this.height - padding.top - padding.bottom

      let yscale = this.yscale
      let delayScale = this.delayScale
      let rootH = this.rootH
      if (this.aligned) {
        svg.selectAll('.rect')
          .attr('y', (d) => {
            // return alignScale(d.delayStep)
            return delayScale(d.delayTime)
          })

        svg.selectAll('text')
          .text(function(d, i) {
            return self.delayTimeFormat(self.delayTimeTicks[i])
          })
          .attr('opacity', (d, i) => {
            if (i == 0 || self.delayTimeFormat(self.delayTimeTicks[i]) != self.delayTimeFormat(self.delayTimeTicks[i - 1]))
              return 1
            return 0
          })

        svg.selectAll('.vertical')
          .attr('y2', d => {
            return padding.top
          })

      } else {
        svg.selectAll('.kprect')
          .attr('y', d => {
            return yscale(d.root.t) - rootH
          })

        svg.selectAll('.rect')
          .attr('y', d => {
            return yscale(d.t)
          })

        svg.selectAll('text')
          .text(function(d, i) {
            return self.delayTimeFormat(self.timeTicks[i])
          })
          .attr('opacity', (d, i) => {
            if (i == 0 || self.delayTimeFormat(self.timeTicks[i]) != self.delayTimeFormat(self.timeTicks[i - 1]))
              return 1
            return 0
          })

        svg.selectAll('.vertical')
          .attr('y2', d => {
            return padding.top + yscale(d.root.t)
          })
      }
    },

    formatTooltip: function(val) {
      let min = this.minSampleTime
      let max = this.maxSampleTime
      let scale = d3.scaleLinear().domain([0, 100]).range([min, max])
      let tmp = scale(val)
      tmp = this.delayTimeFormat(tmp)
      return tmp
    },

    sliderChange: function(val) {
      let min = this.minSampleTime
      let max = this.maxSampleTime
      let scale = d3.scaleLinear().domain([0, 100]).range([min, max])
      let tmp = scale(val)
      this.sampleTime = tmp
      this.getScales()
      this.changeSampleTime()
    }
  }
}

</script>
