<script>
import { mapGetters } from 'vuex';
export default {
  name: 'SkeletonView',
  data() {
    return {
      cellCnt: 0
    }
  },
  mounted: function() {
    this.fontSize = '0.6rem'
    this.radius = 0.4
    this.percent = 0.3
    this.padding = 4
    this.drawRoute()
    this.drawBridge()
    this.drawCountry()
    this.drawRegion()
    this.drawRiver()
    this.drawCapital()
    this.drawcounty()
    this.drawLake()
    this.init()
    this.drawLevel()
    this.drawSentiment()
    // this.draw()
  },
  computed: {
    ...mapGetters(['keyplayersExtracted']),
  },
  watch: {
    keyplayersExtracted: function() {
      // return
      this.drawLevel()
      this.drawSentiment()

    }
  },
  methods: {
    init() {
      d3.selectAll(("input[name='switchBtn']")).on("change", function() {
        Global.switchColorFlag = this.value
        Store.state.changeColorEncoding = Math.random()
        console.log(this.value)
        d3.selectAll('.city')
          .style("fill", d => {
            if (Global.switchColorFlag == 'level') {
              return MapColor(d.tree)
            } else if (Global.switchColorFlag == 'sentiment') {
              return SentimentColor(d.tree)
            }
          })
          .style("stroke", d => {
            if (Global.switchColorFlag == 'level') {
              return MapColor(d.tree)
            } else if (Global.switchColorFlag == 'sentiment') {
              return SentimentColor(d.tree)
            }
          })
      })
      d3.selectAll(("input[name='checkbox']")).property('checked', true)
      d3.select(("input[value='county'")).property('checked', false)
      d3.selectAll(("input[name='checkbox']")).on("change", function() {
        let value = this.value,
          status = d3.select(this).property('checked')
        if (value == 'river') {
          RiverView.riverShowUpdate(status)
        }
        if (value == 'county') {
          Global.cityShown = status
          VoronoiView.cityShowUpdate(status)
        }
        if (value == 'country') {
          BoundaryView.countryShowUpdate(status)
        }
        if (value == 'region') {
          BoundaryView.regionShowUpdate(status)
        }
        if (value == 'bridge') {
          RiverView.bridgeShowUpdate(status)
        }
        if (value == 'route') {
          RiverView.routeShowUpdate(status)
        }
      })
    },
    drawRoute: function() {
      $('#l_route').empty()
      let width = $('#l_route').width(),
        height = $('#l_route').height()

      let start = this.padding
      let end = width - 2 * start
      let svg = d3.select('#l_route').append('svg').attr('width', width).attr('height', height)
      svg.append('line')
        .attr('x1', start)
        .attr('y1', height / 2)
        .attr('x2', end - start)
        .attr('y2', height / 2)
        .style('stroke', d => {
          return Global.routeColor
        })
        .style('stroke-width', 1.5)
        .style("stroke-dasharray", ("4, 2"))


      // svg.append('text')
      //   .text('Route')
      //   .attr('x', end)
      //   .attr('y', height / 2)
      //   .attr('font-size', this.fontSize)
      //   .attr('text-anchor', 'start')
      //   .attr('alignment-baseline', 'middle')
    },
    drawBridge: function() {
      $('#l_bridge').empty()
      let width = $('#l_bridge').width(),
        height = $('#l_bridge').height()

      let start = this.padding
      let end = width - 2 * start

      let svg = d3.select('#l_bridge').append('svg').attr('width', width).attr('height', height)
      svg.append('line')
        .attr('x1', start)
        .attr('y1', height / 2)
        .attr('x2', end)
        .attr('y2', height / 2)
        .style('stroke', d => {
          return Global.bridgeColor
        })
        .style('stroke-width', 1.5)
        .append('title')
        .text('Unfollower Reposting')
      // svg.append('text')
      //   .text('Bridge')
      //   .attr('x', width * 0.5)
      //   .attr('y', height / 2)
      //   .attr('font-size', this.fontSize)
      //   .attr('text-anchor', 'start')
      //   .attr('alignment-baseline', 'middle')
    },
    drawCountry: function() {
      $('#l_country').empty()
      let width = $('#l_country').width(),
        height = $('#l_country').height()
      let start = this.padding
      let end = width - 2 * start
      let svg = d3.select('#l_country').append('svg').attr('width', width).attr('height', height)
      svg.append('line')
        .attr('x1', start)
        .attr('y1', height / 2)
        .attr('x2', end)
        .attr('y2', height / 2)
        .style('stroke', d => {
          return Global.countryStroke
        })
        .style('stroke-width', 1.5)
        .append('title')
        .text('Terrain by Key Player')
      // svg.append('text')
      //   .text('Country')
      //   .attr('x', width * 0.5)
      //   .attr('y', height / 2)
      //   .attr('font-size', this.fontSize)
      //   .attr('text-anchor', 'start')
      //   .attr('alignment-baseline', 'middle')
    },
    drawRegion: function() {
      $('#l_region').empty()
      let width = $('#l_region').width(),
        height = $('#l_region').height()
      let start = this.padding
      let end = width - 2 * start
      let svg = d3.select('#l_region').append('svg').attr('width', width).attr('height', height)
      svg.append('line')
        .attr('x1', start)
        .attr('y1', height / 2)
        .attr('x2', end)
        .attr('y2', height / 2)
        .style('stroke', d => {
          return Global.regionStroke
        })
        .style('stroke-width', 1)
        .append('title')
        .text('Groups of counties with Same Semantics')
      // svg.append('text')
      //   .text('Region')
      //   .attr('x', width * 0.5)
      //   .attr('y', height / 2)
      //   .attr('font-size', this.fontSize)
      //   .attr('text-anchor', 'start')
      //   .attr('alignment-baseline', 'middle')
    },

    drawRiver: function() {
      $('#l_river').empty()
      let width = $('#l_river').width(),
        height = $('#l_river').height()

      let start = this.padding
      let end = width - 2 * start
      let svg = d3.select('#l_river').append('svg').attr('width', width).attr('height', height)
      svg.append('line')
        .attr('x1', start)
        .attr('y1', height / 2)
        .attr('x2', end)
        .attr('y2', height / 2)
        .style('stroke', d => {
          return Global.riverColor
        })
        .style('stroke-width', 1.5)
      // svg.append('text')
      //   .text('River')
      //   .attr('x', width * 0.5)
      //   .attr('y', height / 2)
      //   .attr('font-size', this.fontSize)
      //   .attr('text-anchor', 'start')
      //   .attr('alignment-baseline', 'middle')
      //   .append('title')
      //   .text('Reposting between Key Players')
    },

    drawCapital: function() {
      $('#l_capital').empty()
      let width = $('#l_capital').width(),
        height = $('#l_capital').height()

      let svg = d3.select('#l_capital').append('svg').attr('width', width).attr('height', height)

      let start = this.padding
      let end = width - 2 * start
      let r = height * this.radius
      let scale = 1.4
      let path = [

        [-1 * r / Math.sqrt(2), 0],
        [0, -1 * r / Math.sqrt(2)],
        [r / Math.sqrt(2), 0],
        [0, r / Math.sqrt(2)]
      ]

      let path2 = [

        [-1 * r / Math.sqrt(2) * scale, 0],
        [0, -1 * r / Math.sqrt(2) * scale],
        [r / Math.sqrt(2) * scale, 0],
        [0, r / Math.sqrt(2) * scale]
      ]


      svg.append('g').attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')')
        .append('path')
        .attr('d', 'M' + path.join('L') + 'Z')
        .style('fill', Global.riverColor)
        .style('stroke', d => {
          return 'black'
        })
        .style('stroke-width', 0.5)
        .append('title')
        .text('Key Player')

      svg.append('g').attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')')
        .append('path')
        .attr('d', 'M' + path2.join('L') + 'Z')
        .style('fill', 'none')
        .style('stroke', d => {
          return 'black'
        })
        .style('stroke-width', 0.5)
        .append('title')
        .text('Root User')


      // svg.append('text')
      //   .text('Source')
      //   .attr('x', width * 0.5)
      //   .attr('y', height / 2)
      //   .attr('font-size', this.fontSize)
      //   .attr('text-anchor', 'start')
      //   .attr('alignment-baseline', 'middle')
    },


    drawcounty: function() {
      $('#l_county').empty()
      let width = $('#l_county').width(),
        height = $('#l_county').height()

      let svg = d3.select('#l_county').append('svg').attr('width', width).attr('height', height)

      let start = this.padding
      let end = width - 2 * start
      let r = height * this.radius
      let scale = 1.4
      let path = [

        [-1 * r / Math.sqrt(2), 0],
        [0, -1 * r / Math.sqrt(2)],
        [r / Math.sqrt(2), 0],
        [0, r / Math.sqrt(2)]
      ]


      svg.append('g').attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')')
        .append('path')
        .attr('d', 'M' + path.join('L') + 'Z')
        .style('fill', Global.cityColor)
        .style('stroke', d => {
          return 'black'
        })
        .style('stroke-width', 0.5)


      // svg.append('text')
      //   .text('county')
      //   .attr('x', width * 0.5)
      //   .attr('y', height / 2)
      //   .attr('font-size', this.fontSize)
      //   .attr('text-anchor', 'start')
      //   .attr('alignment-baseline', 'middle')
      //   .append('title')
      //   .text('Reposting Node')
    },

    drawLake: function() {
      $('#l_lake').empty()
      let width = $('#l_lake').width(),
        height = $('#l_lake').height()

      let svg = d3.select('#l_lake').append('svg').attr('width', width).attr('height', height)
      let start = this.padding
      let end = width - 2 * start
      let r = height * this.radius
      let scale = 1.4
      let path = [

        [-1 * r / Math.sqrt(2), 0],
        [0, -1 * r / Math.sqrt(2)],
        [r / Math.sqrt(2), 0],
        [0, r / Math.sqrt(2)]
      ]


      svg.append('g').attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')')
        .append('path')
        .attr('d', 'M' + path.join('L') + 'Z')
        .style('fill', Global.riverColor)
        .style('stroke', d => {
          return 'black'
        })
        .style('stroke-width', 0.5)


      // svg.append('text')
      //   .text('Lake')
      //   .attr('x', width * 0.5)
      //   .attr('y', height / 2)
      //   .attr('font-size', this.fontSize)
      //   .attr('text-anchor', 'start')
      //   .attr('alignment-baseline', 'middle')
      //   .append('title')
      //   .text('Key Player')
    },

    drawSentiment: function() {
      $('#l_sentiment').empty()
      let width = $('#l_sentiment').width(),
        height = $('#l_sentiment').height()

      let svg = d3.select('#l_sentiment').append('svg').attr('width', width).attr('height', height)
      var positiveColor = Global.positiveColor

      var neutralColor = Global.neutralColor

      var negativeColor = Global.negativeColor

      let domain = [negativeColor, neutralColor, positiveColor]
      let mapping = ['Negative', 'Neutral', 'Positive']

      let left = 0.75
      let padding = 15
      let r = this.levelR
      let textwidth = width * 0.15
      let split = width * left
      svg.selectAll('rect')
        .data(domain)
        .enter()
        .append('rect')
        .attr('x', (d, i) => {
          return split - (domain.length - i) * (r + textwidth)
        })
        .attr('y', (d, i) => {
          return height / 2 - r / 2
        })
        .attr('width', r)
        .attr('height', r)
        .style('fill', d => {
          return d
        })
        .style('stroke', 'none')
        .append('title')
        .text((d, i) => {
          return mapping[i]
        })

      svg.append('text')
        .text('Negative')
        .attr('x', split - domain.length * (r + textwidth) + r)
        .attr('y', height / 2)
        .attr('font-size', this.fontSize)
        .attr('text-anchor', 'start')
        .attr('alignment-baseline', 'middle')

      svg.append('text')
        .text('Neutral')
        .attr('x', split - (domain.length - 1) * (r + textwidth) + r)
        .attr('y', height / 2)
        .attr('font-size', this.fontSize)
        .attr('text-anchor', 'start')
        .attr('alignment-baseline', 'middle')


      svg.append('text')
        .text('Positive')
        .attr('x', split - (domain.length - 2) * (r + textwidth) + r)
        .attr('y', height / 2)
        .attr('font-size', this.fontSize)
        .attr('text-anchor', 'start')
        .attr('alignment-baseline', 'middle')


      svg.append('text')
        .attr('x', split)
        .attr('y', height / 2)
        .text('Sentiment')
        .attr('font-size', this.fontSize)
        .attr('text-anchor', 'start')
        .attr('alignment-baseline', 'middle')


    },

    drawLevel: function() {
      $('#l_level').empty()

      let width = $('#l_level').width(),
        height = $('#l_level').height()

      let svg = d3.select('#l_level').append('svg').attr('width', width).attr('height', height)
      let padding = 15
      let domain = d3.range(1, Global.maxDepth + 1)
      let left = 0.75
      let r = (width * left - padding) / (domain.length + 2)
      r = Math.min(r, height * 0.8)
      let split = width * left
      console.log(domain)
      let space = 20
      this.levelR = r
      svg.selectAll('rect')
        .data(domain)
        .enter()
        .append('rect')
        .attr('x', (d, i) => {
          return split - (domain.length - i) * r - space
        })
        .attr('y', (d, i) => {
          return height / 2 - r / 2
        })
        .attr('width', r)
        .attr('height', r)
        .style('fill', d => {
          return MapColor({ 'depth': d + 1 })
        })
        .style('stroke', 'none')
        .append('title')
        .text((d, i) => {
          return d
        })

      svg.append('text')
        .attr('x', split - space)
        .attr('y', height / 2)
        .text(Global.maxDepth)
        .attr('font-size', this.fontSize)
        .attr('text-anchor', 'start')
        .attr('alignment-baseline', 'middle')

      svg.append('text')
        .attr('x', split - space - domain.length * r - 2)
        .attr('y', height / 2)
        .text(2)
        .attr('font-size', this.fontSize)
        .attr('text-anchor', 'end')
        .attr('alignment-baseline', 'middle')

      svg.append('text')
        .attr('x', split)
        .attr('y', height / 2)
        .text('Level')
        .attr('font-size', this.fontSize)
        .attr('text-anchor', 'start')
        .attr('alignment-baseline', 'middle')
    }

  }
}

</script>
