module.exports = function(words, paths, padding, right, bottom) {

  // console.log(words, paths, padding, right, bottom)
  var width = right,
    height = bottom, // 整个 页面的width，height, 然后每个部分分为不同的box
    size = [width, height],
    font = "Arial"


  var cw = 1 << 14,
    ch = 1 << 11


  // var left = d3.min(boundary, d => {
  //     return d[0]
  // })
  // var top = d3.min(boundary, d => {
  //     return d[1]
  // })

  // boundary.forEach(d => {
  //     d[0] = d[0] - left
  //     d[1] = d[1] - top
  // })

  words.forEach(d => {
    d.padding = padding
  })

  getImageData(words)

  var board = buildBoard(paths)
  for (let i in words) {
    place(words[i], board)
  }


  function getImageData(words) {
    // Get image Data for all words
    var c = getContext(cloudCanvas());
    c.clearRect(0, 0, cw, ch)

    var x = 0,
      y = 0,
      maxh = 0

    for (let i = 0; i < words.length; i++) {
      d = words[i]
      c.save()
      d.fontSize = d.size
      d.padding = padding
      d.rotate = 0

      c.font = ~~(d.fontSize + 1) + "px " + font;
      var w = ~~(c.measureText(d.text).width),
        h = d.fontSize;
      if (d.rotate) {
        var sr = Math.sin(d.rotate * cloudRadians),
          cr = Math.cos(d.rotate * cloudRadians),
          wcr = w * cr,
          wsr = w * sr,
          hcr = h * cr,
          hsr = h * sr;
        w = ~~Math.max(Math.abs(wcr + hsr), Math.abs(wcr - hsr));
        h = ~~Math.max(Math.abs(wsr + hcr), Math.abs(wsr - hcr));
      }

      if (h > maxh) maxh = h;
      if (x + w >= cw) {
        x = 0;
        y += maxh;
        maxh = 0;
      }
      if (y + h >= ch) break;
      c.translate((x + (w >> 1)), (y + (h >> 1)));
      if (d.rotate) c.rotate(d.rotate * cloudRadians);
      c.fillText(d.text, 0, 0);
      if (d.padding) c.lineWidth = 2 * d.padding, c.strokeText(d.text, 0, 0);
      c.restore();

      d.width = w;
      d.height = h;
      d.x = x;
      d.y = y;
      d.x1 = w >> 1;
      d.y1 = h >> 1;
      d.x0 = -d.x1;
      d.y0 = -d.y1;
      x += w;

    }


    for (let i = 0; i < words.length; i++) {
      let d = words[i]
      let w = d.width,
        h = d.height,
        x = d.x,
        y = d.y
      let pixels = c.getImageData(d.x, d.y, d.width, d.height).data;
      d.sprite = Array();
      for (let i = 0; i << 2 < pixels.length; i++) {
        d.sprite.push(pixels[i << 2]);
      }
    }

  }

  function buildSvg(boundary) {
    var path = 'M' + boundary.join('L') + 'Z'
    // var svg = d3.select('svg').attr('width', width).attr('height', height)
    var svg = d3.select(document.createElement('svg'))
      .attr('width', width)
      .attr('height', height)

    svg.append('path').attr('d', path).attr('fill', 'red').attr('id', 'path')
    return svg
  }

  function buildCanvas(paths) {
    // var svg = buildSvg(boundary)
    // var path = svg.select('#path').attr('d')
    // var path2d = new Path2D(path)
    // var canvas = document.getElementById("canvas")
    var canvas = document.createElement("canvas")
    var ctx = canvas.getContext('2d');
    canvas.width = width
    canvas.height = height
    ctx.fillStyle = 'red';
    for (let i in paths) {
      let path2d = new Path2D(paths[i])
      ctx.fill(path2d)
    }
    return canvas;
  }

  function buildBoard(paths) {

    var canvas = buildCanvas(paths)
    var width = canvas.width,
      height = canvas.height
    var board = {};
    board.x = 0;
    board.y = 0;
    board.width = width;
    board.height = height;
    var sprite = []
    for (var i = 0; i < width * height; i++) sprite[i] = 0;
    var c = canvas.getContext('2d');
    var pixels = c.getImageData(0, 0, width, height).data;
    // for (var i = 0; i < width * height; i++) {
    //   pixels[i] = !pixels[i]
    // }
    for (var i = 0; i < width * height; i++) {
      sprite[i] = !pixels[i << 2];
    }
    board.sprite = sprite;
    // var xRange = d3.extent(boundary, d => {
    //   return d[0]
    // })

    // var yRange = d3.extent(boundary, d => {
    //   return d[1]
    // })

    // board.boxX = xRange[0]
    // board.boxWidth = xRange[1] - xRange[0]
    // board.boxY = yRange[0]
    // board.boxHeight = yRange[1] - yRange[0]
    board.boxX = 0
    board.boxWidth = width
    board.boxY = 0
    board.boxHeight = height


    return board;
  }

  function place(word, board) {
    var bw = board.width,
      bh = board.height,
      maxDelta = ~~Math.sqrt((board.boxWidth * board.boxWidth) + (board.boxHeight * board.boxHeight)),
      // startX = ~~(board.boxX + (board.boxWidth * (Math.random() + .5) >> 1)),
      // startY = ~~(board.boxY + (board.boxHeight * (Math.random() + .5) >> 1)),
      // 不能改为如下的固定方式？否则结果不对。。
      startX = ~~(board.boxX + board.boxWidth >> 1),
      startY = ~~(board.boxY + board.boxHeight >> 1),
      s = achemedeanSpiral([board.boxWidth, board.boxHeight]),
      dt = Math.random() < .5 ? 1 : -1,
      // dt = 1,
      t = -dt,
      dxdy, dx, dy;
    word.x = startX;
    word.y = startY;
    word.placed = false;
    // console.log(startX, startY)
    while (dxdy = s(t += dt)) {

      dx = ~~dxdy[0];
      dy = ~~dxdy[1];

      // console.log(word.x, word.y, dx, dy)

      if (Math.max(Math.abs(dx), Math.abs(dy)) >= (maxDelta))
        break;

      word.x = startX + dx;
      word.y = startY + dy;

      if (word.x + word.x0 < 0 || word.y + word.y0 < 0 || word.x + word.x1 > size[0] || word.y + word.y1 > size[1])
        continue;
      if (!cloudCollide(word, board)) {
        // console.log('place word')
        // console.log(word.x, word.y)
        placeWordToBoard(word, board);
        word.placed = true;
        break;
      }
    }
  }

  function achemedeanSpiral(size) {
    var e = size[0] / size[1];
    return function(t) {
      return [e * (t *= .1) * Math.cos(t), t * Math.sin(t)];
    }
  };

  function cloudCollide(word, board) {
    var wh = word.height,
      ww = word.width,
      bw = board.width;
    //For each pixel in word
    for (var j = 0; j < wh; j++) {
      for (var i = 0; i < ww; i++) {
        var wsi = j * ww + i; //word sprite index;
        var wordPixel = word.sprite[wsi];
        // console.log(wsi, wordPixel)
        var bsi = (j + word.y + word.y0) * bw + i + (word.x + word.x0); //board sprite index
        var boardPixel = board.sprite[bsi];

        if (boardPixel != 0 && wordPixel != 0) {
          return true;
        }
      }
    }
    return false;
  }

  function placeWordToBoard(word, board) {
    //Add the sprite
    var y0 = word.y + word.y0,
      x0 = word.x + word.x0,
      bw = board.width,
      ww = word.width,
      wh = word.height;
    for (var j = 0; j < wh; j++) {
      for (var i = 0; i < ww; i++) {
        var wsi = j * ww + i;
        var bsi = (j + y0) * bw + i + x0;
        if (word.sprite[wsi] != 0) board.sprite[bsi] = word.sprite[wsi];
      }
    }
  }

  function cloudCanvas() {
    return document.createElement("canvas");
  }

  function getContext(canvas) {
    canvas.width = cw;
    canvas.height = ch;
    var context = canvas.getContext("2d");
    context.fillStyle = context.strokeStyle = "red";
    context.textAlign = "center";
    context.textBaseline = "middle";
    return context;
  }
  this.buildSvg = buildSvg
  this.buildCanvas = buildCanvas
  this.buildBoard = buildBoard
  this.place = place
  this.getImageData = getImageData

}



