    function fixPolygonsOnthePath(child, parent) {
      let px = parent.x,
        py = parent.y,
        cx = child.x,
        cy = child.y,
        mid1 = parent.mid,
        mid2 = child.mid

      if (Global.errosion[mid1 + ',' + mid2]) {
        return
      }

      let A = [px, py],
        B = [cx, cy]

      let cpolygons = child.allocatedPolygons,
        ppolygons = parent.allocatedPolygons

      for (let i = 0; i < cpolygons.length; i++) {
        let polygon = self.polygons[cpolygons[i]]
        let boundary = polygon.boundary
        let tag = false
        for (let j = 0; j < boundary.length; j++) {
          for (let k = j + 1; k < boundary.length; k++) {
            let C = boundary[j],
              D = boundary[k]
            // console.log(C, D)
            if (segmentIntersect(A, B, C, D)) {
              tag = true
            }
          }
        }
        if (tag) {
          console.log('fixed nodes')
          polygon.notMove = true
        }
      }

      for (let i = 0; i < ppolygons.length; i++) {
        let polygon = self.polygons[ppolygons[i]]
        let boundary = polygon.boundary
        let tag = false
        for (let j = 0; j < boundary.length; j++) {
          for (let k = j + 1; k < boundary.length; k++) {
            let C = boundary[j],
              D = boundary[k]
            // console.log(C, D)
            if (segmentIntersect(A, B, C, D)) {
              tag = true
            }
          }
        }
        if (tag) {
          polygon.notMove = true
        }
      }

      function segmentIntersect(A, B, C, D) {
        let AC = [A[0] - C[0], A[1] - C[1]],
          AD = [A[0] - D[0], A[1] - D[1]],
          BC = [B[0] - C[0], B[1] - C[1]],
          BD = [B[0] - D[0], B[1] - D[1]]
        // console.log(vector_product(AC, AD))
        if (vector_product(AC, AD) * vector_product(BC, BD) <= 0 &&
          vector_product(AC, BC) * vector_product(AD, BD) <= 0) {
          return true
        }
        return false
      }
      window.segmentIntersect = segmentIntersect

      function vector_product(A, B) {
        return A[0] * B[1] - A[1] * B[0]
      }
    }

    function updateCentroids() {
      allSeeds.forEach(d => {
        if (d.type != 'corner') {
          let center = [0, 0]
          let nodes = d.allocatedPolygons
          for (let i in nodes) {
            center[0] = center[0] + polygons[nodes[i]].site[0]
            center[1] = center[1] + polygons[nodes[i]].site[1]
          }
          if (nodes.length != 0) {
            center[0] = center[0] / nodes.length
            center[1] = center[1] / nodes.length
          }
          d.x = center[0]
          d.y = center[1]
        }
      })
    }

