  manuallyRemovePolygons: function(allSeeds, seed, maxIndex, requiredNum) {
    let self = this
    let polygons = this.polygons
    let iterNum = 0
    console.log('requiredNum', requiredNum)
    while (1) {
      if (iterNum++ > 100) {
        console.log('manuallyRemovePolygons loop worong')
        break
      }
      let finished = false
      let length = seed.allocatedPolygons.length
      let allocatedPolygons = seed.allocatedPolygons
      let center = [seed.x, seed.y]
      // allocatedPolygons.forEach(d => {
      //   center[0] += polygons[d].site[0]
      //   center[1] += polygons[d].site[1]
      // })
      // center[0] /= length
      // center[1] /= length
      let boundaryPolygons = self.calBoundaryPolygons(allocatedPolygons, seed.keyplayerIndex, maxIndex)
      boundaryPolygons.sort(function(a, b) {
        return -1 * Math.distance(polygons[a].site, center) + Math.distance(polygons[b].site, center)
      })
      for (let i = 0; i < boundaryPolygons.length; i++) {
        // if (i >= seed.allocatedPolygons.length) {
        //   break
        // }
        //let boundaryPolygons = self.calBoundaryPolygons(seed.allocatedPolygons, seed.keyplayerIndex, maxIndex)
        let polygon = polygons[boundaryPolygons[i]]
        if (polygon.notMove == true) continue
        let neighbors = polygon.neighbors
        // console.log('seed length', seed.allocatedPolygons.length)
        for (let j in neighbors) {
          let neighborIndex = neighbors[j]
          let adjPolygon = polygons[neighborIndex]
          // console.log(adjPolygon, polygon)
          // console.log(adjPolygon.index, adjPolygon.keyplayerIndex, polygon.keyplayerIndex)
          if (adjPolygon.keyplayerIndex != polygon.keyplayerIndex) {
            if (!allSeeds[adjPolygon.keyplayerIndex].protected) {
              // 防止来回划分死循环
              self.removeAllocatedPolygon(seed.allocatedPolygons, polygon.index)
              polygon.keyplayerIndex = adjPolygon.keyplayerIndex
              allSeeds[adjPolygon.keyplayerIndex].allocatedPolygons.push(polygon.index)
              // polygon.manuallyRemoved = true
              if (seed.allocatedPolygons.length == requiredNum) {
                finished = true
                break
              }
              break
            }
          }
        }
        if (finished) {
          break
        }
      }
      if (finished) break
    }
    seed.protected = true
  },


