  calBoundaryPolygons: function(allocatedPolygons, keyplayerIndex, maxIndex) {
    let polygons = this.polygons
    let boundaryPolygons = []
    allocatedPolygons.forEach(d => {
      let neighbors = polygons[d].neighbors
      let tag = false
      for (let j in neighbors) {
        if (polygons[neighbors[j]].keyplayerIndex != keyplayerIndex) { // 边界判断条件
          tag = true
        }
      }
      if (tag) {
        boundaryPolygons.push(d)
      }
    })
    return boundaryPolygons
  },

